<script lang="ts">
export default {
  name: "TheDetailsLayout",
};
</script>

<script setup lang="ts">
import { useRoute } from "vue-router";
import MainContent from "@/components/content/MainContent.vue";
import DetailsHeader from "@/components/headers/DetailsHeader.vue";
import TheNotification from "@/components/TheNotification/TheNotification.vue";
import useNotificationsStore from "@/store/notifications";
import { computed, ref, watchEffect } from "vue";
import FixedHeader from "@/components/headers/FixedHeader.vue";

const route = useRoute();
const notificationsStore = useNotificationsStore();
const pageTitle = ref("");
const grayBackground = ref(false);
const notification = computed(() => notificationsStore.notifications[0]);

notificationsStore.get();

watchEffect(() => {
  pageTitle.value = (route?.meta?.title as string) || "Give this page a title";
  grayBackground.value = !!route?.meta?.grayBackground;
});
</script>

<template>
  <FixedHeader />
  <TheNotification v-if="notification" :notification="notification" />
  <DetailsHeader :page-title="pageTitle" />
  <MainContent :gray-background="grayBackground">
    <slot />
  </MainContent>
</template>
