import type { ExtensionContext, MessageBody, SendMessageBody } from "./types";

export default class Message {
  private context?: ExtensionContext;
  private namespace: string;

  constructor(namepsace: string, context?: ExtensionContext) {
    this.namespace = namepsace;
    this.context = context;
  }

  async send(message: SendMessageBody): Promise<any> {
    const data = {
      ...message,
      from: this.namespace,
    } as MessageBody;

    return await this.context?.runtime?.sendMessage(data);
  }

  addListener(
    callback: (
      message: any,
      sender: chrome.runtime.MessageSender,
      sendResponse: any
    ) => void
  ) {
    this.context?.runtime?.onMessage?.addListener(callback);
  }
}
