<script lang="ts">
interface DetailsHeaderProps {
  pageTitle: string;
}
</script>

<script setup lang="ts">
import { inject } from "vue";
import HeaderContent from "@/components/content/HeaderContent.vue";
import BaseButton from "@/components/BaseButton/BaseButton.vue";
import IconArrowLeft from "@/components/icons/IconArrowLeft.vue";
import { CONTEXT } from "@/types/App";
import { useRouter, useRoute, type RouteRecordName } from "vue-router";

const interstitial = inject("context") === CONTEXT.INTERSTITIAL;
defineProps<DetailsHeaderProps>();

const router = useRouter();
const route = useRoute();

const noBackRouteNames: Array<RouteRecordName> = [
  "card-collapsed",
  "login",
  "tfa",
  "password-resest",
];

const goBack = () => {
  const { meta } = router.currentRoute.value;

  if (!meta.fromRoute || noBackRouteNames.includes(meta.fromRoute as string)) {
    // If last route does not exist OR shouldn't be accessible,
    // then go to the cards list
    router.push({ name: "cards" });
  } else if (meta.fromRoute === "new-card" && route.query.newCategoryCard) {
    router.go(-2);
  } else {
    // otherwise, go back
    router.back();
  }
};
</script>

<template>
  <HeaderContent
    class="details-header text-center"
    :class="{ 'has-close': interstitial }"
  >
    <BaseButton variant="nav" @click="goBack" size="sm">
      <IconArrowLeft />
    </BaseButton>
    <h1 class="mt-1 mb-0 w-75">{{ pageTitle }}</h1>
  </HeaderContent>
</template>

<style lang="scss" scoped>
.details-header {
  justify-content: start !important;
}
.details-header:not(.has-close)::after {
  content: "";
  display: block;
  width: 24px;
}
</style>
