import { v2Api, type ApiResponse } from "@/lib/request";
import type {
  Card,
  CardState,
  SpendLimitDuration,
  CardType,
} from "@/types/Card";

const resource = "/cards";

export type ListCardParams = {
  endDate?: Date;
  fullInfo?: boolean;
  limit?: number;
  month?: Date | string;
  offset?: number;
  query?: string;
  spentThisMonth?: boolean;
  startDate?: Date;
};

export type GetCardParams = {
  fullInfo?: boolean;
  spentThisMonth?: boolean;
};

export type ListCardResponse = {
  total: number;
  data: Card[];
};

export type CreateCardStates = CardState.OPEN | CardState.PAUSED;
type CreateCardTypes =
  | CardType.SINGLE_USE
  | CardType.MERCHANT_LOCKED
  | CardType.UNLOCKED
  | CardType.DIGITAL_WALLET;

export type CreateCardData = {
  bankAccountID?: number;
  cardProgramID?: number;
  hostname?: string;
  memo?: string;
  merchantCategory?: string;
  meta?: Record<string, any>;
  spendLimit?: number;
  spendLimitDuration?: SpendLimitDuration;
  state?: CreateCardStates;
  type?: CreateCardTypes;
};

export type UpdateCardData = {
  bankAccountID?: Card["bankAccountID"];
  hostname?: Card["hostname"];
  memo?: Card["memo"];
  meta?: Card["meta"];
  spendLimit?: Card["spendLimit"] | null;
  spendLimitDuration?: SpendLimitDuration;
  type?: CardType.SINGLE_USE | CardType.MERCHANT_LOCKED;
};

export type SetStateData = {
  state: CardState.OPEN | CardState.PAUSED | "CLOSED";
};

export type SetNoteData = {
  note: string;
};

export function list(
  params?: ListCardParams
): Promise<ApiResponse<ListCardResponse>> {
  if (params?.month instanceof Date) {
    const month = (params.month.getMonth() + 1).toString().padStart(2, "0");

    params.month = params.month.getFullYear() + "-" + month;
  }

  return v2Api.get(resource, params);
}

export function create(data?: CreateCardData): Promise<ApiResponse<Card>> {
  return v2Api.post(resource, data);
}

export function get(
  cardUuid: string,
  params?: GetCardParams
): Promise<ApiResponse<Card>> {
  return v2Api.get(`${resource}/${cardUuid}`, params);
}

export function update(
  cardUuid: string,
  data: UpdateCardData
): Promise<ApiResponse<Card>> {
  return v2Api.patch(`${resource}/${cardUuid}`, data);
}

export function setState(
  cardUuid: string,
  data: SetStateData
): Promise<ApiResponse<Card>> {
  return v2Api.patch(`${resource}/${cardUuid}/state`, data);
}

export function setNote(
  cardUuid: string,
  data: SetNoteData
): Promise<ApiResponse<Card>> {
  return v2Api.put(`${resource}/${cardUuid}/note`, data);
}
