<script setup lang="ts">
import { useAuthStore } from "@/store/auth/authStore";
import { computed, ref } from "vue";
import { useError } from "@/lib/errors/errors";
import BaseAlert from "@/components/BaseAlert/BaseAlert.vue";
import BaseButton from "@/components/BaseButton/BaseButton.vue";
import BaseInput from "@/components/BaseInput/BaseInput.vue";
import IconCheck from "@/components/icons/IconCheck.vue";
import appExtensionApi from "@/lib/extension/appExtensionApi";
import { useRouter } from "vue-router";

const router = useRouter();
const authStore = useAuthStore();
const { errorMessage, setError, resetError } = useError();

const resetCompleted = ref(false);
const code = ref("");

const isSubmitDisabled = computed(() => code.value.length !== 5);

const removeTFA = async () => {
  try {
    resetError();

    await authStore.removeTFA(code.value);

    resetCompleted.value = true;
  } catch (err: unknown) {
    setError(err, "Error confirming verification code");
  }
};

async function toTFAView() {
  await appExtensionApi.storage.set({ startPage: "tfa" });

  router.push({ name: "tfa" });
}
</script>

<template>
  <div class="px-4">
    <h1 class="mb-4">TFA Reset</h1>
    <p>
      We've sent the TFA Recovery Code via SMS. Enter it below to reset your
      TFA.
    </p>
    <form @submit.prevent="removeTFA" class="d-grid gap-2">
      <BaseAlert v-if="errorMessage" variant="danger" data-test="confirm-error">
        {{ errorMessage }}
      </BaseAlert>
      <BaseInput
        v-model="code"
        placeholder="5 Digit code"
        maxlength="5"
        data-test="confirm-code-input"
        aria-label="5 Digit code"
        :disabled="resetCompleted"
        autocomplete="one-time-code"
        inputmode="numeric"
      />
      <BaseButton
        v-if="resetCompleted"
        variant="success"
        type="button"
        data-test="submit-success"
        class="pe-none"
        readonly
      >
        <IconCheck class="me-2" />
        TFA Reset
      </BaseButton>
      <BaseButton
        :to="{ name: 'login' }"
        v-if="resetCompleted"
        data-test="to-login"
      >
        Go to Login
      </BaseButton>
      <BaseButton
        v-else
        variant="primary"
        type="submit"
        class="mb-2"
        data-test="confirm-submit"
        :disabled="isSubmitDisabled"
      >
        Reset TFA
      </BaseButton>
    </form>
    <BaseButton
      v-if="!resetCompleted"
      class="w-100"
      variant="link"
      data-test="confirm-go-back"
      @click="toTFAView"
    >
      Back
    </BaseButton>
  </div>
</template>
