<script setup lang="ts">
import UserIcon from "@/components/UserIcon/UserIcon.vue";
import BaseButton from "@/components/BaseButton/BaseButton.vue";
import TheLogout from "@/components/TheLogout/TheLogout.vue";
import type { Theme } from "@/lib/theme";
import IconArrowUpRight from "@/components/icons/IconArrowUpRight.vue";
import type { Subscription } from "@/types/Subscription";
import appExtensionApi from "@/lib/extension/appExtensionApi";
import { useAuthStore } from "@/store/auth/authStore";
import { SubscriptionType } from "@/types/Subscription";
import { useEventStore } from "@/store/event/eventStore";
import { SUBSCRIPTION_EVENTS } from "@/types/Event";

export interface SpendLimit {
  frequency: "daily" | "weekly" | "monthly";
  total: number;
  limit: number;
}

const isSafari = navigator?.userAgent?.includes("Safari");
const appUrl = import.meta.env.EXPOSED_PRIVACY_WEB_BASE_URL;
const authStore = useAuthStore();
const eventStore = useEventStore();

defineProps<{
  theme: Theme;
  photoUrl?: string;
  email: string;
  fullName: string;
  credit?: number;
  cashback?: number;
  spendLimits?: SpendLimit[];
  showLimits?: boolean;
  subscription?: Subscription;
}>();

const toCurrency = (num: number, round = false) => {
  if (round) {
    return "$" + num.toLocaleString("en-US");
  } else {
    return num.toLocaleString("en-US", {
      style: "currency",
      currency: "USD",
    });
  }
};

const upgradeHandler = async () => {
  const token = await authStore.getShortLivedAuthToken();

  try {
    await appExtensionApi.message.send({
      event: "addShortLivedAuthCookie",
      payload: {
        url: appUrl,
        name: "token",
        value: token,
      },
    });
  } catch (error) {
    /* swallow error */
  }

  eventStore.track({ name: SUBSCRIPTION_EVENTS.UPGRADE_CLICKED });
  window.open(`${appUrl}/select-plan`, "_blank");
};
</script>

<template>
  <UserIcon size="large" :url="photoUrl" class="m-auto mb-3" />
  <h3 class="text-center mb-1" v-if="fullName">{{ fullName }}</h3>
  <div class="text-center mb-3">{{ email }}</div>
  <div class="d-grid mb-3">
    <BaseButton :to="`${appUrl}/account`" target="_blank">
      My Account
      <IconArrowUpRight class="ms-1" />
    </BaseButton>
    <div v-if="isSafari" class="mt-3 alert alert-light">
      Head to my account to
      {{
        subscription?.subscriptionType === SubscriptionType.FREE
          ? "sign up for a"
          : "manage your"
      }}
      plan.
    </div>
    <BaseButton
      v-else
      variant="secondary"
      class="mt-2"
      @click="upgradeHandler"
      target="_blank"
      data-test="upgrade-button"
    >
      {{
        subscription?.subscriptionType === SubscriptionType.FREE
          ? "Upgrade"
          : "Change Plan"
      }}
      <IconArrowUpRight class="ms-1" />
    </BaseButton>
  </div>
  <ul class="list-group mb-3 fw-medium">
    <li
      v-if="credit !== undefined"
      class="list-group-item d-flex justify-content-between"
      data-test="credit"
    >
      <span class="text-light-emphasis">Credit</span>
      <span>{{ toCurrency(credit) }}</span>
    </li>
    <li
      v-if="cashback !== undefined"
      class="list-group-item d-flex justify-content-between"
      data-test="cashback"
    >
      <span class="text-light-emphasis">Cashback</span>
      <span>{{ toCurrency(cashback) }}</span>
    </li>
    <li v-if="showLimits && !spendLimits?.length" class="list-group-item">
      <div class="row gx-0 placeholder-glow">
        <div class="rounded-1 placeholder col-12 mb-2" />
        <div class="rounded-1 placeholder col-6" />
      </div>
    </li>
    <li v-if="showLimits && !spendLimits?.length" class="list-group-item">
      <div class="row gx-0 placeholder-glow">
        <div class="rounded-1 placeholder col-12 mb-2" />
        <div class="rounded-1 placeholder col-6" />
      </div>
    </li>
    <li
      v-for="spendLimit in spendLimits"
      :key="spendLimit.frequency"
      class="list-group-item"
      :data-test="spendLimit.frequency + '-spend-limit'"
    >
      <div class="text-light-emphasis text-capitalize mb-2">
        {{ spendLimit.frequency }} Spend Limit
      </div>
      <div class="mb-2">
        {{ toCurrency(spendLimit.total) }}
        <span class="text-light-emphasis">
          of {{ toCurrency(spendLimit.limit, true) }}
        </span>
      </div>
      <div class="progress">
        <div
          class="progress-bar"
          :style="`width: ${(spendLimit.total / spendLimit.limit) * 100}%`"
        />
      </div>
    </li>
    <li
      v-if="showLimits && subscription"
      class="list-group-item"
      data-test="plan-limits"
    >
      <div class="text-light-emphasis text-capitalize mb-2">Monthly Cards</div>
      <div class="mb-2">
        {{ subscription.cardsCreatedLast30 }}
        <span class="text-light-emphasis">
          of {{ subscription.cardsPerMonth }}
        </span>
      </div>
      <div class="progress">
        <div
          class="progress-bar"
          :style="`width: ${
            (subscription.cardsCreatedLast30 / subscription.cardsPerMonth) * 100
          }%`"
        />
      </div>
    </li>
  </ul>
  <div class="d-grid">
    <TheLogout />
  </div>
</template>

<style scoped lang="scss">
.text-light-emphasis {
  color: $color-privacy-gray-3 !important;
}

.progress {
  height: 4px;
}
</style>
