<script lang="ts">
interface PropType {
  state?: Card["state"] | "paused-force";
}
</script>
<template>
  <div class="card-state-banner" :class="cardStateClass" v-if="cardStateStr">
    {{ cardStateStr }}
  </div>
</template>

<script setup lang="ts">
import { CardState, type Card } from "@/types/Card";
import { computed } from "vue";

const props = defineProps<PropType>();

const cardStateClass = computed(() => {
  if (props.state === CardState.PAUSED) {
    return "paused";
  }

  if (props.state === "paused-force") {
    return "paused-force";
  }

  if (props.state === CardState.CLOSED) {
    return "closed";
  }

  return "";
});

const cardStateStr = computed(() => {
  if (props.state === CardState.PAUSED || props.state === "paused-force") {
    return "paused";
  }

  if (props.state === CardState.CLOSED) {
    return "closed";
  }

  return "";
});
</script>

<style lang="scss" scoped>
.card-state-banner {
  padding: 8px;
  width: 100%;
  color: $white;
  font-size: 11px;
  font-weight: 600;
  text-align: center;
  text-transform: uppercase;
}

.paused {
  background-color: $accent-default;
}

.paused-force {
  background-color: $danger;
}

.closed {
  background-color: $neutrals-4;
}
</style>
