import { ref } from "vue";
import { defineStore } from "pinia";
import extensionApi from "@/lib/extension/appExtensionApi";
import { metric as getMetric } from "@/apis/metric/metric";
import type { Metric } from "@/types/Metric";

export const useMetricStore = defineStore("metric", () => {
  // STATES
  const metricState = ref<Metric>();

  async function init() {
    const { token } = await extensionApi.storage.get("token");

    if (token) {
      getMetrics();
    }
  }

  function $reset() {
    metricState.value = undefined;
  }

  // ACTIONS
  async function getMetrics(): Promise<Metric> {
    const { data } = await getMetric();
    metricState.value = data;
    return data;
  }

  return {
    // actions
    init,
    getMetrics,

    // state
    data: metricState,

    $reset,
  };
});
