import { type Card, CardType, SpendLimitDuration } from "@/types/Card";
import { computed, type Ref } from "vue";

type UpdateSpendLimit = {
  amount: number | null;
  duration: SpendLimitDuration | "REMOVE";
};

export type SpendLimitUpdates = {
  spendLimit: number | null;
  spendLimitDuration?: SpendLimitDuration;
};

export function useUpdateSpendLimit(card: Ref<Card>) {
  const current = computed(() => card.value);
  const cardType = computed(() => current.value.type);

  return function ({ amount, duration }: UpdateSpendLimit): SpendLimitUpdates {
    if (cardType.value === CardType.SINGLE_USE && !amount) {
      throw new Error("Can not remove limits on a Single-Use card");
    }

    const updateData: SpendLimitUpdates = {
      spendLimit: amount,
    };

    if (duration === "REMOVE") {
      updateData.spendLimitDuration = SpendLimitDuration.TRANSACTION;
    } else {
      updateData.spendLimitDuration = duration as SpendLimitDuration;
    }

    return updateData;
  };
}
