<script lang="ts">
type Step = {
  max: number;
  min: number;
  step: number;
};

interface Props {
  max?: number;
  min?: number;
  modelValue?: number;
  name: string;
  step?: number;
  steps?: Step[];
}
</script>

<script setup lang="ts">
defineProps<Props>();
const emit = defineEmits(["update:modelValue"]);

function onInput(event: Event) {
  emit("update:modelValue", (event.target as HTMLInputElement).valueAsNumber);
}
</script>

<template>
  <input
    class="form-range"
    type="range"
    :name="name"
    :min="min"
    :max="max"
    :step="step"
    :value="modelValue"
    @input.passive="onInput"
  />
</template>

<style scoped lang="scss">
@import "~bootstrap/scss/mixins";

@include color-mode(dark) {
  .form-range {
    &::-moz-range-track,
    &::-webkit-slider-runnable-track {
      background-color: var(--bs-tertiary-bg);
    }
  }
}
</style>
