<script setup lang="ts">
import { computed, ref } from "vue";
import { useUserStore } from "@/store/user/userStore";
import { setTheme, getTheme, type Theme } from "@/lib/theme";
import TheAccount, {
  type SpendLimit,
} from "@/components/TheAccount/TheAccount.vue";
import { subscription as getSubscription } from "@/apis/subscription/subscription";
import type { Subscription } from "@/types/Subscription";
import { useMetricStore } from "@/store/metric/metricStore";

const userStore = useUserStore();
const metricStore = useMetricStore();

const user = computed(() => userStore.user);
const metric = computed(() => metricStore.data);

const photoUrl = computed((): string | undefined => {
  return user.value.photo
    ? `${
        import.meta.env.EXPOSED_PRIVACY_WEB_BASE_URL
      }/api/v1/user/getAccountPhoto/${user.value.photo}`
    : undefined;
});

const theme = ref<Theme>();
getTheme()
  .then((storedTheme) => (theme.value = storedTheme))
  .catch(() => {
    theme.value = "auto";
    setTheme(theme.value);
  });

const subscription = ref<Subscription>();
getSubscription()
  .then(({ data }) => (subscription.value = data.data[0]))
  .catch(() => {});

const credit = computed(() =>
  user.value?.promoCredits ? Number(user.value.promoCredits.amount) : undefined
);

const fullName = computed(() => {
  const { preferredName = "", lastName = "" } = user.value;
  return `${preferredName} ${lastName}`.trim();
});

const cashback = computed(() =>
  user.value?.cashbackProgram?.enabled
    ? Number(metric.value?.metricList.totalPendingCashback || 0) / 100
    : undefined
);

const spendLimits = computed<SpendLimit[]>(() => {
  const spendLimits: SpendLimit[] = [];

  if (user.value.shouldShowLimits && metric.value) {
    if (user.value.isLimitWeekly) {
      spendLimits.push({
        frequency: "weekly",
        total: Number(metric.value.metricList.weekTotal) / 100,
        limit: Number(user.value.dailySpendLimit || 0),
      });
    } else {
      spendLimits.push({
        frequency: "daily",
        total: Number(metric.value.metricList.dayTotal) / 100,
        limit: Number(user.value.dailySpendLimit || 0),
      });
    }

    spendLimits.push({
      frequency: "monthly",
      total: Number(metric.value.metricList.monthTotal) / 100,
      limit: Number(user.value.monthlySpendLimit || 0),
    });
  }

  return spendLimits;
});
</script>

<template>
  <div class="px-4">
    <TheAccount
      v-if="theme && user.email"
      :theme="theme"
      :photoUrl="photoUrl"
      :email="user.email"
      :fullName="fullName"
      :credit="credit"
      :cashback="cashback"
      :spendLimits="spendLimits"
      :showLimits="user.shouldShowLimits"
      :subscription="subscription"
    />
  </div>
</template>
