<script lang="ts">
import type { User } from "@/types/User";
import {
  CardState,
  CardLogoSize,
  type Card,
  type CardUser,
} from "@/types/Card";
import {
  SubscriptionFeaturesEnum,
  type SubscriptionFeatures,
} from "@/types/Subscription";

interface CardListProps {
  cardUuid?: string;
  categoryCard?: boolean;
  user?: User;
  loading?: boolean;
  showUseCard?: boolean;
  subscriptionFeatures?: SubscriptionFeatures;
}
</script>

<script setup lang="ts">
import { computed, inject } from "vue";
import useCardNickname from "@/lib/cards/nickname";
import PrivacyCardLogo from "@/components/PrivacyCardLogo/PrivacyCardLogo.vue";
import BaseLabel from "@/components/BaseLabel/BaseLabel.vue";
import { useFillCheckout, useShowCollapsedCard } from "@/lib/cards/useCheckout";
import BaseButton from "@/components/BaseButton/BaseButton.vue";
import { CONTEXT } from "@/types/App";
import useCardsStore from "@/store/cards";
import { useEventStore } from "@/store/event/eventStore";
import { CARD_EVENTS } from "@/types/Event";
import { CardType } from "@/types/Card";
import popupAlert from "@/lib/popupAlert";
import IconPlay from "../icons/IconPlayList.vue";
import IconPause from "../icons/IconPauseList.vue";
import IconAutofill from "../icons/IconAutofill.vue";

const cardsStore = useCardsStore();
const eventStore = useEventStore();
const interstitial = inject("context") === CONTEXT.INTERSTITIAL;

const props = withDefaults(defineProps<CardListProps>(), {
  loading: false,
});

const card = computed(() => cardsStore.card(props.cardUuid!) || ({} as Card));
const cardUser = computed(() =>
  props.user
    ? {
        name: props.user.firstName + " " + props.user.lastName,
        firstName: props.user.firstName,
        lastName: props.user.lastName,
        streetAddress: props.user.address1 + " " + props.user.address2,
        addressLine1: props.user.address1,
        addressLine2: props.user.address2,
        addressLevel2: props.user.city,
        addressLevel1: props.user.state,
        postalCode: props.user.zipcode,
      }
    : ({} as CardUser)
);
const nickname = useCardNickname(card);

const isPaused = computed(() => card.value.state === CardState.PAUSED);
const hasCategoryCardsFeature = computed(
  () => props.subscriptionFeatures?.[SubscriptionFeaturesEnum.CATEGORY_CARDS]
);
const hasDigitalWalletCardsFeature = computed(
  () =>
    props.subscriptionFeatures?.[SubscriptionFeaturesEnum.DIGITAL_WALLET_CARDS]
);
const forcePaused = computed(() => {
  return (
    isPaused.value &&
    ((!hasCategoryCardsFeature.value && props.categoryCard) ||
      (!hasDigitalWalletCardsFeature.value &&
        card.value.type === CardType.DIGITAL_WALLET))
  );
});
const isOpen = computed(() => card.value.state === CardState.OPEN);
const isClosed = computed(() => {
  return (
    card.value.state === CardState.CLOSED_PENDING_REISSUE ||
    card.value.state == CardState.CLOSED_REISSUED
  );
});
const labelText = computed(() => (isPaused.value ? "Paused" : "Closed"));

async function fillAndRedirect() {
  await useFillCheckout(card.value, cardUser.value, true);
  useShowCollapsedCard(card.value.cardUuid, interstitial);
}

async function togglePause() {
  const state = isOpen.value ? CardState.PAUSED : CardState.OPEN;
  const event =
    state === CardState.OPEN ? CARD_EVENTS.PAUSED : CARD_EVENTS.RESUMED;

  try {
    await cardsStore.setState(card.value.cardUuid, { state });
    eventStore.track({
      name: event,
      data: { cardUuid: card.value.cardUuid },
    });
    const status = state === CardState.OPEN ? "resumed" : state.toLowerCase();
    popupAlert("Card " + status, "success");
  } catch (err) {
    eventStore.error([
      {
        name: event,
        data: {
          cardUuid: card.value.cardUuid,
          state,
          error: err,
        },
      },
    ]);
    popupAlert("Unable to update card");
  }
}
</script>

<template>
  <div v-if="loading" data-test="card-list-item-loading">
    <div class="row gx-0 align-items-center placeholder-glow px-4 py-2">
      <div class="placeholder placeholder-logo me-2" />
      <div class="col-6">
        <div class="rounded-1 placeholder col-12 mb-2" />
        <div class="rounded-1 placeholder col-6" />
      </div>
    </div>
  </div>
  <div
    v-else
    class="card-list-item d-flex w-100 min-w-0 px-4 py-2"
    :class="{
      'card-closed': isClosed,
      'force-paused': forcePaused,
    }"
  >
    <PrivacyCardLogo
      :type="card.type"
      :mcc="card.mcc"
      :mcc-icon-url="card.mccIconUrl"
      :style="card.style"
      :size="CardLogoSize.LARGE"
      :merchant-category="card.merchantCategory"
      class="flex-shrink-0 me-3"
      data-test="card-list-item-logo"
    />
    <div class="d-flex min-w-0 w-100 align-items-center me-2">
      <div class="min-w-0 w-100">
        <div
          class="text-truncate fw-medium mb-1"
          data-test="card-list-item-name"
        >
          {{ nickname }}
        </div>
        <div class="pan" data-test="card-list-item-pan">
          {{ card.lastFour }}
        </div>
      </div>
    </div>
    <div class="d-flex align-items-center">
      <BaseLabel
        v-if="isClosed || isPaused"
        data-test="card-list-item-state"
        class="card-label"
        :title="forcePaused ? 'Paid plan required to resume card' : ''"
      >
        {{ labelText }}
      </BaseLabel>
      <div class="card-controls" v-if="!forcePaused">
        <BaseButton
          class="card-state me-3"
          variant="icon"
          size="sm"
          @click.stop.prevent="togglePause"
          data-test="resume-pause"
          :title="isOpen ? 'Pause Card' : 'Resume card'"
        >
          <IconPause v-if="isOpen" aria-label="Pause card" />
          <IconPlay v-if="isPaused" aria-label="Resume card" />
        </BaseButton>
        <BaseButton
          v-if="showUseCard && !isPaused"
          class="autofill me-3"
          variant="icon"
          size="sm"
          :disabled="isPaused"
          @click.stop.prevent="fillAndRedirect"
          aria-label="Fill payment form with this card"
          title="Fill payment form with this card"
        >
          <IconAutofill />
        </BaseButton>
      </div>
    </div>
  </div>
</template>

<style scoped lang="scss">
.card-list-item {
  line-height: 1.125rem;

  .card-controls {
    display: none;
    align-items: center;
    justify-content: flex-end;
    flex-shrink: 0;
    flex-direction: row-reverse;
    margin-top: -$spacer;
  }

  &:not(.force-paused):hover {
    background-color: $color-privacy-hover;
    .card-label {
      display: none;
    }
    .card-controls {
      display: flex;
    }
  }
}

@include color-mode(dark) {
  .card-list-item:hover {
    background-color: $color-privacy-hover-dark;
  }
}

.placeholder-logo {
  height: 48px;
  width: 48px;
  border-radius: 14px;
}

.pan {
  color: $color-privacy-gray-3;
}

.card-closed :deep(.badge) {
  background-color: $color-privacy-gray-3 !important;
  color: $color-privacy-white;
}

.force-paused :deep(.badge) {
  background-color: $danger !important;
  color: $color-privacy-white;
}
</style>
