import { computed, ref } from "vue";
import { defineStore } from "pinia";
import { notification as notificationApi } from "@/apis";
import type { Notification } from "@/types/Notification";
import { useEventStore } from "../event/eventStore";
import { NOTIFICATION_EVENTS } from "@/types/Event";

export default defineStore("notifications", () => {
  const eventStore = useEventStore();

  const notificationsState = ref<Notification[]>([]);
  const requestMadeState = ref(false);

  const notifications = computed(() => notificationsState.value);

  async function get(): Promise<Notification[]> {
    if (requestMadeState.value) {
      return notificationsState.value;
    }

    try {
      const { data } = await notificationApi.list();
      notificationsState.value = data.data;
    } catch (error: any) {
      eventStore.error([
        {
          name: NOTIFICATION_EVENTS.REQUESTED,
          data: {
            message: error.message,
            error: error,
          },
        },
      ]);
    }

    requestMadeState.value = true;

    return notificationsState.value;
  }

  function $reset() {
    notificationsState.value = [];
  }

  return {
    // getters
    notifications,

    // actions
    get,
    $reset,

    // state
    notificationsState,
    requestMadeState,
  };
});
