import type { ExtensionContext } from "./types";

// Note: additional management methods might require adding manifest permissions
// https://developer.mozilla.org/en-US/docs/Mozilla/Add-ons/WebExtensions/API/management

export default class Management {
  private context?: ExtensionContext;

  constructor(context?: ExtensionContext) {
    this.context = context;
  }

  uninstall() {
    const options: chrome.management.UninstallOptions = {
      showConfirmDialog: true,
    };

    return this.context?.management.uninstallSelf(options);
  }
}
