  CONSENT_DECLINE_CANCELLED = "Extension: Consent Decline Cancelled",
  OPENED = "Extension: Opened",
  DISMISSED = "Extension: Dismissed",
  SIGN_UP = "Extension: Sign Up",
  ACCOUNT_REDIRECT = "Extension: Account Redirect",
  CHECKOUT = "Extension: Checkout",
  DETECTED_CHECKOUT_FORM = "Extension: Detected Checkout Form",
  FILL_CHECKOUT = "Extension: Fill Checkout",
  FILL_CHECKOUT_NO_FORM = "Extension: Fill Checkout No Form Detected",
  FILL_CHECKOUT_INPUT_FORMAT = "Extension: Unable to Determine Input Field Format",
