export enum USER_EVENTS {
  LOGOUT = "Extension: User Logout",
}

export enum CARD_EVENTS {
  CLOSED = "Extension: Card Closed",
  CREATED = "Extension: Card Created",
  CREATED_BLOCKED = "Extension: Card Create Blocked",
  PAUSED = "Extension: Card Paused",
  RESUMED = "Extension: Card Resumed",
  PAN_COPIED = "Extension: Card PAN Copied",
  EXP_COPIED = "Extension: Card EXP Copied",
  CVV_COPIED = "Extension: Card CVV Copied",
  SET_FUNDING_SOURCE = "Extension: Card Set Funding Source",
  SET_NICKNAME = "Extension: Card Set Nickname",
  SET_NOTE = "Extension: Card Set Note",
  SET_SPEND_LIMIT = "Extension: Card Set Spend Limit",
  SHARED = "Extension: Card Shared",
  VIEWED = "Extension: Card Viewed",
}

export enum ERROR_EVENTS {
  UNHANDLED = "Extension: Unhandled Error",
}

export enum NOTIFICATION_EVENTS {
  REQUESTED = "Extension: Notifications Requested",
}

export enum SUBSCRIPTION_EVENTS {
  UPGRADE_CLICKED = "Extension: Subscription Upgrade Clicked",
}

export type EVENT_NAMES =
  | EXTENSION_EVENTS
  | CARD_EVENTS
