<script lang="ts">
import { CardType, SpendLimitDuration } from "@/types/Card";
import SpendLimitModal, {
  type SubmitEvent,
} from "../SpendLimitModal/SpendLimitModal.vue";

const spendLimitMap = {
  [SpendLimitDuration.ANNUALLY]: "Yearly",
  [SpendLimitDuration.FOREVER]: "Total",
  [CardType.SINGLE_USE]: "Total",
  [SpendLimitDuration.MONTHLY]: "Monthly",
  [SpendLimitDuration.TRANSACTION]: "Transaction",
  REMOVE: null,
};

const spendLimitAmountMap = {
  [SpendLimitDuration.ANNUALLY]: "spentThisYearRolling",
  [SpendLimitDuration.FOREVER]: "spentTotal",
  [CardType.SINGLE_USE]: "spentTotal",
  [SpendLimitDuration.MONTHLY]: "spentThisMonthRolling",
  [SpendLimitDuration.TRANSACTION]: null,
  REMOVE: null,
};

interface ButtonProps {
  isSingleUse: boolean;
  cardIsShared?: boolean;
  cardSharingSpendLimit?: number;
  disabled?: boolean;
  loading?: boolean;
  spendLimit: number | null;
  spendLimitDuration: SpendLimitDuration | "REMOVE";
  spentThisMonthRolling: number;
  spentThisWeek: number;
  spentThisYearRolling: number;
  spentTotal: number;
}

interface SpendLimitEvents {
  (e: "update", { amount, duration }: SubmitEvent): void;
}

type SpendLimitKeys = keyof Pick<
  ButtonProps,
  "spentThisYearRolling" | "spentTotal" | "spentThisMonthRolling" | "spendLimit"
>;
</script>

<script setup lang="ts">
import { computed, ref, type ComputedRef } from "vue";
import { formatCurrency } from "@/lib/numbers";

const props = withDefaults(defineProps<ButtonProps>(), {
  cardIsShared: false,
  cardSharingSpendLimit: 1000,
  spendLimit: 0,
  spendLimitDuration: SpendLimitDuration.TRANSACTION,
  spentThisMonthRolling: 0,
  spentThisWeek: 0,
  spentThisYearRolling: 0,
  spentTotal: 0,
  loading: false,
});

const effectiveSpendLimitDuration: ComputedRef<SpendLimitDuration | "REMOVE"> =
  computed(() => {
    return props.isSingleUse
      ? SpendLimitDuration.FOREVER
      : props.spendLimitDuration;
  });

const emit = defineEmits<SpendLimitEvents>();

const modalOpen = ref(false);

const spendLimitTitle = computed(() => {
  const { spendLimit } = props;

  let title = "Add Spend Limit";

  if (spendLimit) {
    title = `${spendLimitMap[effectiveSpendLimitDuration.value]} Spend Limit`;
  }

  return title;
});

const spendLimitStatus = computed(() => {
  const { spendLimit } = props;

  if (!spendLimit) {
    return;
  }

  // @ts-ignore - it's fine to get undefined here
  const amountKey: SpendLimitKeys =
    spendLimitAmountMap[effectiveSpendLimitDuration.value];
  const limit = formatLimits(spendLimit);

  if (!amountKey) {
    return limit;
  }

  const amount = props[amountKey];

  if (amount === null) {
    return formatLimits(spendLimit);
  }

  return `${formatLimits(amount)} of ${formatLimits(spendLimit)}`;
});

function formatLimits(amount: number) {
  return formatCurrency(amount, {
    formatOptions: {
      maximumFractionDigits: 0,
    },
  });
}

function updateSpendLimit(data: SubmitEvent) {
  emit("update", data);
  modalOpen.value = false;
}
</script>

<template>
  <section class="spend-limit rounded-3">
    <div v-if="loading" class="p-3" aria-hidden="true" data-test="placeholder">
      <div class="placeholder-glow mb-1">
        <span class="placeholder col-10" />
      </div>
      <div class="placeholder-glow">
        <span class="placeholder col-8" />
      </div>
    </div>
    <button
      :disabled="disabled"
      aria-label="Adjust spend limits"
      class="spend-limit-control w-100 p-3 rounded-3"
      @click="modalOpen = true"
      v-else
    >
      <p class="fw-medium mb-0" data-test="title">{{ spendLimitTitle }}</p>
      <p v-if="spendLimitStatus" class="mb-0 mt-2" data-test="status">
        {{ spendLimitStatus }}
      </p>
    </button>
    <SpendLimitModal
      :open="modalOpen"
      :amount="spendLimit"
      :duration="effectiveSpendLimitDuration"
      @submit="updateSpendLimit"
      @close="modalOpen = false"
      :is-single-use="isSingleUse"
      :card-is-shared="cardIsShared"
      :max-amount="cardIsShared ? cardSharingSpendLimit : undefined"
    />
  </section>
</template>

<style scoped lang="scss">
@import "~bootstrap/scss/mixins/color-mode.scss";

.spend-limit {
  border: 1px solid rgba($color-privacy-black, 0.08);
}

.spend-limit-control {
  text-align: left;
  border: 0;
  background: none;
  color: inherit;
}

@include color-mode(dark) {
  .spend-limit {
    border-color: $color-privacy-gray-4;
  }
}
</style>
