import dayjs, { Dayjs, type ConfigType, type OptionType } from "dayjs";
import dayjsUtc from "dayjs/plugin/utc";

dayjs.extend(dayjsUtc);

export function getDate(date?: ConfigType, inputFormat?: OptionType): Dayjs {
  return dayjs(date, inputFormat);
}

export function utcDate(date?: ConfigType, inputFormat?: OptionType): Dayjs {
  return getDate(date, inputFormat).utc();
}
