import { v2Api, type ApiResponse } from "@/lib/request";
import type { MerchantCategory } from "@/types/MerchantCategory";

const MERCHANT_CATEGORIES_ROUTE = "/merchantCategories";

export type ListMerchantCategoriesResponse = {
  data: MerchantCategory[];
};

export function list(): Promise<ApiResponse<ListMerchantCategoriesResponse>> {
  return v2Api.get(MERCHANT_CATEGORIES_ROUTE);
}
