<script setup lang="ts">
import BaseButton from "@/components/BaseButton/BaseButton.vue";
import { BaseDropdown, BaseDropdownItem } from "@/components/BaseDropdown";
import IconSun from "@/components/icons/IconSun.vue";
import { getTheme, setTheme, type Theme } from "@/lib/theme";
import { hasActivityConsent, setActivityConsent } from "@/lib/updates";
import { isFirefox } from "@/lib/utils/utils";
import { ref, onMounted, computed } from "vue";
import extensionApi from "@/lib/extension/appExtensionApi";

const theme = ref<Theme>();
getTheme()
  .then((storedTheme) => (theme.value = storedTheme))
  .catch(() => {
    theme.value = "auto";
    setTheme(theme.value);
  });

const buttonText = computed(() => {
  switch (theme.value) {
    case "light":
      return "Light";
    case "dark":
      return "Dark";
    default:
      return "Match System";
  }
});

function setThemeHandler(newTheme: Theme) {
  setTheme(newTheme);
  theme.value = newTheme;
}

const activityConsent = ref(false);

async function setActivityConsentHandler() {
  setActivityConsent(!activityConsent.value);
  activityConsent.value = !activityConsent.value;

  // Signal to resume or pause document parsing for autofill
  if (activityConsent.value) {
    extensionApi.message.send({
      event: "resumeParsing",
    });
  } else {
    extensionApi.message.send({
      event: "pauseParsing",
    });
  }
}

onMounted(async () => {
  activityConsent.value = await hasActivityConsent();
});
</script>
<template>
  <div class="px-4">
    <div
      v-if="isFirefox()"
      class="data-settings d-flex flex-column justify-content-around p-3 mb-4 text-center"
    >
      <h3>Data Settings</h3>
      <p>
        Allow Privacy to track activity and user behavior in order to enable
        autofill at checkout.
        <a
          href="https://privacy.com/privacy-policy"
          class="d-block text-decoration-underline"
          target="_blank"
          data-test="privacy-policy"
        >
          Learn more
        </a>
      </p>
      <BaseButton variant="light" @click="setActivityConsentHandler">
        <span v-if="activityConsent">Disable Autofill</span>
        <span v-else>Enable Autofill</span>
      </BaseButton>
    </div>
    <div class="mb-3 position-relative">
      <BaseDropdown class="theme">
        <template #button>
          <IconSun class="icon-sun me-1" />
          {{ buttonText }} Theme
        </template>
        <BaseDropdownItem @click="setThemeHandler('light')">
          Light
        </BaseDropdownItem>
        <BaseDropdownItem @click="setThemeHandler('dark')">
          Dark
        </BaseDropdownItem>
        <BaseDropdownItem @click="setThemeHandler('auto')">
          Match System
        </BaseDropdownItem>
      </BaseDropdown>
    </div>
  </div>
</template>

<style scoped lang="scss">
@import "~bootstrap/scss/mixins/color-mode.scss";

