import { v2Api, type ApiResponse } from "@/lib/request";
import type { Transaction, TransactionResultFilter } from "@/types/Transaction";

export type ListTransactionParams = {
  fullInfo?: boolean;
  limit?: number;
  offset?: number;
  cardID?: number;
  transactionFilter?: TransactionResultFilter;
  query?: string;
};

export type ListTransactionResponse = {
  total: number;
  data: Transaction[];
};

const resource = "/transactions";

export function list(
  params?: ListTransactionParams
): Promise<ApiResponse<ListTransactionResponse>> {
  return v2Api.get(resource, params);
}
