<script setup lang="ts">
import { computed } from "vue";
import type { CardStyle, CardType, Card } from "@/types/Card";
import IconArrowsDiagonal from "@/components/icons/IconArrowsDiagonal.vue";
import IconChevronDown from "@/components/icons/IconChevronDown.vue";
import BaseButton from "@/components/BaseButton/BaseButton.vue";
import InterstitialCloseButton from "@/components/InterstitialCloseButton/InterstitialCloseButton.vue";
import PrivacyCardLogo from "@/components/PrivacyCardLogo/PrivacyCardLogo.vue";
import { useClickToCopy } from "@/lib/cards/clickToCopy";
import { isIPhone } from "@/lib/utils/utils";

const props = withDefaults(
  defineProps<{
    copyInfo?: boolean;
    cvv?: string;
    expMonth: string;
    expYear: string;
    mcc?: string;
    mccIconUrl?: string;
    pan: string;
    type: CardType;
    style?: CardStyle;
    uuid: Card["cardUuid"];
    merchantCategory?: Card["merchantCategory"];
  }>(),
  {
    copyInfo: true,
  }
);

const formattedPAN = computed(() => props.pan.replace(/(.{4})/g, "$1 ").trim());
const pan = computed(() => props.pan || "");
const cvv = computed(() => props.cvv || "");
const exp = computed(() => `${props.expMonth}/${props.expYear.slice(-2)}`);

const { copyExp, copyCVV, copyPAN } = useClickToCopy({ cvv, exp, pan });
const isMobile = computed(() => isIPhone());
</script>

<template>
  <div class="collapsed-card">
    <div
      class="d-flex py-2 px-3 align-items-center"
      data-test="privacy-card-collapsed"
    >
      <PrivacyCardLogo
        class="me-2"
        :mcc="props?.mcc"
        :mccIconUrl="props?.mccIconUrl"
        :style="props?.style"
        :type="props?.type"
        :merchant-category="props?.merchantCategory"
        data-test="collapsed-card-logo"
      />
      <div>
        <div
          class="pan p-2"
          :class="{ clickable: copyInfo }"
          @click="copyPAN"
          data-test="collapsed-card-pan"
        >
          {{ formattedPAN }}
        </div>
        <div class="d-flex justify-content-center">
          <div
            class="exp p-2"
            :class="{ clickable: copyInfo }"
            @click="copyExp"
            data-test="collapsed-card-exp"
          >
            <span class="label me-2">EXP</span>
            {{ exp }}
          </div>
          <div
            class="cvv p-2"
            :class="{ clickable: copyInfo }"
            @click="copyCVV"
            data-test="collapsed-card-cvv"
          >
            <span class="label me-2">CVV</span>
            {{ cvv }}
          </div>
        </div>
      </div>
      <BaseButton
        v-if="isMobile"
        variant="nav"
        size="sm"
        :to="{ name: 'card-details', params: { uuid: props.uuid } }"
        class="ms-auto expand"
      >
        <IconArrowsDiagonal />
      </BaseButton>
      <InterstitialCloseButton :class="isMobile ? 'ms-3' : 'ms-auto'" />
    </div>
    <BaseButton
      v-if="!isMobile"
      variant="link"
      :to="{ name: 'card-details', params: { uuid: props.uuid } }"
      class="link-more w-100 rounded-top-0 border-top"
    >
      <IconChevronDown class="me-1" />
      Show More
    </BaseButton>
  </div>
</template>

