import extensionApi from "@/lib/extension/appExtensionApi";
import { v4 as uuidv4 } from "uuid";
import type { ServiceWorkerApiResponse } from "../extension/types";
import { removeEmptyParams } from "./format";

const apiUrl = import.meta.env.EXPOSED_PRIVACY_WEB_BASE_URL;

export type ApiResponse<T = any> = ServiceWorkerApiResponse<T>;
export type ApiError = Error;

export class PrivacyApi {
  readonly appVersion: string = "";
  readonly apiVersion: string;
  readonly baseURL: string;

  constructor(version: "1" | "2" = "1") {
    try {
      this.appVersion = extensionApi.getManifest()?.version || this.appVersion;
    } catch (err) {
      // noop
    }

    this.apiVersion = version;
    this.baseURL = `${apiUrl}/api/v${version}/extension`;
  }

  get(url: string, params?: Record<string, any>, config?: any) {
    return this.request(url, "GET", config, params);
  }

  post(url: string, data?: any, config?: RequestInit) {
    return this.request(url, "POST", config, {}, data);
  }

  patch(url: string, data?: any, config?: RequestInit) {
    return this.request(url, "PATCH", config, {}, data);
  }

  put(url: string, data?: any, config?: RequestInit) {
    return this.request(url, "PUT", config, {}, data);
  }

  delete(url: string, config?: RequestInit) {
    return this.request(url, "DELETE", config, {});
  }

  // Wrap fetch to automatically include request headers
  // and to handle error responses
  private async request(
    url: string,
    method: string,
    config: RequestInit = {},
    params?: Record<string, any>,
    body?: Record<string, any>
  ) {
    config = JSON.parse(JSON.stringify(config));
    body = JSON.parse(JSON.stringify(body || {}));

    params = removeEmptyParams(params);
    await PrivacyApi.authTokenInterceptor(config);
    await PrivacyApi.setSessionIdInterceptor(config);
    PrivacyApi.setApiHeaders(config);

    url = this.baseURL + url;

    return extensionApi.message
      .send({
        event: "sendApiRequest",
        payload: { url, method, params, body, config },
      })
      .then(PrivacyApi.responseInterceptor);
  }

  private static async responseInterceptor(response: ServiceWorkerApiResponse) {
    if (response.ok) {
      return response;
    }

    const isLogin = response.url?.endsWith("/auth/login");

    if (!isLogin && response.status === 401) {
      try {
        await extensionApi.storage.clear({ keepDeviceId: true });
      } catch (err) {
        // noop
      } finally {
        // @ts-ignore
        window.location.assign(
          extensionApi.popup ? "/index.html" : "/interstitial.html"
        );
      }

      return response;
    }

    const errorData = response.data || { message: response.text };
    return Promise.reject(errorData);
  }

  private static async authTokenInterceptor(config: RequestInit) {
    try {
      const { token } = await extensionApi.storage.get("token");

      if (token) {
        config.headers = {
          ...config.headers,
          Authorization: `Bearer ${token}`,
        };
      }
    } catch (err) {
      // noop
    }

    return config;
  }

  private static async setSessionIdInterceptor(
    config: RequestInit
  ): Promise<Record<string, any>> {
    let sessionID;

    try {
      const { sessionID: session } = await extensionApi.storage.get(
        "sessionID"
      );
      sessionID = session;
    } catch (err) {
      // noop
    }

    if (!sessionID) {
      sessionID = uuidv4();

      try {
        await extensionApi.storage.set({ sessionID });
      } catch (err) {
        // noop
      }
    }

    config.headers = {
      ...config.headers,
      sessionID,
    };

    return config;
  }

  static setApiHeaders(config: RequestInit) {
    config.headers = {
      "Content-Type": "application/json",
      "x-extension-id": "djEuMC4x",
      "x-extension-version": extensionApi.getManifest()?.version || "",
      ...config.headers,
    };
    return config;
  }
}

const v1Api = new PrivacyApi();
const v2Api = new PrivacyApi("2");

export { v1Api, v2Api };
