import { CARD_EVENTS } from "@/types/Event";
import { track } from "@/apis/event/event";
import popupAlert from "../popupAlert";
import type { Ref } from "vue";

const copyToClipboard = async (
  text: string,
  type: string,
  eventName: CARD_EVENTS
) => {
  try {
    await navigator.clipboard.writeText(text);
    popupAlert(`Copied ${type} to clipboard`, "success");
  } catch (err) {
    popupAlert("Failed to copy to clipboard", "danger");
  }

  try {
    await track({ name: eventName });
  } catch (e) {
    // ignore
  }
};

export function useClickToCopy({
  exp,
  cvv,
  pan,
}: {
  exp: Ref<string>;
  cvv: Ref<string>;
  pan: Ref<string>;
}) {
  const copyExp = () =>
    copyToClipboard(exp.value, "expiration", CARD_EVENTS.EXP_COPIED);
  const copyCVV = () =>
    copyToClipboard(cvv.value, "CVV", CARD_EVENTS.CVV_COPIED);
  const copyPAN = () =>
    copyToClipboard(pan.value, "card number", CARD_EVENTS.PAN_COPIED);

  return {
    copyExp,
    copyCVV,
    copyPAN,
  };
}
