import type { ServiceWorkerApiResponse } from "../extension/types";

export async function transformFetchResponse(response: Response) {
  const { ok, status, url } = response;
  const headers = Object.fromEntries(response.headers.entries());

  const respObj: ServiceWorkerApiResponse = {
    data: undefined,
    ok,
    status,
    url,
    headers,
  };

  if (response.ok) {
    try {
      respObj.data = await response.json();
    } catch (err) {
      // noop
    }
  } else {
    try {
      respObj.data = JSON.parse(await response.clone().text());
    } catch (err) {
      respObj.text = await response.text();
    }
  }

  return respObj;
}

export function transformFetchError(
  err: any,
  url: ServiceWorkerApiResponse["url"]
) {
  return {
    data: undefined,
    ok: false,
    url,
    text: err.message,
  };
}

export function removeEmptyParams(params: Record<string, any> = {}) {
  return Object.fromEntries(
    Object.entries(params || {}).filter(([, value]) => value !== undefined)
  );
}
