<script lang="ts">
import { SpendLimitDuration } from "@/types/Card";

interface ModalProps {
  isSingleUse: boolean;
  amount: number | null;
  cardIsShared: boolean;
  duration: string;
  maxAmount?: number;
  open: boolean;
}

interface DurationOptions {
  label: string;
  value: string;
}

export type SubmitEvent = {
  amount: number | null;
  duration: SpendLimitDuration | "REMOVE";
};

interface ModalEvents {
  (e: "submit", { amount, duration }: SubmitEvent): void;
}

const limits: DurationOptions[] = Object.entries(SpendLimitDuration).map(
  ([key, value]) => {
    let label = key.charAt(0).toUpperCase() + key.slice(1).toLowerCase();

    if (key === SpendLimitDuration.TRANSACTION) {
      label = `Per ${label}`;
    }

    return {
      value,
      label,
    };
  }
);
</script>

<script setup lang="ts">
import { computed, ref, watchEffect } from "vue";
import BaseButton from "@/components/BaseButton/BaseButton.vue";
import BaseModal from "@/components/BaseModal/BaseModal.vue";
import BaseAlert from "@/components/BaseAlert/BaseAlert.vue";
import BaseSelectList from "@/components/BaseSelectList/BaseSelectList.vue";
import BaseRangeInput from "@/components/BaseRangeInput/BaseRangeInput.vue";
import BaseInputGroup from "@/components/BaseInputGroup/BaseInputGroup.vue";

const props = withDefaults(defineProps<ModalProps>(), {
  maxAmount: 5000,
});
const emit = defineEmits<ModalEvents>();

const currentDuration = ref("");
const currentAmount = ref(props.amount || 1);
const invalid = ref("");
const amountInput = ref<HTMLInputElement | null>(null);
const form = ref<HTMLFormElement | null>(null);

const limitOptions = computed(() => {
  const options = limits.slice();

  if (props.cardIsShared) {
    const txnIndex = options.findIndex(
      (option) => option.value === SpendLimitDuration.TRANSACTION
    );
    options.splice(txnIndex, 1);
  }

  return options;
});

watchEffect(() => {
  currentAmount.value = props.amount || 1;
  currentDuration.value = props.duration;
});

function updateAmount(event: Event) {
  const target = event.target as HTMLInputElement;

  currentAmount.value = target.valueAsNumber;
  invalid.value = amountInput.value?.validationMessage || "";
}

function onSubmit(event: Event) {
  event.preventDefault();

  emit("submit", {
    amount: currentAmount.value,
    duration: currentDuration.value as SpendLimitDuration,
  });
}

function onRemove() {
  emit("submit", {
    amount: null,
    duration: "REMOVE",
  });
}

function reset() {
  currentDuration.value = props.duration || "";
  currentAmount.value = props.amount || 1;
  invalid.value = "";
}
</script>

<template>
  <BaseModal
    class="spend-limit-modal"
    title="Spend Limit"
    :open="props.open"
    overflow="visible"
    @close="reset"
    :showCloseButton="true"
  >
    <form id="spend-limits" ref="form" novalidate @submit="onSubmit">
      <BaseAlert variant="danger" v-if="invalid" class="mb-2">
        {{ invalid }}
      </BaseAlert>
      <BaseAlert variant="info" v-if="cardIsShared" class="mb-2">
        The spend limit for Shared Cards cannot exceed ${{
          new Intl.NumberFormat("en-US").format(maxAmount)
        }}.
      </BaseAlert>
      <BaseAlert variant="info" v-if="isSingleUse" class="mb-2">
        Single-Use cards close shortly after the first transaction.
      </BaseAlert>
      <div class="container-fluid p-0">
        <div class="row form-group rounded-top gx-1">
          <div class="col">
            <BaseInputGroup prefix="$">
              <input
                ref="amountInput"
                type="number"
                inputmode="numeric"
                class="form-control limit-amount ps-1"
                id="spend-limit"
                name="amount"
                min="1"
                required
                aria-label="Amount"
                :value="currentAmount"
                @input="updateAmount"
                :max="maxAmount"
              />
            </BaseInputGroup>
          </div>
          <div class="col">
            <div
              v-if="isSingleUse"
              class="duration-label"
              data-test="duration-label"
            >
              Single Use
            </div>
            <BaseSelectList
              v-else
              class="duration-select"
              v-model="currentDuration"
              name="duration"
              data-test="duration-select"
              :options="limitOptions"
            />
          </div>
        </div>
        <div class="row slider gx-1">
          <div class="form-group py-2 px-3 rounded-bottom">
            <BaseRangeInput
              name="amount"
              v-model="currentAmount"
              :min="1"
              :max="maxAmount"
              @input="updateAmount"
            />
          </div>
        </div>
      </div>
    </form>
    <template #footer>
      <BaseButton
        variant="primary"
        type="submit"
        form="spend-limits"
        :disabled="!!invalid"
      >
        Done
      </BaseButton>
      <BaseButton
        data-test="remove-limit"
        variant="secondary"
        @click="onRemove"
        v-if="!cardIsShared && !isSingleUse"
      >
        Remove
      </BaseButton>
    </template>
  </BaseModal>
</template>

