<script lang="ts">
interface ModalProps {
  open?: boolean;
  overflow?: "auto" | "hidden" | "scroll" | "visible";
  showFooter?: boolean;
  showHeader?: boolean;
  showCloseButton?: boolean;
  title?: string;
  padSides?: boolean;
}
</script>

<script setup lang="ts">
import { watchEffect, ref, computed } from "vue";

import BaseButton from "@/components/BaseButton/BaseButton.vue";
import IconCross from "@/components/icons/IconCross.vue";

const props = withDefaults(defineProps<ModalProps>(), {
  overflow: "auto",
  showHeader: true,
  showFooter: true,
  showCloseButton: false,
  padSides: true,
});

const dialog = ref<HTMLDialogElement | null>(null);

const classes = computed(() => ({
  [`overflow-${props.overflow}`]: props.overflow,
}));

function closeModal() {
  dialog.value?.close?.();
}

function clickCloseDialog(event: MouseEvent) {
  if (event.target === dialog.value) {
    closeModal();
  }
}

watchEffect(() => {
  if (props.open && dialog.value) {
    dialog.value.showModal?.();

    dialog.value.addEventListener("click", clickCloseDialog, { capture: true });
  } else {
    closeModal();
  }
});
</script>

<template>
  <dialog
    ref="dialog"
    class="dialog shadow-lg overflow-y-scroll"
    :class="classes"
  >
    <div :class="`${padSides ? 'p-4' : 'py-4'} content`">
      <header class="dialog-header pb-4" v-if="showHeader">
        <slot name="header">
          <h5 class="dialog-title h2">{{ title }}</h5>
        </slot>
        <BaseButton
          v-if="showCloseButton"
          variant="nav"
          size="sm"
          @click="closeModal"
          class="dialog-close-btn"
        >
          <IconCross />
        </BaseButton>
      </header>
      <div class="dialog-body"><slot /></div>
      <div class="dialog-footer pt-4" v-if="showFooter">
        <slot name="footer" />
      </div>
    </div>
  </dialog>
</template>

<style scoped lang="scss">
@import "~bootstrap/scss/mixins";

.dialog {
  padding: 0;

  &::backdrop {
    background: rgba($color-privacy-gray-1, 0.5);
