<script setup lang="ts">
import { computed } from "vue";
import {
  CardType,
  type CardStyle,
  PrivacyCardNetwork,
  CardState,
  type Card,
} from "@/types/Card";
import { useClickToCopy } from "@/lib/cards/clickToCopy";
import IconVisa from "../icons/IconVisa.vue";
import IconMastercard from "../icons/IconMastercard.vue";
import IconCopy from "../icons/IconCopy.vue";
import CardStateBanner from "./CardStateBanner.vue";
import CardHeader from "./CardHeader.vue";
import { getCustomCardColors } from "@/lib/cards/style";
import type { MerchantCategory } from "@/types/MerchantCategory";
import {
  SubscriptionFeaturesEnum,
  type SubscriptionFeatures,
} from "@/types/Subscription";

const cardNetworkMap = {
  [PrivacyCardNetwork.VISA]: IconVisa,
  [PrivacyCardNetwork.MASTERCARD]: IconMastercard,
};

const props = withDefaults(
  defineProps<{
    loading?: boolean;
    copyInfo?: boolean;
    cvv?: string;
    expMonth?: string;
    expYear?: string;
    isExpiring?: boolean;
    mcc?: string;
    mccIconUrl?: string;
    network: PrivacyCardNetwork;
    newCard?: boolean;
    pan?: string;
    type: CardType;
    style?: CardStyle;
    state?: CardState;
    merchantCategory?: Card["merchantCategory"];
    subscriptionFeatures?: SubscriptionFeatures;
  }>(),
  {
    loading: false,
    copyInfo: true,
    newCard: false,
  }
);

const networkIcon = computed(() => {
  return cardNetworkMap[props.network];
});

const formattedPAN = computed(() => {
  const pan = props.pan || "".repeat(16);
  return pan.replace(/(.{4})/g, "$1 ").trim();
});
const cvv = computed(() => props.cvv || "");
const pan = computed(() => props.pan || "");
const exp = computed(() => {
  let expString = "/";

  if (props.expMonth && props.expYear) {
    expString = `${props.expMonth}/${props.expYear.slice(-2)}`;
  }

  return expString;
});
const isUnlocked = computed(
  () => props.type === CardType.UNLOCKED && !props.merchantCategory
);
const isCategoryLocked = computed(
  () => props.type === CardType.UNLOCKED && !!props.merchantCategory
);
const isCustomizableCard = computed(() => {
  return props.type === CardType.MERCHANT_LOCKED || isCategoryLocked.value;
});
const hasCategoryCardsFeature = computed(
  () => props.subscriptionFeatures?.[SubscriptionFeaturesEnum.CATEGORY_CARDS]
);
const hasDigitalWalletCardsFeature = computed(
  () =>
    props.subscriptionFeatures?.[SubscriptionFeaturesEnum.DIGITAL_WALLET_CARDS]
);
const computedState = computed(() => {
  if (
    props.state === CardState.PAUSED &&
    ((!!props.merchantCategory && !hasCategoryCardsFeature.value) ||
      (props.type === CardType.DIGITAL_WALLET &&
        !hasDigitalWalletCardsFeature.value))
  ) {
    return "paused-force";
  }

  return props.state;
});

const { copyExp, copyCVV, copyPAN } = useClickToCopy({ cvv, exp, pan });

let bgColor = "";
let textColor = "#FFFFFF";

const isCustomCard =
  props.type !== CardType.DIGITAL_WALLET &&
  props.type !== CardType.SINGLE_USE &&
  (!!props.style?.bgColor || !!props.style?.filename || !!props.style?.icon);

if (isCustomCard) {
  const customColors = getCustomCardColors(props.style?.bgColor);

  if (customColors) {
    bgColor = customColors.bgColor;
    textColor = customColors.textColor;
  }
}

const cardBgClasses = computed(() => {
  const classes: Record<string, boolean> = {};

  if (props.type === CardType.SINGLE_USE) {
    classes["single-use-card"] = true;
  }

  if (isUnlocked.value) {
    classes["unlocked-card"] = true;
  }

  if (props.type === CardType.DIGITAL_WALLET) {
    classes["digital-wallet"] = true;
  }

  if (isCustomizableCard.value && isCustomCard) {
    classes["locked-custom"] = true;
  }

  if (isCustomizableCard.value && !isCustomCard) {
    classes["locked-default"] = true;
  }

  if (props.isExpiring) {
    classes["expiring"] = true;
  }

  return classes;
});

const cardBottomClasses = computed(() => {
  const classes: Record<string, boolean> = {};

  if (props.type === CardType.MERCHANT_LOCKED) {
    if (isCustomCard) {
      classes["card-bottom-custom"] = true;
    } else {
      classes["card-bottom-default"] = true;
    }
  } else if (isCategoryLocked.value) {
    classes["card-bottom-custom"] = true;
  }

  return classes;
});
</script>

<template>
  <div v-if="loading" class="card p-3" data-test="placeholder">
    <div class="placeholder-glow">
      <span class="placeholder col-3 placeholder-logo mb-3" />
    </div>
    <div class="placeholder-glow mb-2">
      <span class="placeholder col-10" />
    </div>
    <div class="placeholder-glow">
      <span class="placeholder col-5" />
      <span class="placeholder col-4 offset-1" />
    </div>
  </div>
  <div
    v-else
    class="card d-flex flex-column justify-content-between shadow-sm"
    :class="cardBgClasses"
    data-test="privacy-card"
    :style="{ backgroundColor: bgColor }"
  >
    <CardHeader
      :type="type"
      :style="style"
      :mccIconUrl="mccIconUrl"
      :merchant-category="merchantCategory"
    />
    <CardStateBanner :state="computedState" />
    <div
      v-if="newCard && merchantCategory"
      class="merchant-category-description"
    >
      {{ (merchantCategory as MerchantCategory).description }}
    </div>
    <div v-else class="card-bottom p-3" :class="cardBottomClasses">
      <div class="card-info d-flex align-items-center">
        <div
          class="attr card-info-pan"
          :class="{ clickable: copyInfo }"
          @click="copyPAN"
          data-test="card-pan"
        >
          <IconCopy />
          {{ formattedPAN }}
        </div>
      </div>
      <div class="d-flex card-info align-items-center mt-2">
        <div
          class="attr card-info-exp"
          :class="{ clickable: copyInfo }"
          @click="copyExp"
          data-test="card-exp"
        >
          <span class="label me-1">EXP</span>
          {{ exp }}
        </div>
        <div
          class="attr card-info-cvv"
          :class="{ clickable: copyInfo }"
          @click="copyCVV"
          data-test="card-cvv"
        >
          <span class="label me-1">CVV</span>
          {{ cvv }}
        </div>
        <component
          v-if="networkIcon"
          :is="networkIcon"
          class="ms-auto mt-auto card-network"
          :aria-label="network"
        />
      </div>
    </div>
  </div>
</template>

