  background: url("@/assets/images/card-locked-default.svg") center center,
    linear-gradient(292.13deg, #fbf5ef 0%, #4949f1 64%, #2121a4 100%);
  background-size: 100%;
}

.locked-custom {
  background-image: url("@/assets/images/card-locked-custom.svg");
  background-size: 100%;
}

.single-use-card {
  background: linear-gradient(292deg, #ed8b72 0%, #c12943 80.5%);
  background-size: 100%;
}

.unlocked-card {
  background: url("@/assets/images/card-unlocked-default.svg") center center,
    linear-gradient(292deg, #7221a4 0%, #4949f1 80%);
  background-size: 100%;
}

.digital-wallet {
  background: url("@/assets/images/card-digital-wallet.svg") center center,
    linear-gradient(292deg, #000 0%, #000 100%);
  background-size: 100%;
}

