  font-weight: $font-weight-medium;

  &:not(.clickable) {
    pointer-events: none;
  }
}

.card-bottom {
  font-family: $font-family-sans-serif;
  font-size: 16px;
  font-weight: 500;
}

.card-bottom-custom {
  background-color: fade-out($black, 0.9);
