  background-color: fade-out($brand-purple, 0.9);
  backdrop-filter: blur(32px);
}

.merchant-category-description {
  background-color: var(--bs-body-bg);
  color: var(--bs-body-color);
  text-align: center;
  padding: 24px 16px 16px 16px;
}

.card-info {
  .label {
    font-size: 14px;
    opacity: 50%;
  }
}

.card-info-cvv {
  padding-left: 16px;
}

.card-info-exp,
.card-info-cvv {
  display: flex;
  flex-direction: row;
  align-items: flex-end;
}

.card-network {
  height: 21px;
  width: 34px;
  object-fit: contain;
}

.expiring::before {
  content: "";
  width: 34px;
  height: 34px;
  position: absolute;
  right: $spacer;
  top: $spacer;
  background-image: url("@/assets/images/expiring-clock.png");
