<script setup lang="ts">
import { inject } from "vue";
import { useAuthStore } from "@/store/auth/authStore";
import { useEventStore } from "@/store/event/eventStore";
import extensionApi from "@/lib/extension/appExtensionApi";
import BaseButton from "@/components/BaseButton/BaseButton.vue";
import popupAlert from "@/lib/popupAlert";
import { USER_EVENTS } from "@/types/Event";
import { CONTEXT } from "@/types/App";

const authStore = useAuthStore();
const eventStore = useEventStore();
const interstitial = inject("context") === CONTEXT.INTERSTITIAL;
const redirect = interstitial ? "/interstitial.html" : "/index.html";

async function logout(): Promise<void> {
  try {
    await authStore.logout();
    await extensionApi.storage.clear();

    window.location.assign(redirect);
  } catch (error: any) {
    eventStore.error([
      {
        name: USER_EVENTS.LOGOUT,
        data: {
          message: error.message,
          error: error,
        },
      },
    ]);

    popupAlert("Unable to logout, please try again");
  }
}
</script>

<template>
  <BaseButton variant="link" data-test="logout" @click="logout">
    Logout
  </BaseButton>
</template>
