<script lang="ts">
export default {
  inheritAttrs: false,
};
</script>

<script setup lang="ts">
import { nextTick, onUnmounted, ref, watchEffect, type Ref } from "vue";
import {
  createPopperLite,
  flip,
  preventOverflow,
  type Instance as PopperInstance,
} from "@popperjs/core";

withDefaults(
  defineProps<{
    tag?: string;
    tooltip?: string;
    variant: "danger" | "info";
  }>(),
  {
    tag: "div",
    variant: "info",
  }
);

let popper: PopperInstance | null = null;
const id = `tooltip-${Math.random().toString(36).substring(2)}`;
const visible = ref(false);
const tooltipEl: Ref<HTMLElement | null> = ref(null);
const triggerEl: Ref<HTMLElement | null> = ref(null);

const updateTooltip = async (show = true) => {
  if (!popper) {
    // @ts-ignore amountEl ref is not null
    popper = createPopperLite(triggerEl.value, tooltipEl.value, {
      placement: "top-end",
      modifiers: [flip, preventOverflow],
    });
  }

  visible.value = show;
  await nextTick();
  popper.update();
};

watchEffect(() => {
  if (triggerEl.value) {
    triggerEl.value.addEventListener("mouseenter", () => {
      updateTooltip(true);
    });

    triggerEl.value.addEventListener("mouseleave", () => {
      updateTooltip(false);
    });
  }
});

onUnmounted(() => {
  popper?.destroy();
});
</script>

<template>
  <component :is="tag" ref="triggerEl" v-bind="$attrs" :aria-describedby="id">
    <slot />
  </component>
  <div
    class="tooltip fw-medium"
    :class="[variant, { show: visible }]"
    ref="tooltipEl"
    :aria-hidden="!visible"
    :id="id"
  >
    <slot name="content">{{ tooltip }}</slot>
  </div>
</template>

<style scoped lang="scss">
@import "~bootstrap/scss/mixins/color-mode.scss";

.tooltip {
  display: none;
  background-color: var(--bs-body-bg);
  box-shadow: 0 0 0 1px rgba($color-privacy-black, 0.08), $box-shadow-dark;
  border-radius: calc($spacer / 4);
  padding: $spacer * 0.375 $spacer * 0.5;
  z-index: 1;

  &.show {
    display: inline-block;
  }
}

@include color-mode(dark) {
  .tooltip {
    box-shadow: 0 0 0 1px $color-privacy-gray-4,
      0 4px 12px 0 rgba($color-privacy-black, 0.32);
  }
}

.danger {
  background-color: $danger-bg-subtle;
  color: $red;
}
</style>
