<script lang="ts">
export default {
  name: "TheAppLayout",
};
</script>

<script setup lang="ts">
import { computed } from "vue";
import { useRoute } from "vue-router";

const route = useRoute();
const layout = computed(() => {
  const requiresAuth = route?.meta?.requiresAuth;
  let layout = route?.meta?.layout;

  if (requiresAuth && !layout) {
    layout = "Section";
  }

  if (layout) {
    return `The${layout}Layout`;
  }

  return `TheDefaultLayout`;
});
</script>

<template>
  <component :is="layout">
    <router-view />
  </component>
</template>
