<script lang="ts">
interface Emits {
  (event: "update", value: string): void;
}

interface Props {
  value: string;
}
</script>

<script setup lang="ts">
import { ref } from "vue";
import BaseInput from "@/components/BaseInput/BaseInput.vue";

const emit = defineEmits<Emits>();
const props = defineProps<Props>();

const nickname = ref(props.value);

function handleUpdate() {
  if (nickname.value === "") {
    nickname.value = props.value;
    return;
  }

  emit("update", nickname.value.trim());
}

function handleKeyUp(event: KeyboardEvent) {
  const target = event.target as HTMLInputElement;
  target.blur();
}
</script>

<template>
  <label class="h1 nickname clickable w-100 m-0" aria-label="Card Nickname">
    <div
      class="memo text-truncate"
      aria-hidden="true"
      data-test="nickname-label"
    >
      {{ nickname }}
    </div>
    <BaseInput
      class="nickname-input"
      maxlength="255"
      name="nickname"
      v-model="nickname"
      @keyup.stop.prevent.enter="handleKeyUp"
      @blur="handleUpdate"
    />
  </label>
</template>

<style lang="scss" scoped>
.nickname {
  transition: $input-transition;
  border-radius: $input-border-radius;
  display: block;
  line-height: unset;

  &:hover {
    outline: 2px solid $focus-ring-color;
  }

  &:focus-within {
    box-shadow: $input-focus-box-shadow;
    outline: none;
    transition: outline 0.15s ease-out;

    .memo {
      position: absolute;
      left: -9999px;
    }

    .nickname-input {
      position: initial;
      left: initial;
    }
  }
}

.memo {
  padding: $input-padding-y $input-padding-x;
  border: var(--bs-border-width) solid transparent;
  line-height: normal;
}

.nickname-input {
  position: absolute;
  left: -9999px;
  font-size: inherit;
  font-family: inherit;
  font-weight: inherit;
  line-height: normal;

  &:focus {
    box-shadow: none !important;
  }
}
</style>
