import { computed, ref } from "vue";
import { defineStore } from "pinia";
import { cards } from "@/apis";
import type { Card } from "@/types/Card";

type CardList = {
  total: number;
  cards: string[];
};

type CardsStates = {
  cards: Record<string, Card>;
  lists: Record<string, CardList>;
};

export default defineStore("cards", () => {
  // STATES
  const cardsState = ref<CardsStates["cards"]>({});
  const listsState = ref<CardsStates["lists"]>({});

  // GETTERS
  const card = computed(() => (cardUuid: string) => cardsState.value[cardUuid]);

  // ACTIONS
  async function list(
    params: cards.ListCardParams = {}
  ): Promise<cards.ListCardResponse> {
    params = {
      fullInfo: true,
      spentThisMonth: true,
      ...params,
    };

    const cacheKey = JSON.stringify(params);

    if (listsState.value[cacheKey]) {
      return Promise.resolve({
        total: listsState.value[cacheKey].total,
        data: listsState.value[cacheKey].cards.map(
          (cardUuid) => cardsState.value[cardUuid]
        ),
      });
    }

    const { data } = await cards.list(params);

    const cardUuids: string[] = [];
    data.data.forEach((card) => {
      cardsState.value[card.cardUuid] = card;
      cardUuids.push(card.cardUuid);
    });

    listsState.value[cacheKey] = {
      total: data.total,
      cards: cardUuids,
    };

    return data;
  }

  async function get(
    cardUuid: string,
    params: cards.GetCardParams = {}
  ): Promise<Card> {
    params = {
      fullInfo: true,
      spentThisMonth: true,
      ...params,
    };

    if (cardsState.value[cardUuid]) {
      return Promise.resolve(cardsState.value[cardUuid]);
    }

    const { data } = await cards.get(cardUuid, params);
    cardsState.value[data.cardUuid] = data;

    return data;
  }

  async function create(params?: cards.CreateCardData): Promise<Card> {
    const { data } = await cards.create(params);

    cardsState.value[data.cardUuid] = data;

    // Reset all lists as their data is no longer valid
    listsState.value = {};

    return data;
  }

  async function update(
    cardUuid: string,
    params: cards.UpdateCardData
  ): Promise<Card> {
    const { data } = await cards.update(cardUuid, params);

    cardsState.value[data.cardUuid] = data;

    return data;
  }

  function $reset() {
    cardsState.value = {};
    listsState.value = {};
  }

  async function setState(
    cardUuid: string,
    params: cards.SetStateData
  ): Promise<Card> {
    // Optimistic update to the UI - this will
    // be replaced with data from API call
    const current = cardsState.value[cardUuid];
    cardsState.value[cardUuid] = { ...current, ...params } as Card;

    try {
      const { data } = await cards.setState(cardUuid, params);
      cardsState.value[data.cardUuid] = data;

      if (params.state === "CLOSED") {
        // Reset all lists as their data is no longer valid
        listsState.value = {};
      }

      return data;
    } catch (error) {
      // Revert optimistic update
      cardsState.value[cardUuid] = current;

      throw error;
    }
  }

  async function setNote(
    cardUuid: string,
    params: cards.SetNoteData
  ): Promise<Card> {
    const { data } = await cards.setNote(cardUuid, params);

    cardsState.value[data.cardUuid] = data;

    return data;
  }

  return {
    // actions
    $reset,
    create,
    get,
    list,
    update,
    setState,
    setNote,

    // getters
    card,

    // states
    lists: listsState,
    cards: cardsState,
  };
});
