<script setup lang="ts">
import { useError } from "@/lib/errors/errors";
import { useAuthStore } from "@/store/auth/authStore";
import ThePasswordReset from "@/components/ThePasswordReset/ThePasswordReset.vue";
import { ref } from "vue";

const { sendPasswordResetEmail } = useAuthStore();
const { errorMessage, resetError, setError } = useError();
const resetCompleted = ref(false);

const resetPassword = async (email: string) => {
  resetError();

  try {
    await sendPasswordResetEmail(email);
    resetCompleted.value = true;
  } catch (err) {
    setError(err, "Error sending password reset email");
  }
};
</script>

<template>
  <div class="px-4">
    <ThePasswordReset
      :reset-completed="resetCompleted"
      :error-msg="errorMessage"
      @submit="resetPassword"
    />
  </div>
</template>
