import { type Card, CardType } from "@/types/Card";
import { computed, type Ref } from "vue";

export default function useCardNickname(card: Ref<Card>) {
  const nickname = computed(() => {
    const current = card.value;

    const lastOption =
      current.type === CardType.SINGLE_USE ? "New Burner" : "New Card";

    return (
      current?.memo ||
      current?.hostname ||
      current?.meta?.hostname ||
      lastOption
    );
  });

  return nickname;
}
