import { createApp } from "vue";
import * as Sentry from "@sentry/vue";

import App from "./App.vue";
import router from "./router";
import store, { initAllStores } from "./store";
import { useEventStore } from "./store/event/eventStore";
import { registerLayouts } from "./layouts/register";

import "./assets/theme.scss";
import { ERROR_EVENTS, type Event } from "./types/Event";
import { CONTEXT } from "./types/App";
import appExtensionApi from "@/lib/extension/appExtensionApi";

const app = createApp(App);

app.use(store);
app.provide("context", CONTEXT.POPUP);

const eventStore = useEventStore();

app.config.errorHandler = async (err, vm, info) => {
  const tab = await appExtensionApi.message.send({ event: "getCurrentTab" });
  const errEvent: Event = {
    name: ERROR_EVENTS.UNHANDLED,
    data: { err, component: vm?.$options.__name, info, url: tab?.url },
  };

  try {
    await eventStore.error([errEvent]);
  } catch (err) {
    console.warn(err);
  }
};

Sentry.init({
  app,
  integrations: [Sentry.debugIntegration()],
  dsn: "https://839c6c53a4ef04849e6410167a8a00f0@o1053332.ingest.us.sentry.io/4507618374909952",
  tracesSampleRate: 1.0, //  Capture 100% of the transactions
  // Set 'tracePropagationTargets' to control for which URLs distributed tracing should be enabled
  tracePropagationTargets: ["localhost", /^https:\/\/app\.privacy\.com\/api/],
});

initAllStores().then(() => {
  app.use(router);
  registerLayouts(app);

  // In Vue router's memory mode, need to manually push the initial state
  router.push({ name: "index" });
  app.mount("#app");
});
