<script lang="ts">
interface Emits {
  (event: "update", value: string): void;
}

interface Props {
  value: string;
  loading: boolean;
  hasCardNoteFeature: boolean;
}
</script>

<script setup lang="ts">
import { computed, ref } from "vue";
import BasePill from "@/components/BasePill/BasePill.vue";
import BaseTextArea from "@/components/BaseTextArea/BaseTextArea.vue";
import IconPadlock from "@/components/icons/IconPadlock.vue";

const emit = defineEmits<Emits>();
const props = withDefaults(defineProps<Props>(), {
  loading: false,
  hasCardNoteFeature: false,
});

const MAX_NOTE_LENGTH = 300;
const MAX_ROWS = 12;
const note = ref(props.value ?? "");
const isFocused = ref(false);
const charactersLeft = computed<number>(
  () => MAX_NOTE_LENGTH - (note?.value?.length ?? 0)
);

function handleUpdate() {
  isFocused.value = false;

  if (note.value === props.value) {
    return;
  }

  emit("update", note.value);
}
</script>

<template>
  <div>
    <section class="note-section rounded-3">
      <div v-if="loading" class="p-3" aria-hidden="true">
        <div class="placeholder-glow">
          <span class="placeholder col-10" />
        </div>
      </div>
      <label v-else class="note clickable m-0 w-100" aria-label="Card Note">
        <p
          :class="[
            'note-text',
            'm-0',
            'p-3',
            {
              'note-placeholder': !note,
              'note-disabled': !hasCardNoteFeature,
            },
          ]"
          aria-hidden="true"
          data-test="note-label"
        >
          {{ !!note ? note : "Add a note..." }}
        </p>
        <BaseTextArea
          class="note-input p-3"
          :maxlength="MAX_NOTE_LENGTH"
          :rows="MAX_ROWS"
          name="note"
          v-model="note"
          @blur="handleUpdate"
          @focus="isFocused = true"
          :disabled="!hasCardNoteFeature"
        />
      </label>
    </section>
    <div v-if="isFocused" data-test="note-edit">
      <p class="note-counter m-0 p-2" data-test="note-counter">
        {{ charactersLeft }} / {{ MAX_NOTE_LENGTH }}
      </p>
      <p class="note-warning m-0 p-2" data-test="note-warning">
        <span class="fw-medium">Please note:</span>
        For your security, avoid entering sensitive account information here,
        such as passwords or credit card numbers.
      </p>
    </div>
    <BasePill
      v-if="!hasCardNoteFeature"
      class="note-upgrade mt-2 px-3 py-2 fw-medium badge-secondary"
      data-test="note-upgrade"
    >
      <IconPadlock />
      <span class="note-upgrade-text">Upgrade to edit notes</span>
    </BasePill>
  </div>
</template>

