import type {
  LoginPayload,
  CodeLoginWithUserToken,
  LoginResponse,
  TfaLoginResponse,
  LoginSuccess,
  UserToken,
  MessageResponse,
  TfaRecoveryPayload,
  PasswordResetPayload,
} from "@/types/Auth";
import { v2Api, v1Api, type ApiResponse } from "@/lib/request";
import appExtensionApi from "@/lib/extension/appExtensionApi";

const AUTH_ROUTE = "/auth";

export const AUTH_ENDPOINTS = {
  login: `${AUTH_ROUTE}/login`,
  logout: `${AUTH_ROUTE}/logout`,
  tfaLogin: `${AUTH_ROUTE}/login/tfa`,
  sendVerifySms: `${AUTH_ROUTE}/send-verify-sms`,
  removeTFA: `${AUTH_ROUTE}/tfa/reset`,
  sendOneTimeCode: `${AUTH_ROUTE}/tfa/resend`,
  passwordReset: `${AUTH_ROUTE}/password-reset`,
  shortLivedAuthToken: `${AUTH_ROUTE}/token`,
};

export const login = async (
  user: LoginPayload
): Promise<ApiResponse<LoginSuccess>> => {
  const { deviceId } = await appExtensionApi.storage.get("deviceId");
  const config: Record<string, any> = {};

  if (deviceId) {
    config.headers = { "x-tfa-deviceid": deviceId };
  }

  return await v2Api.post(AUTH_ENDPOINTS.login, user, config);
};

export const logout = (): Promise<ApiResponse<any>> => {
  return v2Api.post(AUTH_ENDPOINTS.logout);
};

export const tfaLogin = (
  data: CodeLoginWithUserToken
): Promise<ApiResponse<TfaLoginResponse>> => {
  const { code, userToken, rememberDevice } = data;

  return v2Api.post(AUTH_ENDPOINTS.tfaLogin, {
    token: code,
    userToken,
    rememberDevice,
  });
};

export const oneTimeCodeLogin = (
  data: CodeLoginWithUserToken
): Promise<ApiResponse<LoginResponse>> => {
  const { code, userToken, rememberDevice } = data;

  return v2Api.post(AUTH_ENDPOINTS.tfaLogin, {
    code,
    userToken,
    rememberDevice,
  });
};

export const sendVerifySms = (
  data: UserToken
): Promise<ApiResponse<MessageResponse>> => {
  const { userToken } = data;

  return v1Api.post(AUTH_ENDPOINTS.sendVerifySms, { userToken });
};

export const removeTFA = (
  data: TfaRecoveryPayload
): Promise<ApiResponse<any>> => {
  return v2Api.post(AUTH_ENDPOINTS.removeTFA, data);
};

export const sendOneTimeCode = (
  data: UserToken
): Promise<ApiResponse<MessageResponse>> => {
  const { userToken } = data;

  return v2Api.post(AUTH_ENDPOINTS.sendOneTimeCode, { userToken });
};

export const sendPasswordResetEmail = (
  data: PasswordResetPayload
): Promise<ApiResponse<MessageResponse>> => {
  const { email } = data;
  return v1Api.post(AUTH_ENDPOINTS.passwordReset, { email });
};

export const getShortLivedToken = (): Promise<
  ApiResponse<{ token: string }>
> => {
  return v2Api.post(AUTH_ENDPOINTS.shortLivedAuthToken);
};
