<script lang="ts">
import { computed, ref } from "vue";
import { RouterLink, type RouteLocationRaw } from "vue-router";

export interface ButtonProps {
  variant?:
    | "primary"
    | "secondary"
    | "success"
    | "danger"
    | "link"
    | "icon"
    | "nav"
    | "light";
  size?: "sm" | "lg";
  to?: RouteLocationRaw;
  title?: string;
}
</script>

<script setup lang="ts">
const props = withDefaults(defineProps<ButtonProps>(), { variant: "primary" });
const element = ref<any | null>(null);

const externalHostRegex = /^https?:\/\//i;

const isExternalLink = computed(() =>
  externalHostRegex.test(props.to as string)
);

const href = computed<string>(() => props.to as string);

const classes = computed(() => {
  const classes = {
    btn: true,
    [`btn-${props.variant}`]: props.variant,
  };

  if (props.size) {
    classes[`btn-${props.size}`] = true;
  }

  if (props.variant === "nav") {
    classes["btn-icon"] = true;
    classes["rounded-2"] = true;
  }

  return classes;
});

/**
 * We want to expose the element to the parent component so that we can
 * use it as part of the popper config for the dropdown menu.
 */
defineExpose({ element });
</script>

<template>
  <a
    v-if="isExternalLink"
    :href="href"
    :class="classes"
    target="_blank"
    :ref="(el) => (element = el)"
  >
    <slot />
  </a>
  <RouterLink
    v-else-if="to"
    :to="to"
    :class="classes"
    :ref="(el) => (element = el)"
  >
    <slot />
  </RouterLink>
  <button
    v-else
    type="button"
    :title="props.title"
    :class="classes"
    :ref="(el) => (element = el)"
  >
    <slot />
  </button>
</template>

<style scoped lang="scss">
@import "~bootstrap/scss/mixins/color-mode.scss";

.btn-link {
  font-weight: $font-weight-medium;
}

.btn-icon {
  width: 40px;
  height: 40px;
  padding: $spacer * 0.625;
  border: none;
  color: var(--bs-btn-hover-color);
  line-height: 1;

  &.btn-sm {
    width: 24px;
    height: 24px;
    padding: $spacer * 0.25;
  }
}

.btn-nav {
  color: $color-privacy-gray-3;

  &:hover {
    background-color: var(--bs-tertiary-bg);
  }
}

@include color-mode(dark) {
  .btn-nav {
    color: $color-privacy-gray-2;
    background-color: $color-privacy-gray-4;

    &:hover {
