<script setup lang="ts">
import { ref, computed, onMounted, nextTick, watch } from "vue";
import { debounce } from "@/lib/utils/utils";
import { TransactionResultFilter, type Transaction } from "@/types/Transaction";
import type { ListTransactionParams } from "@/apis/transactions";
import useTransactionsStore from "@/store/transactions";
import BaseAlert from "@/components/BaseAlert/BaseAlert.vue";
import BaseButton from "@/components/BaseButton/BaseButton.vue";
import IconChevronDown from "@/components/icons/IconChevronDown.vue";
import TransactionListItem from "@/components/TransactionListItem/TransactionListItem.vue";
import router from "@/router";

const props = withDefaults(
  defineProps<{
    cardId?: number;
    isCollapsed?: boolean;
    query?: string;
    showCardLink?: boolean;
  }>(),
  {
    isCollapsed: false,
    query: "",
    showCardLink: true,
  }
);

const { list: listTransactions } = useTransactionsStore();

const allDataLoaded = ref(false);
const expanded = ref(!props.isCollapsed);
const initialLoad = ref(true);
const loader = ref<HTMLElement | null>(null);
const loading = ref(false);
const loadingError = ref("");
const searching = ref(false);
const transactions = ref<Transaction[]>(new Array(3).fill({} as Transaction));

const limit = 20;
let offset = 0;
let total = 0;

const showTxnsButton = computed(() => {
  return !expanded.value && transactions.value.length && !initialLoad.value;
});

const showCollapsedNoTxnsMessage = computed(() => {
  return !expanded.value && !transactions.value.length && !initialLoad.value;
});

const showNoTransactions = computed(() => {
  return (
    !transactions.value.length &&
    !loading.value &&
    !loadingError.value &&
    !props.query &&
    !searching.value
  );
});

onMounted(() => {
  observeLoader();
});

function reset() {
  initialLoad.value = true;
  allDataLoaded.value = false;
  offset = 0;
  total = 0;
}

const loadTransactions = async () => {
  loading.value = true;
  loadingError.value = "";

  try {
    const params: ListTransactionParams = {
      fullInfo: true,
      limit,
      offset,
      transactionFilter: TransactionResultFilter.ALL,
    };

    if (props.cardId) {
      params.cardID = props.cardId;
    }

    if (props.query) {
      params.query = props.query;
    }

    const { data, total: totalLoaded } = await listTransactions(params);

    transactions.value = initialLoad.value
      ? data
      : [...transactions.value, ...data];

    offset += limit;
    total = totalLoaded;

    if (offset >= total) {
      allDataLoaded.value = true;
    }
  } catch (err) {
    if (initialLoad.value) {
      transactions.value = [];
    }

    loadingError.value = "Unable to load transactions";
  } finally {
    initialLoad.value = false;
    loading.value = false;
  }
};

async function observeLoader(observeOnly = false) {
  const observer = new IntersectionObserver((entries) => {
    entries.forEach(async (entry) => {
      if (entry.isIntersecting || initialLoad.value) {
        observer.unobserve(loader.value!);

        if (!observeOnly) {
          await loadTransactions();
        }

        if (!allDataLoaded.value && !loadingError.value) {
          observeLoader();
        }
      }
    });
  });

  // Make sure the loader exists before observing it
  await nextTick();
  observer.observe(loader.value!);
}

const debouncedUpdateQuery = debounce(async () => {
  searching.value = false;
  updateQuery();
  await loadTransactions();
  observeLoader(true);
}, 350);

function updateQuery() {
  if (!props.query) {
    router.push({ query: {} });
    return;
  }

  router.push({ query: { q: props.query } });
}

watch(
  () => props.query,
  () => {
    reset();
    searching.value = true;
    debouncedUpdateQuery();
  }
);
</script>

<template>
  <div>
    <BaseButton
      v-if="showTxnsButton"
      class="w-100 text-center mt-2"
      variant="link"
      @click="expanded = true"
      data-test="show-transactions-button"
    >
      <IconChevronDown class="me-1" />
      Show transactions
    </BaseButton>
    <div
      v-else-if="showCollapsedNoTxnsMessage"
      class="user-prompt mt-2 p-2"
      data-test="collapsed-list-no-transactions"
    >
      <p>This card has no transactions yet</p>
    </div>
    <div v-show="expanded">
      <div
        v-if="showNoTransactions"
        class="user-prompt"
        data-test="transaction-list-no-transactions"
      >
        <p>You don't have any transactions</p>
      </div>

      <ul
        class="transaction-list"
        v-if="transactions.length"
        data-test="transaction-list"
        :aria-busy="loading"
      >
        <li
          v-for="(txn, index) in transactions"
          :key="txn.transactionUuid || index"
          class="mb-3"
          :class="{
            'transaction-loader': !txn.transactionUuid && initialLoad,
            'px-4': !cardId,
          }"
        >
          <TransactionListItem
            :transaction="txn"
            :loading="!txn.transactionUuid"
            :show-card-link="showCardLink"
            aria-hidden="true"
          />
        </li>
        <li v-if="loading && !initialLoad" class="transaction-loader px-4">
          <TransactionListItem :loading="loading" aria-hidden="true" />
        </li>
      </ul>

      <div
        v-else-if="!loadingError && props.query.length"
        class="text-center text-body-secondary"
        data-test="transaction-list-no-results"
      >
        <p>No matching transactions found</p>
      </div>

      <div ref="loader">
        <span class="visually-hidden" v-if="loading">
          Loading transactions...
        </span>
      </div>

      <div v-if="loadingError" class="text-center">
        <BaseAlert variant="warning">
          {{ loadingError }}
        </BaseAlert>
        <BaseButton
          variant="primary"
          class="mt-3"
          @click="() => observeLoader()"
          :disabled="loading"
          data-test="retry"
        >
          Try again
        </BaseButton>
      </div>
    </div>
  </div>
</template>

<style lang="scss" scoped>
.transaction-list {
  list-style: none;
  margin: 0;
  padding: 0;
  position: relative;

  @for $i from 2 through 3 {
    :nth-child(#{$i} of li.transaction-loader) {
      opacity: 1 - (0.4 * ($i - 1));
    }
  }

  & > :last-child {
    margin-bottom: 0 !important;
  }
}

.user-prompt {
  display: flex;
  flex-direction: column;
  height: 100%;
  align-items: center;
  justify-content: center;
  text-align: center;
  color: var(--bs-body-secondary);
}
</style>
