import type { CardType } from "./Card";

export enum SettlementStates {
  DECLINED = "DECLINED",
  EXPIRED = "EXPIRED",
  PENDING = "PENDING",
  SETTLED = "SETTLED",
  VOIDED = "VOIDED",
  BOUNCED = "BOUNCED",
}

export enum DisputeStates {
  DISPUTE_OPEN = "DISPUTE_OPEN",
  DISPUTE_PENDING = "DISPUTE_PENDING",
  DISPUTE_REJECTED = "DISPUTE_REJECTED",
  DISPUTE_APPROVED = "DISPUTE_APPROVED",
  DISPUTE_CLOSED = "DISPUTE_CLOSED",
  CHARGEBACK_PENDING = "CHARGEBACK_PENDING",
  CHARGEBACK_APPROVED = "CHARGEBACK_APPROVED",
  CHARGEBACK_REJECTED = "CHARGEBACK_REJECTED",
  INTERNAL_CHARGEBACK_PENDING = "INTERNAL_CHARGEBACK_PENDING",
  INTERNAL_CHARGEBACK_APPROVED = "INTERNAL_CHARGEBACK_APPROVED",
  INTERNAL_CHARGEBACK_REJECTED = "INTERNAL_CHARGEBACK_REJECTED",
  ACTIVE = "ACTIVE",
}

export enum TransactionResultFilter {
  APPROVALS = "APPROVALS",
  DECLINES = "DECLINES",
  ALL = "ALL",
}

export enum DeclineReasons {
  CARD_PAUSED = "CARD_PAUSED",
  CARD_CLOSED = "CARD_CLOSED",
  GLOBAL_TRANSACTION_LIMIT = "GLOBAL_TRANSACTION_LIMIT",
  GLOBAL_MONTHLY_LIMIT = "GLOBAL_MONTHLY_LIMIT",
  USER_TRANSACTION_LIMIT = "USER_TRANSACTION_LIMIT",
  UNAUTHORIZED_MERCHANT = "UNAUTHORIZED_MERCHANT",
  SINGLE_USE_RECHARGED = "SINGLE_USE_RECHARGED",
  BANK_CONNECTION_ERROR = "BANK_CONNECTION_ERROR",
  INSUFFICIENT_FUNDS = "INSUFFICIENT_FUNDS",
  INSUFFICIENT_FUNDS_JIT = "INSUFFICIENT_FUNDS_JIT",
  MERCHANT_BLACKLIST = "MERCHANT_BLACKLIST",
  INVALID_CARD_DETAILS = "INVALID_CARD_DETAILS",
  BANK_NOT_VERIFIED = "BANK_NOT_VERIFIED",
  ACCOUNT_STATE_REVIEW = "ACCOUNT_STATE_REVIEW",
  INACTIVE_ACCOUNT = "INACTIVE_ACCOUNT",
  ACCOUNT_STATE_TRANSACTION_FAIL = "ACCOUNT_STATE_TRANSACTION_FAIL",
  FOREIGN_MERCHANT_VELOCITY = "FOREIGN_MERCHANT_VELOCITY",
  MERCHANT_ABUSE = "MERCHANT_ABUSE",
}

export interface Transaction {
  accepted: boolean;
  achResult: string;
  amount: number;
  cardID: number;
  cardMeta: Record<string, any>;
  cardState?: string;
  cardStyle?: Record<string, any>;
  cardType: CardType;
  cardUuid: string;
  dateAuthorized: Date;
  dateSettled: Date;
  declineReason?: DeclineReasons;
  descriptor: string;
  hasBounced: boolean;
  mcc: string;
  mccIconUrl?: string;
  merchantCategory?: string;
  originalAuth: Record<string, any>;
  promoCreditAmount?: number;
  refunds?: Record<string, any>[];
  reversed: boolean;
  settledAmount: number;
  settlementStatus: SettlementStates;
  state: string;
  transactionUuid: string;
}
