import Management from "./management";
import Message from "./message";
import Storage from "./storage";
import type { ExtensionContext, ExtensionEvents, Manifest } from "./types";

export default class ExtensionApi {
  private _context?: ExtensionContext;
  storage: Storage;
  message: Message;
  management: Management;

  constructor(namespace: string) {
    // @ts-ignore
    if (typeof browser !== "undefined") {
      // @ts-ignore
      this._context = browser;
    } else if (typeof chrome !== "undefined") {
      this._context = chrome;
    }

    if (this._context) {
      this._context.runtime?.connect();
    }

    this.storage = new Storage(this._context);
    this.message = new Message(namespace, this._context);
    this.management = new Management(this._context);
  }

  get context(): ExtensionContext | undefined {
    return this._context;
  }

  get popup(): boolean {
    if (this.context?.extension?.getViews) {
      return !!this.context?.extension?.getViews({ type: "popup" })?.length;
    }

    return false;
  }

  getManifest(): Manifest {
    if (!this.context) {
      return {} as Manifest;
    }

    return this.context.runtime.getManifest();
  }

  getURL(url: string) {
    if (!this.context) {
      return "";
    }

    return this.context.runtime?.getURL(url);
  }

  async getBrowserInfo() {
    // Firefox only for now, which works well for our purposes
    // @ts-ignore
    if (this.context?.runtime?.getBrowserInfo) {
      // @ts-ignore
      return this.context.runtime.getBrowserInfo();
    }

    return {
      name: "",
      vendor: "",
      version: "",
      buildID: "",
    };
  }

  addListener(event: ExtensionEvents, callback: any) {
    if (!this.context) {
      return;
    }

    this.context.runtime[event].addListener(callback);
  }

  addBrowserActionListener(callback: any) {
    if (!this.context) {
      return;
    }

    this.context.action.onClicked.addListener(callback);
  }

  async getCurrentTab(): Promise<chrome.tabs.Tab | undefined> {
    if (!this.context || !this.context.tabs) {
      return;
    }

    const [tab] = await this.context.tabs.query({
      active: true,
      currentWindow: true,
    });

    return tab;
  }

  addTabListener(listener: any) {
    this.context?.tabs?.onUpdated.addListener(listener);
  }
}
