import { ref, shallowRef, type ShallowRef } from "vue";
import { CardType, type Card, type CardLogo } from "@/types/Card";
import digitalWalletIconFilename from "@/assets/icons/card-digital-wallet-icon.svg?url";
import IconSingleUseCard from "@/components/icons/IconSingleUseCard.vue";
import IconUnlockedCard from "@/components/icons/IconUnlockedCard.vue";
import IconLockedCard from "@/components/icons/IconLockedCard.vue";
import IconPrivacyLogoSquare from "@/components/icons/IconPrivacyLogoSquare.vue";
import IconPadlock from "@/components/icons/IconPadlock2.vue";
import IconFlame from "@/components/icons/IconFlame.vue";
import IconUnlocked from "@/components/icons/IconUnlocked.vue";

const baseImageBucket = "https://s3.amazonaws.com/privacy-web/images/";
const CARD_IMAGES = baseImageBucket + "cards/";
const MCC_ICONS = baseImageBucket + "streamline/";

interface BackgroundColorOptions {
  mcc?: number | string;
  mccIconUrl?: string;
  style?: Card["style"];
  type?: Card["type"];
  merchantCategory?: Card["merchantCategory"];
}

// Digital wallet cards have a predefined logo.
// Otherwise, if the card has a logo file specified, use that
function logoFilename({
  type,
  style,
}: BackgroundColorOptions): string | undefined {
  if (type === CardType.DIGITAL_WALLET) {
    return digitalWalletIconFilename;
  } else {
    return style?.filename;
  }
}

// Use a predefined bg color for digital wallet and single use cards
// Otherwise, use the color specified in the card style
function cardBgColor({
  type,
  style,
  merchantCategory,
}: BackgroundColorOptions): string | undefined | null {
  let color = style?.bgColor;
  const isUnlocked = type === CardType.UNLOCKED && !merchantCategory;

  if (
    [CardType.DIGITAL_WALLET, CardType.SINGLE_USE].includes(type as CardType) ||
    isUnlocked
  ) {
    color = null;
  }

  color &&= "#" + color;

  return color;
}

function getPseudoRandomColor(mccInput: number | string, alpha?: string) {
  const mcc = Number(mccInput);

  let x = Math.sin(mcc) * 10000;
  x -= Math.floor(x);

  const hue = Math.floor(x * 360);
  const saturation = 100;
  const lightness = Math.floor(x * 25) + 45; // 35-70%

  return alpha
    ? `hsla(${hue}, ${saturation}%, ${lightness}%, ${alpha})`
    : `hsl(${hue}, ${saturation}%, ${lightness}%)`;
}

function logoAltText({
  type,
  friendlyNames,
  strippedHostname = "",
}: {
  type: Card["type"] | undefined;
  friendlyNames?: string[];
  strippedHostname?: string;
}): string {
  let alt = "";

  if (type === CardType.DIGITAL_WALLET) {
    alt = "Digital Wallet";
  } else if (type === CardType.SINGLE_USE) {
    alt = "Single-Use";
  } else if (type === CardType.UNLOCKED) {
    alt = "Unlocked";
  }

  alt ||= friendlyNames?.[0] || "";
  alt ||= strippedHostname;

  return alt;
}

export function useBackgroundColor({
  mcc,
  mccIconUrl,
  style,
  type,
  merchantCategory,
}: BackgroundColorOptions) {
  const backgroundColor = ref({});

  let color: string | undefined | null = cardBgColor({
    type,
    style,
    merchantCategory,
  });

  if (mcc && mccIconUrl && type) {
    const hasLogoFile = !!logoFilename({ type, style });

    if (!style?.icon && !hasLogoFile && !color) {
      color = getPseudoRandomColor(mcc!, "10%");
    }
  }

  if (color) {
    backgroundColor.value = {
      backgroundColor: color,
    };
  }

  return backgroundColor.value;
}

const componentLogos = {
  DEFAULT: IconLockedCard,
  [CardType.SINGLE_USE]: IconSingleUseCard,
  [CardType.UNLOCKED]: IconUnlockedCard,
  [CardType.DIGITAL_WALLET]: IconPrivacyLogoSquare,
};

export function useLogo({
  mccIconUrl,
  type,
  merchantCategory,
  style = {},
}: Partial<Card>) {
  const { friendlyNames, icon, lastModified, strippedHostname } = style;
  const filename = logoFilename({ type, style });

  const logo: ShallowRef<Partial<CardLogo>> = shallowRef({});
  const isUnlocked = type === CardType.UNLOCKED && !merchantCategory;

  if (
    type === CardType.SINGLE_USE ||
    isUnlocked ||
    type === CardType.DIGITAL_WALLET
  ) {
    logo.value = {
      type: "component",
      src: componentLogos[type],
      alt: logoAltText({ type, friendlyNames, strippedHostname }),
    };
    return logo.value;
  }

  if (icon) {
    logo.value = {
      type: "icon",
      src: icon,
      alt: logoAltText({ type, friendlyNames, strippedHostname }),
    };
    return logo.value;
  }

  if (filename) {
    const image =
      type === CardType.MERCHANT_LOCKED
        ? `${CARD_IMAGES}${filename}${
            lastModified ? `?lastModified=${lastModified}` : ""
          }`
        : filename;

    logo.value = {
      type: "image",
      src: image,
      alt: logoAltText({ type, friendlyNames, strippedHostname }),
    };
    return logo.value;
  }

  if (mccIconUrl) {
    logo.value = {
      type: "mcc",
      src: `${MCC_ICONS}${mccIconUrl}`,
      alt: logoAltText({ type, friendlyNames, strippedHostname }),
    };
    return logo.value;
  }

  logo.value = {
    type: "component",
    src: componentLogos.DEFAULT,
    alt: "",
  };
  return logo.value;
}

export const getCustomCardColors = (
  bgColor?: string | null
): { bgColor: string; textColor: string; isDarkBg: boolean } => {
  const customBgColor = bgColor ? bgColor : "FFFFFF";

  const r = parseInt(customBgColor.substring(0, 2), 16);
  const g = parseInt(customBgColor.substring(2, 4), 16);
  const b = parseInt(customBgColor.substring(4, 6), 16);

  // Use the formula for luminance to determine the best color for readability
  const isDarkBackground = r * 0.299 + g * 0.587 + b * 0.114 < 186;

  const cardColors = {
    bgColor: `#${customBgColor}`,
    textColor: "#000000",
    isDarkBg: false,
  };

  if (isDarkBackground) {
    cardColors.textColor = "#FFFFFF";
    cardColors.isDarkBg = true;
  }

  return cardColors;
};

export const getLabelIcon = (
  type: Card["type"],
  merchantCategory: Card["merchantCategory"]
) => {
  if (type === CardType.SINGLE_USE) {
    return IconFlame;
  }

  if (type === CardType.UNLOCKED) {
    return merchantCategory ? IconPadlock : IconUnlocked;
  }

  if (type === CardType.DIGITAL_WALLET) {
    return "";
  }

  return IconPadlock;
};

export const getLabelText = (
  type: Card["type"],
  merchantCategory?: Card["merchantCategory"]
): string => {
  if (type === CardType.SINGLE_USE) {
    return "SINGLE USE";
  }

  if (type === CardType.UNLOCKED && !merchantCategory) {
    return "UNLOCKED";
  }

  if (type === CardType.UNLOCKED && merchantCategory) {
    return "CATEGORY LOCKED";
  }

  if (type === CardType.DIGITAL_WALLET) {
    return "DIGITAL WALLET";
  }

  return "MERCHANT LOCKED";
};

export const getCardIconSrc = (style: Card["style"]) => {
  if (!style?.filename) {
    return "";
  }

  let src = `${CARD_IMAGES}${style.filename}`;

  if (style?.lastModified) {
    src += `?lastModified=${style.lastModified}`;
  }

  return src;
};
