<script setup lang="ts">
import { computed, ref } from "vue";
import { useUserStore } from "@/store/user/userStore";
import { useRoute } from "vue-router";
import BaseButton from "@/components/BaseButton/BaseButton.vue";
import BaseInput from "@/components/BaseInput/BaseInput.vue";
import BaseInputGroup from "@/components/BaseInputGroup/BaseInputGroup.vue";
import IconMagnifyingGlass from "@/components/icons/IconMagnifyingGlass.vue";
import TransactionList from "@/components/TransactionList/TransactionList.vue";

const route = useRoute();
const privacyUrl = import.meta.env.EXPOSED_PRIVACY_WEB_BASE_URL;
const userStore = useUserStore();
const userLoaded = computed(() => !!userStore.user._id);
const completedOnboarding = computed(() => userStore.completedOnboarding);
const query = ref<string>(route.query.q as string | "");
</script>
<template>
  <div class="px-4">
    <BaseInputGroup
      v-if="userLoaded && completedOnboarding"
      class="transactions-search mb-3"
    >
      <template #prefix>
        <IconMagnifyingGlass />
      </template>
      <BaseInput
        type="search"
        aria-label="Search transactions"
        placeholder="Search"
        v-model="query"
      />
    </BaseInputGroup>
  </div>
  <div v-if="userLoaded && !completedOnboarding" class="user-prompt">
    <p>Please complete onboarding to view transactions</p>
    <BaseButton :to="privacyUrl" variant="primary">
      Complete Onboarding
    </BaseButton>
  </div>
  <TransactionList v-else-if="completedOnboarding" :query="query" />
</template>

<style scoped lang="scss">
@import "~bootstrap/scss/mixins/color-mode.scss";

.transactions-search {
  color: $color-privacy-gray-3;

  &:focus-within {
    color: $color-privacy-gray-4;
  }
