import { createPinia, type PiniaPluginContext, type Store } from "pinia";

const store = createPinia();
const allStores = new Set<Store>();
store.use(({ store }: PiniaPluginContext) => allStores.add(store));

export const initAllStores = async () => {
  await Promise.all([
    import("./auth/authStore").then((store) => store.useAuthStore().init()),
    import("./user/userStore").then((store) => store.useUserStore().init()),
    import("./metric/metricStore").then((store) =>
      store.useMetricStore().init()
    ),
    // Add other stores to init here
  ]);
};

export default store;
