import { v2Api, type ApiResponse } from "@/lib/request";
import type { FundingSource } from "@/types/Funding";

const resource = "/funding";

export type ListFundingResponse = {
  total: number;
  data: FundingSource[];
};

export function list(): Promise<ApiResponse<ListFundingResponse>> {
  return v2Api.get(resource);
}
