<script setup lang="ts">
import { useRoute, useRouter } from "vue-router";
import DataConsent from "@/components/DataConsent/DataConsent.vue";
import appExtensionApi from "@/lib/extension/appExtensionApi";

const router = useRouter();
const route = useRoute();

async function nextPage() {
  const { redirect, ...otherParams } = route.query || {};

  router.push({
    name: (redirect as string) || "cards",
    query: otherParams,
  });
}

async function uninstall() {
  appExtensionApi.message.send({
    target: "checkout",
    event: "removeInterstitial",
  });
}
</script>

<template>
  <div class="d-flex flex-column h-100">
    <DataConsent
      class="consent-wrapper h-100"
      @uninstall="uninstall"
      @consent="nextPage"
    />
  </div>
</template>

