<script setup lang="ts">
import { CardLogoSize } from "@/types/Card";
import { DisputeStates, type Transaction } from "@/types/Transaction";
import { utcDate } from "@/lib/dates";
import { computed } from "vue";
import { formatCurrency } from "@/lib/numbers";
import { parseDeclineReason } from "@/lib/transactions";
import IconReturn from "@/components/icons/IconReturn.vue";
import PrivacyCardLogo from "@/components/PrivacyCardLogo/PrivacyCardLogo.vue";
import BaseTooltip from "@/components/BaseTooltip/BaseTooltip.vue";

const props = withDefaults(
  defineProps<{
    transaction?: Transaction;
    loading?: boolean;
    showCardLink?: boolean;
  }>(),
  {
    loading: false,
    showCardLink: true,
  }
);

const transaction = computed(() => props.transaction || ({} as Transaction));

const txnDateFormatted = computed(() => {
  const date =
    transaction.value.dateAuthorized || transaction.value.dateSettled || "";
  return date ? utcDate(date).local().format("MMM D, h:mma") : null;
});

const hasRefunds = computed(() => {
  const { amount, refunds = [] } = transaction.value;
  return amount >= 0 && refunds.length > 0;
});

const amountFormatted = computed(() => {
  const amount = transaction.value.amount || 0;
  const promoCredit = transaction.value.promoCreditAmount || 0;
  const originalAuthAmount = transaction.value.originalAuth?.amount;

  if (hasRefunds.value && originalAuthAmount) {
    return originalAuthAmount;
  }

  return formatCurrency((amount - promoCredit) / 100);
});

const isRefunded = computed(() => {
  return (
    transaction.value.state === DisputeStates.DISPUTE_CLOSED ||
    transaction.value.amount < 0
  );
});

const isPending = computed(() => {
  return (
    transaction.value.state === DisputeStates.ACTIVE &&
    !transaction.value.dateSettled
  );
});

const isDecline = computed(() => {
  return !!transaction.value.declineReason;
});

const transactionClasses = computed(() => {
  const { accepted, achResult, dateSettled, hasBounced, state } =
    transaction.value;

  return {
    blocked: !accepted,
    bounced: hasBounced,
    offset: achResult === "-2",
    refunded: isRefunded.value,
    unconfirmed: state === DisputeStates.ACTIVE && !dateSettled,
  };
});

const tooltipText = computed(() => {
  if (isDecline.value) {
    return parseDeclineReason(transaction.value.declineReason);
  } else if (isRefunded.value) {
    return "Refunded";
  } else if (isPending.value) {
    return "Pending";
  }

  return "Settled";
});
</script>

<template>
  <div class="transaction" v-if="loading" data-test="txn-list-item-loading">
    <div class="row gx-0 align-items-center placeholder-glow">
      <div class="placeholder placeholder-logo me-2" />
      <div class="col-6">
        <div class="rounded-1 placeholder col-12 mb-2" />
        <div class="rounded-1 placeholder col-6" />
      </div>
      <div class="col-2 ms-4">
        <div class="rounded-1 placeholder col-12" />
      </div>
    </div>
  </div>
  <div
    v-else
    class="transaction d-flex align-items-center"
    :class="transactionClasses"
  >
    <RouterLink
      v-if="showCardLink"
      :to="{ name: 'card-details', params: { uuid: transaction.cardUuid } }"
      class="me-3"
    >
      <PrivacyCardLogo
        :mcc="transaction.mcc"
        :mccIconUrl="transaction.mccIconUrl"
        :merchantCategory="transaction.merchantCategory"
        :size="CardLogoSize.MEDIUM"
        :style="transaction.cardStyle"
        :type="transaction.cardType"
        data-test="txn-list-item-card-logo"
      />
    </RouterLink>
    <span v-else class="me-3">
      <PrivacyCardLogo
        :mcc="transaction.mcc"
        :mccIconUrl="transaction.mccIconUrl"
        :merchantCategory="transaction.merchantCategory"
        :size="CardLogoSize.MEDIUM"
        :style="transaction.cardStyle"
        :type="transaction.cardType"
        data-test="txn-list-item-card-logo"
      />
    </span>
    <div class="description min-w-0 w-100">
      <div
        class="descriptor text-truncate mb-1"
        data-test="txn-list-item-descriptor"
      >
        {{ transaction.descriptor }}
      </div>
      <div class="date" data-test="txn-list-item-date">
        {{ txnDateFormatted }}
      </div>
    </div>
    <BaseTooltip
      :variant="isDecline ? 'danger' : 'info'"
      :tooltip="tooltipText"
      class="amount ms-3"
      data-test="txn-list-item-amount"
    >
      <IconReturn v-if="isRefunded" data-test="txn-list-item-refunded" />
      {{ amountFormatted }}
    </BaseTooltip>
  </div>
</template>

<style scoped lang="scss">
.transaction {
  cursor: default;
}

.placeholder-logo {
  height: 40px;
  width: 40px;
  border-radius: 14px;
}

.date {
  color: $color-privacy-gray-3;
}

.descriptor,
:deep(.amount) {
  color: var(--color-body-text);
  font-weight: $font-weight-medium;
}

.descriptor {
  .unconfirmed & {
    color: $color-privacy-gray-3;
  }
}

:deep(.amount) {
  white-space: nowrap;
  .unconfirmed &,
  .offset & {
    color: $color-privacy-gray-3;
  }

  .bounced & {
    color: $red;
  }

  .blocked &,
  .offset & {
    text-decoration: line-through;
  }

  .refunded & {
    color: $green;
  }
}
</style>
