import { ref, computed } from "vue";
import { defineStore } from "pinia";
import { user as userApi } from "@/apis";
import { AccountPurposes, type User } from "@/types/User";
import extensionApi from "@/lib/extension/appExtensionApi";

export const useUserStore = defineStore("user", () => {
  // STATES
  const userState = ref<User>({});

  const user = computed(() => userState.value);

  const completedOnboarding = computed(() => {
    const {
      _id,
      accountPurpose,
      chargeTermsAcceptTime,
      commercialChargeTermsAcceptTime,
    } = userState.value;

    return !!(
      _id &&
      ((accountPurpose !== AccountPurposes.BUSINESS && chargeTermsAcceptTime) ||
        (accountPurpose === AccountPurposes.BUSINESS &&
          commercialChargeTermsAcceptTime))
    );
  });

  async function init() {
    const { token } = await extensionApi.storage.get("token");

    if (token) {
      void getUser();
    }
  }

  // ACTIONS
  async function getUser(): Promise<User> {
    const { data } = await userApi.me();

    userState.value = data;

    return data;
  }

  function $reset() {
    userState.value = {};
  }

  return {
    // actions
    init,
    getUser,
    $reset,

    // getters
    user,
    completedOnboarding,

    // state
    userState,
  };
});
