import { createMemoryHistory, createRouter } from "vue-router";
import CardsView from "../views/Cards/CardsView.vue";
import TransactionsView from "../views/Transactions/TransactionsView.vue";
import AccountView from "../views/Account/AccountView.vue";
import LoginView from "@/views/Login/LoginView.vue";
import TFAView from "@/views/TFA/TFAView.vue";
import TFARecoveryConfirm from "@/views/TFARecoveryConfirm/TFARecoveryConfirm.vue";
import { useAuthStore } from "@/store/auth/authStore";
import PasswordReset from "@/views/PasswordReset/PasswordReset.vue";
import CardDetails from "@/views/CardDetails/CardDetails.vue";
import NewCard from "@/views/NewCard/NewCard.vue";
import appExtensionApi from "@/lib/extension/appExtensionApi";
import CardCollapsedView from "@/views/CardCollapsed/CardCollapsedView.vue";
import DataConsent from "@/views/DataConsent/DataConsent.vue";
import SelectMerchantCategory from "@/views/SelectMerchantCategory/SelectMerchantCategory.vue";
import { hasDataConsent, isActivityConsentSet } from "@/lib/updates";
import { isFirefox, isIPhone } from "@/lib/utils/utils";
import ExtensionSettings from "@/views/ExtensionSettings/ExtensionSettingsView.vue";

const defaultHeight = 500;

export const routes = [
  {
    path: "/index.html",
    name: "index",
    redirect: () => ({ name: "cards" }),
  },
  {
    path: "/interstitial.html",
    name: "interstitial",
    redirect: () => ({ name: "cards" }),
  },
  {
    path: "/login",
    name: "login",
    component: LoginView,
  },
  {
    path: "/cards",
    alias: "/",
    meta: {
      requiresAuth: true,
    },
    children: [
      {
        path: "",
        name: "cards",
        component: CardsView,
      },
      {
        path: "select-merchant-category",
        name: "select-merchant-category",
        component: SelectMerchantCategory,
        meta: {
          layout: "Details",
          title: "New Card",
          grayBackground: true,
          height: 600,
        },
      },
      {
        path: "new/:type/:merchantCategory?",
        name: "new-card",
        component: NewCard,
        meta: {
          layout: "Details",
          title: "New Card",
          height: 600,
        },
      },
      {
        path: "suggestions",
        name: "suggestions",
        component: CardsView,
        meta: {
          layout: "Details",
          title: "Choose Card",
        },
      },
      {
        path: ":uuid",
        name: "card-details",
        component: CardDetails,
        meta: {
          layout: "Details",
          title: "Card Details",
          height: 600,
        },
      },
      {
        path: "collapsed",
        name: "card-collapsed",
        component: CardCollapsedView,
        meta: {
          layout: "Headerless",
          height: isIPhone() ? 72 : 120,
        },
      },
    ],
  },
  {
    path: "/transactions",
    name: "transactions",
    component: TransactionsView,
    meta: {
      requiresAuth: true,
    },
  },
  {
    path: "/tfa",
    name: "tfa",
    component: TFAView,
  },
  {
    path: "/tfa-recovery-confirm",
    name: "tfa-recovery-confirm",
    component: TFARecoveryConfirm,
  },
  {
    path: "/account",
    name: "account",
    component: AccountView,
    meta: {
      requiresAuth: true,
    },
  },
  {
    path: "/password-reset",
    name: "password-reset",
    component: PasswordReset,
  },
  {
    path: "/data-consent",
    name: "data-consent",
    component: DataConsent,
    meta: {
      layout: "Default",
    },
  },
  {
    path: "/extension-settings",
    name: "extension-settings",
    component: ExtensionSettings,
    meta: {
      layout: "Details",
      title: "Extension Settings",
      requiresAuth: true,
    },
  },
];

const router = createRouter({
  history: createMemoryHistory(import.meta.env.BASE_URL),
  routes,
});

router.beforeEach(async (to, from) => {
  // Show data consent screen if user has not agreed to it
  // Note: only a Firefox requirement
  const agreed = isFirefox()
    ? (await hasDataConsent()) && (await isActivityConsentSet())
    : true;

  if (!agreed && to.name !== "data-consent") {
    return {
      name: "data-consent",
      query: { redirect: to.name as string, ...to.query },
    };
  }

  // Declare inside beforeEach to avoid calling Pinia before it's ready
  const authStore = useAuthStore();

  to.meta.fromRoute = from?.name;

  // Prevent "back" from bringing the user to the card suggestions page -
  // since it will redirect them to New Card
  if (from?.name === "suggestions" && from.query?.q) {
    to.meta.fromRoute = undefined;
  }

  // Force login if user is not authenticated and route requires it
  if (!authStore.token && to.meta.requiresAuth && to.name !== "login") {
    return {
      name: "login",
      query: { redirect: to.name as string, ...to.query },
    };
  }

  /**
   * The start page is set when loading the TFA or TFA recovery pages.
   * When opening/closing the popup you can get stuck in a loop where you
   * can't validate the one-time code - this solves for that
   */
  if (authStore.userToken) {
    const { startPage } = await appExtensionApi.storage.get("startPage");

    if (startPage && to.name !== startPage) {
      return { name: startPage };
    } else if (!startPage) {
      await authStore.clearTwoFactorAuth();

      return { name: "login", query: { redirect: to.name as string } };
    }
  }

  const interstitialHeight = to.meta.height || defaultHeight;

  if (!appExtensionApi.popup || to.name === "card-collapsed") {
    appExtensionApi.message.send({
      event: "updateInterstitialHeight",
      target: "checkout",
      payload: {
        height: interstitialHeight,
      },
    });
  }
});

export default router;
