<script setup lang="ts">
import { computed, inject, onMounted, ref } from "vue";
import { useRoute } from "vue-router";
import { CardState, CardType } from "@/types/Card";
import type { MerchantCategory } from "@/types/MerchantCategory";
import BaseButton from "@/components/BaseButton/BaseButton.vue";
import PrivacyCard from "@/components/PrivacyCard/PrivacyCard.vue";
import BaseAlert from "@/components/BaseAlert/BaseAlert.vue";
import BaseBanner from "@/components/BaseBanner/BaseBanner.vue";
import BaseLabel from "@/components/BaseLabel/BaseLabel.vue";
import IconPaidFeature from "@/components/icons/IconPaidFeature.vue";
import SpendLimitButton from "@/components/SpendLimit/SpendLimitButton/SpendLimitButton.vue";
import type { SubmitEvent } from "@/components/SpendLimit/SpendLimitModal/SpendLimitModal.vue";
import useCardsStore from "@/store/cards";
import useFundingStore from "@/store/funding";
import { useUserStore } from "@/store/user/userStore";
import { useEventStore } from "@/store/event/eventStore";
import popupAlert from "@/lib/popupAlert";
import { CARD_EVENTS } from "@/types/Event";
import IconPlay from "@/components/icons/IconPlay.vue";
import IconPause from "@/components/icons/IconPause.vue";
import { BaseDropdownItem, BaseDropdown } from "@/components/BaseDropdown";
import IconEllipses from "@/components/icons/IconEllipses.vue";
import useCardNickname from "@/lib/cards/nickname";
import {
  useUpdateSpendLimit,
  type SpendLimitUpdates,
} from "@/lib/cards/spendLimit";
import CardNickname from "@/components/CardNickname/CardNickname.vue";
import CloseCardModal from "@/components/ModalCloseCard/ModalCloseCard.vue";
import FundingButton from "@/components/Funding/FundingButton/FundingButton.vue";
import FundingModal from "@/components/Funding/FundingModal/FundingModal.vue";
import TransactionList from "@/components/TransactionList/TransactionList.vue";
import BasePill from "@/components/BasePill/BasePill.vue";
import IconPadlock from "@/components/icons/IconPadlock.vue";
import {
  useFoundPaymentInputs,
  useFillCheckout,
  useShowCollapsedCard,
} from "@/lib/cards/useCheckout";
import { CONTEXT } from "@/types/App";
import CardNote from "@/components/CardNote/CardNote.vue";
import { useSubscriptionsStore } from "@/store/subscriptions/subscriptionsStore";
import { SubscriptionFeaturesEnum } from "@/types/Subscription";

const privacyUrl = import.meta.env.EXPOSED_PRIVACY_WEB_BASE_URL;

const route = useRoute();
const cardsStore = useCardsStore();
const userStore = useUserStore();
const eventStore = useEventStore();
const fundingStore = useFundingStore();
const subscriptionsStore = useSubscriptionsStore();

const cardUuid = route.params.uuid as string;
const loadingCard = ref(true);
const loadingFunding = ref(true);
const loadingSubscription = ref(true);
const loadingError = ref("");
const fundingError = ref("");
const updating = ref(false);
const showCloseCardModal = ref(false);
const showFundingModal = ref(false);
const paymentInputsFound = useFoundPaymentInputs();
// If we're in the interstitial hide the search bar
// and show the "use" buttons
const interstitial = inject("context") === CONTEXT.INTERSTITIAL;

onMounted(async () => {
  loadCard();
  loadFunding();
  loadSubscription();
});

const card = computed(() => cardsStore.card(cardUuid) || {});
const cardUser = computed(() => ({
  name: userStore.user.firstName + " " + userStore.user.lastName,
  firstName: userStore.user.firstName,
  lastName: userStore.user.lastName,
  streetAddress: userStore.user.address1 + " " + userStore.user.address2,
  addressLine1: userStore.user.address1,
  addressLine2: userStore.user.address2,
  addressLevel2: userStore.user.city,
  addressLevel1: userStore.user.state,
  postalCode: userStore.user.zipcode,
}));
const funding = computed(() =>
  fundingStore.source(card.value.bankAccountUuid || card.value.fundingAccount)
);
const fundingSources = computed(() => fundingStore.all);
const nickname = useCardNickname(card);
const note = ref(card.value.note || "");
const updateSpendLimit = useUpdateSpendLimit(card);

const hasCardNoteFeature = computed(() =>
  subscriptionsStore.hasFeature(SubscriptionFeaturesEnum.CARD_NOTES)
);

const hasCategoryCardsFeature = computed(() =>
  subscriptionsStore.hasFeature(SubscriptionFeaturesEnum.CATEGORY_CARDS)
);

const hasDigitalWalletCardsFeature = computed(() =>
  subscriptionsStore.hasFeature(SubscriptionFeaturesEnum.DIGITAL_WALLET_CARDS)
);

const cardIsShared = computed(() => {
  return !!card.value?.sharedWithRecipients?.length;
});

const cardSharingSpendLimit = computed(() => {
  return subscriptionsStore.featureValue(
    SubscriptionFeaturesEnum.CARD_SHARING_SPEND_LIMIT
  ) as number | undefined;
});

const isCategoryCard = computed(
  () =>
    card.value.type === CardType.UNLOCKED &&
    !!card.value.merchantCategory &&
    (card.value.merchantCategory as MerchantCategory).name
);

const categoryName = computed(() => {
  return (card.value.merchantCategory as MerchantCategory).name.toUpperCase();
});

const paused = computed(() => card.value.state === CardState.PAUSED);

const active = computed(() => {
  const { state } = card.value;

  return state === CardState.OPEN || paused.value;
});

const showUseCard = computed(() => {
  return paymentInputsFound.value && active.value;
});

const upgradeRequired = computed(() => {
  return (
    (!hasCategoryCardsFeature.value && !!isCategoryCard.value) ||
    (!hasDigitalWalletCardsFeature.value &&
      card.value.type === CardType.DIGITAL_WALLET)
  );
});

async function loadCard() {
  loadingError.value = "";
  loadingCard.value = true;

  try {
    await cardsStore.get(cardUuid);
    eventStore.track({
      name: CARD_EVENTS.VIEWED,
      data: { cardUuid },
    });
  } catch (err) {
    loadingError.value = "Unable to load card details";

    eventStore.error([
      {
        name: CARD_EVENTS.VIEWED,
        data: {
          cardUuid,
          error: err,
        },
      },
    ]);
  } finally {
    loadingCard.value = false;
  }
}

async function loadFunding() {
  loadingFunding.value = true;
  fundingError.value = "";

  try {
    await fundingStore.list();
  } catch (err) {
    fundingError.value = "Unable to load funding source";
  } finally {
    loadingFunding.value = false;
  }
}

async function loadSubscription() {
  loadingSubscription.value = true;

  try {
    await subscriptionsStore.get();
  } catch (err) {
    /* fail silently */
  } finally {
    loadingSubscription.value = false;
  }
}

async function togglePause() {
  const state =
    card.value.state === CardState.OPEN ? CardState.PAUSED : CardState.OPEN;
  const event =
    state === CardState.OPEN ? CARD_EVENTS.PAUSED : CARD_EVENTS.RESUMED;

  try {
    updating.value = true;
    await cardsStore.setState(cardUuid, { state });
    eventStore.track({
      name: event,
      data: { cardUuid },
    });

    const status = state === CardState.OPEN ? "resumed" : state.toLowerCase();
    popupAlert("Card " + status, "success");
  } catch (err) {
    eventStore.error([
      {
        name: event,
        data: {
          cardUuid,
          state,
          error: err,
        },
      },
    ]);

    popupAlert("Unable to update card");
  } finally {
    updating.value = false;
  }
}

async function closeCard() {
  try {
    updating.value = true;

    await cardsStore.setState(cardUuid, { state: "CLOSED" });

    showCloseCardModal.value = false;
    popupAlert("Card closed", "success");

    eventStore.track({
      name: CARD_EVENTS.CLOSED,
      data: { cardUuid },
    });
  } catch (err) {
    eventStore.error([
      {
        name: CARD_EVENTS.CLOSED,
        data: {
          cardUuid,
          error: err,
        },
      },
    ]);

    popupAlert("Unable to close card");
  } finally {
    updating.value = false;
  }
}

async function handleSpendLimitUpdate(event: SubmitEvent) {
  let updateData: SpendLimitUpdates;

  try {
    updateData = updateSpendLimit(event);
  } catch (err: any) {
    popupAlert(err.message);
    return;
  }

  try {
    updating.value = true;
    await cardsStore.update(cardUuid, updateData);
    popupAlert("Spend limit updated", "success");
    eventStore.track({
      name: CARD_EVENTS.SET_SPEND_LIMIT,
      data: { cardUuid, ...updateData },
    });
  } catch (err) {
    eventStore.error([
      {
        name: CARD_EVENTS.SET_SPEND_LIMIT,
        data: {
          cardUuid,
          ...updateData,
          error: err,
        },
      },
    ]);

    const error = err as Error;
    popupAlert(error.message || "Unable to update spend limits");
  } finally {
    updating.value = false;
  }
}

async function handleFundingSourceUpdate(bankAccountID: number | undefined) {
  if (!bankAccountID || bankAccountID === funding.value?.bankAccountID) {
    return;
  }

  try {
    updating.value = true;
    await cardsStore.update(cardUuid, { bankAccountID });
    eventStore.track({
      name: CARD_EVENTS.SET_FUNDING_SOURCE,
      data: { cardUuid, bankAccountID },
    });

    showFundingModal.value = false;
  } catch (err) {
    eventStore.error([
      {
        name: CARD_EVENTS.SET_FUNDING_SOURCE,
        data: {
          cardUuid,
          bankAccountID,
          error: err,
        },
      },
    ]);

    popupAlert("Unable to update funding source");
  } finally {
    updating.value = false;
  }
}

async function handleNicknameUpdate(value: string) {
  if (value === nickname.value) {
    return;
  }

  try {
    updating.value = true;
    await cardsStore.update(cardUuid, { memo: value });
    eventStore.track({
      name: CARD_EVENTS.SET_NICKNAME,
      data: { cardUuid, nickname: value },
    });
  } catch (err) {
    eventStore.error([
      {
        name: CARD_EVENTS.SET_NICKNAME,
        data: {
          cardUuid,
          nickname: value,
          error: err,
        },
      },
    ]);

    popupAlert("Unable to update nickname");
  } finally {
    updating.value = false;
  }
}

async function handleNoteUpdate(value: string) {
  if (value === note.value) {
    return;
  }

  try {
    updating.value = true;
    await cardsStore.setNote(cardUuid, { note: value });
    eventStore.track({
      name: CARD_EVENTS.SET_NOTE,
      data: { cardUuid },
    });
  } catch (err) {
    eventStore.error([
      {
        name: CARD_EVENTS.SET_NOTE,
        data: {
          cardUuid,
          error: err,
        },
      },
    ]);

    popupAlert("Unable to set note");
  } finally {
    updating.value = false;
  }
}

async function fillAndRedirect() {
  await useFillCheckout(card.value, cardUser.value, paymentInputsFound.value);
  useShowCollapsedCard(card.value.cardUuid, interstitial);
}
</script>

<template>
  <div>
    <div v-if="loadingError" class="text-center px-4">
      <BaseAlert variant="warning">
        {{ loadingError }}
      </BaseAlert>
      <BaseButton
        variant="primary"
        class="mt-3"
        @click="loadCard"
        :disabled="loadingCard"
      >
        Try again
      </BaseButton>
    </div>
    <template v-else>
      <BaseBanner v-if="upgradeRequired" class="mb-3 paid-banner">
        <IconPaidFeature />
        Paid Plan Required
      </BaseBanner>
      <div class="px-4">
        <header class="mb-3">
          <div
            v-if="loadingCard"
            aria-hidden="true"
            class="placeholder-glow"
            data-test="details-placeholder"
          >
            <span class="placeholder col-7" />
            <span class="placeholder col-4 offset-1" />
          </div>
          <div v-else class="d-flex">
            <div class="min-w-0 w-100">
              <div v-if="!active" class="h1 text-truncate">
                {{ nickname }}
              </div>
              <CardNickname
                v-else
                class="nickname"
                :value="nickname"
                @update="handleNicknameUpdate"
              />
            </div>
            <div class="d-flex flex-shrink-1 text-end" v-if="active">
              <BaseButton
                class="card-state"
                variant="icon"
                @click="togglePause"
                :disabled="updating || (paused && upgradeRequired)"
                data-test="resume-pause"
              >
                <IconPause v-if="!paused" aria-label="Pause card" />
                <IconPlay v-else aria-label="Resume card" />
              </BaseButton>
              <BaseDropdown
                class="card-menu"
                toggle-variant="icon"
                :disabled="updating"
              >
                <template #button>
                  <IconEllipses aria-label="Card menu" />
                </template>
                <BaseDropdownItem
                  @click="showCloseCardModal = true"
                  data-test="menu-close-card"
                >
                  Close Card
                </BaseDropdownItem>
                <BaseDropdownItem
                  v-if="interstitial"
                  @click="useShowCollapsedCard(card.cardUuid, interstitial)"
                  data-test="menu-show-collapsed-card"
                >
                  Collapse Card
                </BaseDropdownItem>
              </BaseDropdown>
            </div>
            <BaseLabel v-else data-test="card-closed">Closed</BaseLabel>
          </div>
        </header>
        <section class="mb-3">
          <PrivacyCard
            class="privacy-card"
            :copy-info="active"
            :loading="loadingCard"
            :cvv="card.CVV"
            :exp-month="card.expMonth"
            :exp-year="card.expYear"
            :mcc="card.mcc"
            :mcc-icon-url="card.mccIconUrl"
            :network="card.network"
            :pan="card.PAN"
            :type="card.type"
            :style="card.style"
            :state="card.state"
            :merchant-category="card.merchantCategory"
            :subscription-features="subscriptionsStore.features"
          />
        </section>
        <BaseLabel v-if="cardIsShared" class="mb-2 shared-label">
          Shared Card
        </BaseLabel>
        <div
          v-if="upgradeRequired"
          class="text-center mb-4"
          data-test="upgrade-card"
        >
          <p>
            Your paid plan has expired and all
            {{ isCategoryCard ? "Category-Locked" : "Digital Wallet" }}
            Cards have now been disabled.
          </p>
          <BaseButton :to="`${privacyUrl}/select-plan`" variant="secondary">
            <IconPadlock />
            Upgrade to Resume Card
          </BaseButton>
        </div>
        <section>
          <CardNote
            v-if="hasCardNoteFeature || !!card.note || loadingSubscription"
            class="mb-2"
            :value="note"
            :loading="loadingSubscription"
            :hasCardNoteFeature="hasCardNoteFeature"
            @update="handleNoteUpdate"
          />
        </section>
        <SpendLimitButton
          class="mb-2"
          :is-single-use="card.type === CardType.SINGLE_USE"
          :card-is-shared="cardIsShared"
          :card-sharing-spend-limit="cardSharingSpendLimit"
          :disabled="updating || !active"
          :loading="loadingCard"
          :spend-limit="card.spendLimit"
          :spend-limit-duration="card.spendLimitDuration"
          :spent-this-month-rolling="card.spentThisMonthRolling"
          :spent-this-week="card.spentThisWeek"
          :spent-this-year-rolling="card.spentThisYearRolling"
          :spent-total="card.spentTotal"
          @update="handleSpendLimitUpdate"
        />
        <div v-if="fundingError">
          <p>{{ fundingError }}</p>
        </div>
        <div v-else>
          <FundingButton
            :loading="loadingFunding"
            :source="funding"
            :disabled="fundingSources.length <= 1"
            @click="showFundingModal = true"
          />
          <FundingModal
            :loading="loadingFunding"
            :fundingSources="fundingSources"
            :bankAccountID="funding?.bankAccountID"
            :open="showFundingModal"
            @update="handleFundingSourceUpdate"
            @close="showFundingModal = false"
          />
        </div>
        <section v-if="isCategoryCard" class="category p-3 rounded-3 mt-2">
          <IconPadlock />
          <BasePill class="category-pill">
            <span
              class="category-text"
              data-test="category-badge"
              :title="categoryName"
            >
              {{ categoryName }}
            </span>
          </BasePill>
        </section>
        <BaseButton
          aria-label="Fill payment form with this card"
          v-if="showUseCard && !paused"
          class="w-100 mt-3"
          @click="fillAndRedirect"
          data-test="use-card"
        >
          Autofill Card
        </BaseButton>
        <CloseCardModal
          :open="showCloseCardModal"
          @confirm="closeCard"
          @close="showCloseCardModal = false"
        />
        <TransactionList
          v-if="card.cardID"
          :card-id="card.cardID"
          :is-collapsed="true"
          :show-card-link="false"
        />
      </div>
    </template>
  </div>
</template>

<style lang="scss" scoped>
.paid-banner {
  margin-top: $spacer * -1;
}

.privacy-card {
  margin: 0 auto;
}

.card-menu :deep(.dropdown-toggle) {
  &::after {
    display: none;
  }
}

.shared-label {
  font-size: 11px;
  letter-spacing: 1.1px;
  text-transform: uppercase;
  color: $accent-default;
  background-color: $accent-10;
}

.category {
  border: 1px solid rgba($color-privacy-black, 0.08);
  color: $color-privacy-gray-2;
  display: flex;
  align-items: center;
  gap: 16px;
  .category-pill {
    border-radius: 4px;
    background-color: $secondary;
    color: $color-privacy-gray-4;
    font-size: 11px;
    max-width: calc(100% - 32px);
  }
  .category-text {
    letter-spacing: 1.1px;
    overflow: hidden;
    display: block;
    text-overflow: ellipsis;
  }
}

:deep(.transaction-list) {
  margin-top: $spacer * 1.5;
}
@include color-mode(dark) {
  .category {
    border-color: $color-privacy-gray-4;
    .category-pill {
      background-color: $color-privacy-gray-4;
      color: $color-privacy-gray-1;
    }
  }
}
</style>
