<script setup lang="ts">
import { debounce } from "@/lib/utils/utils";
import { ref, nextTick, watch, computed, watchEffect } from "vue";
import useCardsStore from "@/store/cards";
import { useUserStore } from "@/store/user/userStore";
import { useSubscriptionsStore } from "@/store/subscriptions/subscriptionsStore";
import { RouterLink, useRouter, useRoute } from "vue-router";
import { CardState, CardType, type Card } from "@/types/Card";
import CardListItem from "@/components/CardListItem/CardListItem.vue";
import BaseButton from "@/components/BaseButton/BaseButton.vue";
import BaseAlert from "@/components/BaseAlert/BaseAlert.vue";
import BaseInput from "@/components/BaseInput/BaseInput.vue";
import IconMagnifyingGlass from "@/components/icons/IconMagnifyingGlass.vue";
import BaseInputGroup from "@/components/BaseInputGroup/BaseInputGroup.vue";
import { useFoundPaymentInputs } from "@/lib/cards/useCheckout";
import extensionApi, {
  type Types as ExtensionTypes,
} from "@/lib/extension/appExtensionApi";
import { hasActivityConsent } from "@/lib/updates";
const privacyUrl = import.meta.env.EXPOSED_PRIVACY_WEB_BASE_URL;

const router = useRouter();
const route = useRoute();
const cardsStore = useCardsStore();
const userStore = useUserStore();
const subscriptionsStore = useSubscriptionsStore();
const loadingError = ref("");
const loader = ref<HTMLLIElement | null>(null);
const loading = ref(true);
const allDataLoaded = ref(false);
const query = ref<string>(route.query.q as string | "");
const searching = ref(false);
const initialLoad = ref(true);
const paymentInputsFound = useFoundPaymentInputs();
const suggestionsRoute = computed(() => route.name === "suggestions");

const activeCardStates = [CardState.OPEN, CardState.PAUSED];
const limit = 20;
let offset = 0;
let total = 0;

const cards = ref<Card[]>(new Array(3).fill({} as Card));
const suggestedCards = ref<Card[]>([]);

const showNoCards = computed(() => {
  return (
    !cards.value.length &&
    !loading.value &&
    !loadingError.value &&
    !query.value &&
    !searching.value
  );
});

const userLoaded = computed(() => !!userStore.user._id);
const completedOnboarding = computed(() => userStore.completedOnboarding);
const user = computed(() => userStore.user);

const unwatch = watchEffect(() => {
  if (userLoaded.value && loader.value) {
    observeLoader();
    unwatch();
  }
});

const unwatchRoute = watch(
  () => route.query,
  async () => {
    query.value = route.query.q as string | "";
    reset();
    await loadCards();
    observeLoader(true);
  }
);

function search() {
  // Using `searching` to prevent "showNoCards" from being
  // true if the user starts with a search value that comes
  // back with no results
  searching.value = true;
  debouncedUpdateQuery();
}

const debouncedUpdateQuery = debounce(updateQuery, 350);

function updateQuery() {
  if (!query.value) {
    router.push({ query: {} });
    return;
  }

  router.push({ query: { q: query.value } });
  searching.value = false;
}

function reset() {
  initialLoad.value = true;
  allDataLoaded.value = false;
  offset = 0;
  total = 0;
}

async function loadSuggestedCards() {
  // Dont load suggestions if the user has not consented to activity tracking
  if (!(await hasActivityConsent())) {
    return;
  }

  // We don't want to load suggestions on the suggestions page
  if (suggestionsRoute.value) {
    return;
  }

  try {
    const tab: ExtensionTypes.Tab | undefined = await extensionApi.message.send(
      {
        event: "getCurrentTab",
      }
    );

    if (!tab?.url?.startsWith("http")) {
      return;
    }

    const url = new URL(tab.url);
    const host = url.hostname.replace("www.", "");

    const results = await cardsStore.list({
      limit,
      offset,
      query: host,
    });

    suggestedCards.value = results.data.filter((card) =>
      activeCardStates.includes(card.state)
    );
  } catch (err) {
    // noop
  }
}

async function loadCards() {
  loadingError.value = "";
  loading.value = true;

  try {
    const [results] = await Promise.all([
      cardsStore.list({
        limit,
        offset,
        query: query.value || undefined,
      }),
      // If on the suggestions route this will exit early
      loadSuggestedCards(),
    ]);

    const activeCards = results.data.filter(
      (card) =>
        activeCardStates.includes(card.state) &&
        !suggestedCards.value.some(
          (sgCard) => sgCard.cardUuid === card.cardUuid
        )
    );

    total = results.total;
    cards.value = initialLoad.value
      ? activeCards
      : [...cards.value, ...activeCards];

    offset += limit;

    // If we've reach the limit of all cards or we've loaded all active cards
    if (offset >= total || activeCards.length < results.data.length) {
      allDataLoaded.value = true;
    }
  } catch (err) {
    if (initialLoad.value) {
      cards.value = [];
    }

    loadingError.value = "Unable to load cards";
  } finally {
    loading.value = false;
    initialLoad.value = false;
  }
}

async function observeLoader(observeOnly = false) {
  const observer = new IntersectionObserver((entries) => {
    entries.forEach(async (entry) => {
      if (entry.isIntersecting || initialLoad.value) {
        // This is to make sure `loadCards` is only called once
        // when the loader is in view
        observer.unobserve(loader.value!);

        if (!observeOnly) {
          await loadCards();

          if (shouldRedirectToNewCard.value) {
            unwatchRoute();
            router.replace({
              name: "new-card",
              params: { type: CardType.MERCHANT_LOCKED },
            });
            return;
          }
        }

        if (!allDataLoaded.value && !loadingError.value) {
          observeLoader();
        }
      }
    });
  });

  // Make sure the loader exists before observing it
  await nextTick();
  observer.observe(loader.value!);
}

const shouldRedirectToNewCard = computed(() => {
  return suggestionsRoute.value && cards.value.length === 0;
});
</script>

<template>
  <div v-if="userLoaded && !completedOnboarding" class="user-prompt px-4">
    <p>Please complete onboarding to view and create cards.</p>
    <BaseButton :to="privacyUrl" variant="primary">
      Complete Onboarding
    </BaseButton>
  </div>
  <div v-else-if="showNoCards" class="user-prompt px-4">
    <p>You don't have any cards</p>
  </div>
  <template v-else-if="completedOnboarding">
    <div class="px-4">
      <BaseInputGroup v-if="!suggestionsRoute" class="mb-3 cards-search">
        <template #prefix>
          <IconMagnifyingGlass />
        </template>
        <BaseInput
          type="search"
          aria-label="Search cards"
          placeholder="Search"
          v-model="query"
          @input="search"
        />
      </BaseInputGroup>
    </div>
    <div class="px-4 mb-3" v-if="suggestionsRoute || suggestedCards.length">
      <h2>Suggestions</h2>
    </div>
    <ul
      class="card-list mb-3"
      v-if="suggestedCards.length"
      data-test="suggested-card-list"
    >
      <li v-for="(card, index) in suggestedCards" :key="card.cardUuid || index">
        <router-link
          v-if="card.cardUuid"
          class="card-item-link"
          :to="{ name: 'card-details', params: { uuid: card.cardUuid } }"
        >
          <CardListItem
            :cardUuid="card.cardUuid"
            :user="user"
            :show-use-card="paymentInputsFound"
            :category-card="!!card.merchantCategory"
            :subscription-features="subscriptionsStore.features"
          />
        </router-link>
      </li>
    </ul>
    <div class="px-4 mb-3" v-if="!suggestionsRoute">
      <h2>All Cards</h2>
    </div>
    <ul
      class="card-list"
      v-if="cards.length"
      data-test="card-list"
      :aria-busy="loading"
    >
      <li
        v-for="(card, index) in cards"
        :key="card.cardUuid || index"
        :class="{ 'card-loader': !card.cardUuid && initialLoad }"
      >
        <router-link
          v-if="card.cardUuid"
          class="card-item-link"
          :to="{ name: 'card-details', params: { uuid: card.cardUuid } }"
        >
          <CardListItem
            :cardUuid="card.cardUuid"
            :user="user"
            :show-use-card="paymentInputsFound"
            :category-card="!!card.merchantCategory"
            :subscription-features="subscriptionsStore.features"
          />
        </router-link>
        <CardListItem v-else :loading="loading" aria-hidden="true" />
      </li>
      <li v-if="loading && !initialLoad" class="card-loader">
        <CardListItem :loading="loading" aria-hidden="true" />
      </li>
    </ul>
    <div v-else-if="!loadingError" class="text-center text-body-secondary">
      <p>No matching cards found</p>
    </div>
    <div ref="loader">
      <span class="visually-hidden" v-if="loading">Loading cards...</span>
    </div>
  </template>
  <div v-if="loadingError" class="text-center">
    <BaseAlert variant="warning">
      {{ loadingError }}
    </BaseAlert>
    <BaseButton
      variant="primary"
      class="mt-3"
      @click="() => observeLoader()"
      :disabled="loading"
      data-test="retry"
    >
      Try again
    </BaseButton>
  </div>
</template>

<style lang="scss" scoped>
@import "~bootstrap/scss/mixins/color-mode.scss";

.user-prompt {
  display: flex;
  flex-direction: column;
  height: 100%;
  align-items: center;
  justify-content: center;
  text-align: center;
  color: var(--bs-body-secondary);
}

.card-list {
  list-style: none;
  margin: 0;
  padding: 0;

  @for $i from 2 through 3 {
    :nth-child(#{$i} of li.card-loader) {
      opacity: 1 - (0.4 * ($i - 1));
    }
  }

  & > :last-child {
    margin-bottom: 0 !important;
  }
}

.card-item-link {
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: var(--bs-body-color);
}

.cards-search {
  color: $color-privacy-gray-3;

  &:focus-within {
    color: $color-privacy-gray-4;
  }
}

@include color-mode(dark) {
  .cards-search:focus-within {
    color: $color-privacy-gray-1;
  }
}
</style>
