import { ref, computed } from "vue";
import { defineStore } from "pinia";
import type {
  Subscription,
  SubscriptionFeaturesEnum,
} from "@/types/Subscription";
import { subscription as getSubscriptions } from "@/apis/subscription/subscription";

export const useSubscriptionsStore = defineStore("subscriptions", () => {
  // STATES
  const subscriptionState = ref<Subscription>();

  // GETTERS
  const features = computed(() => subscriptionState.value?.features);

  const featureValue = computed(
    () => (feature: SubscriptionFeaturesEnum) =>
      subscriptionState.value?.features?.[feature]
  );

  const hasFeature = computed(
    () => (feature: SubscriptionFeaturesEnum) =>
      !!subscriptionState.value?.features?.[feature]
  );

  // ACTIONS
  async function get(): Promise<Subscription> {
    const { data } = await getSubscriptions();
    const subscription = data.data[0];

    subscriptionState.value = subscription;

    return subscription;
  }

  return {
    // actions
    get,

    // getters
    features,
    featureValue,
    hasFeature,

    // state
    subscriptionState,
  };
});
