import { defineStore } from "pinia";
import { events as eventsApi } from "@/apis";
import type { Event } from "@/types/Event";

export const useEventStore = defineStore("event", () => {
  async function track(event: Event) {
    return eventsApi.track(event).catch(() => {});
  }

  async function log(data: Event[]) {
    return eventsApi.log(data).catch(() => {});
  }

  async function error(data: Event[]) {
    return eventsApi.error(data).catch(() => {});
  }

  // We have to create our own $reset functions since were
  // using the setup syntax
  function $reset() {}

  return { track, log, error, $reset };
});
