import Toastify from "toastify-js";

interface Options {
  gravity?: "top" | "bottom";
  style?: Toastify.Options["style"];
}

export default function popupAlert(
  message: string,
  variant: "danger" | "success" | "warning" | "info" = "danger",
  options: Options = {
    gravity: "bottom",
    style: {},
  }
) {
  const toast: any = Toastify({
    text: message,
    className: `alert alert-${variant}`,
    duration: 1500,
    onClick: () => toast.hideToast(),
    gravity: options.gravity,
    style: options.style,
    position: "center",
  }).showToast();
}
