<script lang="ts">
import { FundingType, type FundingSource } from "@/types/Funding";
const apiUrl = import.meta.env.EXPOSED_PRIVACY_WEB_BASE_URL;

interface ButtonProps {
  loading?: boolean;
  disabled?: boolean;
  isSelected?: boolean;
  showBorder?: boolean;
  source?: FundingSource;
}
</script>
<script setup lang="ts">
import { computed, ref } from "vue";

const props = withDefaults(defineProps<ButtonProps>(), {
  loading: false,
  disabled: true,
  isSelected: false,
  showBorder: true,
});

const icon = computed(() => {
  if (props.source?.icon?.startsWith("/")) {
    return apiUrl + props.source.icon;
  }

  return props.source?.icon;
});

const fundingType = computed(() => {
  if (props.source?.type === FundingType.BANK_ACCOUNT) {
    return "bank";
  }

  return "card";
});

const fundingName = computed(() => {
  return (props.source?.name || "Funding Source").slice(0, 20);
});

const fundingInstitution = computed(() => {
  return (props.source?.institution || "").slice(0, 20);
});

const fallbackIcon = ref("");

function iconError() {
  fallbackIcon.value = apiUrl + "/assets/images/icons/bank-40.svg";
}
</script>

<template>
  <section
    :class="
      (showBorder ? 'funding rounded-3 ' : 'px-4 ') +
      (isSelected ? 'funding-selected ' : '')
    "
  >
    <div v-if="loading" class="p-3" aria-hidden="true" data-test="placeholder">
      <div class="placeholder-glow mb-1">
        <span class="placeholder col-10" />
      </div>
      <div class="placeholder-glow">
        <span class="placeholder col-8" />
      </div>
    </div>
    <button
      v-else
      :disabled="disabled"
      class="funding-control w-100 p-3 rounded-3"
    >
      <p class="fw-medium mb-0" data-test="title">{{ fundingName }}</p>
      <div class="mt-2">
        <div
          class="icon-wrap"
          :class="{
            fallback: fallbackIcon,
            [`icon-${fundingType}`]: true,
          }"
        >
          <img
            v-if="icon"
            class="icon me-1"
            :class="{
              fallback: fallbackIcon,
              [`icon-${fundingType}`]: true,
            }"
            :src="fallbackIcon || icon"
            alt=""
            @error="iconError"
          />
        </div>

        {{ fundingInstitution }}
        <sup class="spacer">&hellip;</sup>
        {{ source?.number }}
      </div>
    </button>
  </section>
</template>

<style scoped lang="scss">
@import "~bootstrap/scss/mixins/color-mode.scss";

.funding {
  border: 1px solid rgba($color-privacy-black, 0.08);
}

.funding-control {
  text-align: left;
  border: 0;
  background: none;
  color: inherit;
}

@include color-mode(dark) {
  .funding {
    border-color: $color-privacy-gray-4;
  }
}

.funding-selected {
  background-color: $color-privacy-hover;
}

@include color-mode(dark) {
  .funding-selected {
    background-color: $color-privacy-hover-dark;
  }
}

.icon-wrap {
  display: inline-block;
  width: $funding-icon-large;
  height: $funding-icon-large;
  margin-right: 4px;

  &.fallback,
  &.icon-card {
    background-color: $color-privacy-gray-1;
    border-radius: $border-radius-sm;
  }
}
.icon {
  width: $funding-icon-large;
  height: auto;

  &.fallback,
  &.icon-card {
    width: $funding-icon-small;
    height: auto;
    margin-left: 3px;
  }
  &.icon-bank {
    border-radius: $border-radius-sm;
  }
}
.spacer {
  top: -0.2rem;
}
</style>
