import { defineStore } from "pinia";
import { ref } from "vue";
import * as merchantCategories from "@/apis/merchantCategories";
import type { MerchantCategory } from "@/types/MerchantCategory";

type MerchantCategoriesStates = {
  merchantCategories: Record<string, MerchantCategory>;
};

export default defineStore("merchantCategories", () => {
  const merchantCategoriesState = ref<
    MerchantCategoriesStates["merchantCategories"]
  >({});

  async function list(): Promise<merchantCategories.ListMerchantCategoriesResponse> {
    const { data } = await merchantCategories.list();

    data.data.forEach((category) => {
      merchantCategoriesState.value[category._id] = category;
    });

    return data;
  }

  return {
    // actions
    list,

    // states
    merchantCategories: merchantCategoriesState,
  };
});
