<script setup lang="ts">
import { ref, onMounted } from "vue";
import useMerchantCategoriesStore from "@/store/merchantCategories";
import BaseAlert from "@/components/BaseAlert/BaseAlert.vue";
import BaseButton from "@/components/BaseButton/BaseButton.vue";
import type { MerchantCategory } from "@/types/MerchantCategory";
import MerchantCategoryListItem from "../MerchantCategoryListItem/MerchantCategoryListItem.vue";
import IconChevronDown from "../icons/IconChevronDown.vue";
import IconChevronUp from "../icons/IconChevronUp.vue";

const { list: listMerchantCategories } = useMerchantCategoriesStore();

const loading = ref(false);
const loadingError = ref("");
const showOther = ref(false);
const primaryCategories = ref<MerchantCategory[]>([]);
const otherCategories = ref<MerchantCategory[]>([]);

onMounted(async () => {
  await loadMerchantCategories();
});

async function loadMerchantCategories() {
  loading.value = true;
  loadingError.value = "";

  try {
    const { data } = await listMerchantCategories();

    primaryCategories.value = data.filter((cat) => cat.isPrimary);
    otherCategories.value = data.filter((cat) => !cat.isPrimary);
  } catch (err) {
    loadingError.value = "Unable to load merchant categories";
  } finally {
    loading.value = false;
  }
}

function toggleShowOther() {
  showOther.value = !showOther.value;
}
</script>

<template>
  <div class="container">
    <ul
      class="categories-list"
      v-if="primaryCategories.length"
      data-test="primary-categories-list"
      :aria-busy="loading"
    >
      <li
        v-for="(cat, index) in primaryCategories"
        :key="cat._id || index"
        class="mb-2"
      >
        <MerchantCategoryListItem
          :merchant-category="cat"
          :is-primary="cat.isPrimary"
        />
      </li>
    </ul>

    <div
      v-if="otherCategories.length"
      class="other-container"
      data-test="other-container"
    >
      <button
        class="other-header d-flex justify-content-between align-items-center"
        data-test="show-other-button"
        @click="toggleShowOther"
      >
        <h2>More Categories</h2>
        <IconChevronUp v-if="showOther" />
        <IconChevronDown v-else />
      </button>
      <ul
        class="categories-list other-categories-list"
        v-if="showOther"
        data-test="other-categories-list"
        :aria-busy="loading"
      >
        <li
          v-for="(cat, index) in otherCategories"
          :key="cat._id || index"
          class="mb-2"
        >
          <MerchantCategoryListItem
            :merchant-category="cat"
            :is-primary="cat.isPrimary"
          />
        </li>
      </ul>
    </div>

    <div v-if="loadingError" class="text-center">
      <BaseAlert variant="warning">
        {{ loadingError }}
      </BaseAlert>
      <BaseButton
        variant="primary"
        class="mt-3"
        @click="loadMerchantCategories"
        :disabled="loading"
        data-test="retry"
      >
        Try again
      </BaseButton>
    </div>
  </div>
</template>

