.container {
  padding: 0 24px;
}

.categories-list {
  list-style: none;
  padding: 0;
  margin-bottom: 8px;
}

.other-categories-list {
  margin-top: 8px;
}

.other-container {
  background-color: var(--bs-body-bg);
  border-radius: 12px;
  padding: 8px;
}

.other-header {
  padding: 8px 16px 8px 8px;
  cursor: pointer;
  border: none;
  background-color: var(--bs-body-bg);
  width: 100%;
}

.other-header > h2 {
  margin-bottom: 0;
}
</style>
