import { useEventStore } from "@/store/event/eventStore";
import type { Card, CardUser } from "@/types/Card";
import { EXTENSION_EVENTS } from "@/types/Event";
import { ref } from "vue";
import popupAlert from "@/lib/popupAlert";
import appExtensionApi from "@/lib/extension/appExtensionApi";
import router from "@/router";

export function useFoundPaymentInputs() {
  const found = ref(false);

  appExtensionApi.message
    .send({
      target: "checkout",
      event: "paymentInputsFound",
    })
    .then((inputsFound) => {
      found.value = !!inputsFound?.success;
    })
    .catch(() => {
      // noop
    });

  return found;
}

export async function useFillCheckout(
  card: Card,
  user: CardUser,
  checkoutDetected = true
) {
  const eventStore = useEventStore();
  let tab;

  try {
    tab = await appExtensionApi.message.send({ event: "getCurrentTab" });

    eventStore.track({
      name: EXTENSION_EVENTS.FILL_CHECKOUT,
      data: { cardUuid: card.cardUuid, url: tab?.url },
    });

    // If we didn't find a checkout form but the user is still trying to
    // autofill the page, we want to track the URL to improve our parser
    if (!checkoutDetected) {
      eventStore.track({
        name: EXTENSION_EVENTS.FILL_CHECKOUT_NO_FORM,
        data: { url: tab?.url },
      });

      throw new Error("Payment fields not found");
    } else {
      appExtensionApi.message.send({
        event: "fillCheckout",
        target: "checkout",
        payload: {
          cardUuid: card.cardUuid,
          pan: card.PAN,
          cvv: card.CVV,
          expMonth: card.expMonth,
          expYear: card.expYear,
          expiry: `${card.expMonth}/${card.expYear}`,
          ...user,
        },
      });
    }
  } catch (err: any) {
    eventStore.error([
      {
        name: EXTENSION_EVENTS.FILL_CHECKOUT,
        data: {
          cardUuid: card.cardUuid,
          url: tab?.url,
          error: err.message,
        },
      },
    ]);

    popupAlert("Unable to fill checkout");
  }
}

export async function useShowCollapsedCard(id: string, interstitial: boolean) {
  if (interstitial) {
    router.push({
      name: "card-collapsed",
      query: { uuid: id },
    });

    return;
  }

  await appExtensionApi.message.send({
    event: "removeInterstitial",
    target: "checkout",
  });

  appExtensionApi.message.send({
    target: "checkout",
    event: "showCollapsedCard",
    payload: { cardUuid: id },
  });

  window.close();
}
