<script setup lang="ts">
import { useAuthStore } from "@/store/auth/authStore";
import { useUserStore } from "@/store/user/userStore";
import { useEventStore } from "@/store/event/eventStore";
import { useError } from "@/lib/errors/errors";
import { useRoute, useRouter } from "vue-router";
import BaseButton from "@/components/BaseButton/BaseButton.vue";
import BaseInput from "@/components/BaseInput/BaseInput.vue";
import BaseAlert from "@/components/BaseAlert/BaseAlert.vue";
import IconArrowRight from "@/components/icons/IconArrowRight.vue";
import { EXTENSION_EVENTS } from "@/types/Event";
import { ref } from "vue";
import { useMetricStore } from "@/store/metric/metricStore";

const appUrl = import.meta.env.EXPOSED_PRIVACY_WEB_BASE_URL;
const authStore = useAuthStore();
const userStore = useUserStore();
const eventStore = useEventStore();
const metricStore = useMetricStore();

const { errorMessage, setError } = useError();
const router = useRouter();
const route = useRoute();

const email = ref("");
const password = ref("");

const login = async () => {
  try {
    await authStore.login({ email: email.value, password: password.value });
  } catch (err: unknown) {
    setError(err, "Invalid email or password");
    return;
  }

  if (authStore.userToken) {
    const tfa: any = { name: "tfa" };

    if (route.query?.redirect) {
      tfa.query = { redirect: route.query.redirect };
    }

    return router.push(tfa);
  }

  try {
    await Promise.all([userStore.getUser(), metricStore.getMetrics()]);

    const { redirect, ...otherParams } = route.query || {};
    router.push({
      name: (redirect as string) || "cards",
      query: otherParams,
    });
  } catch (err: unknown) {
    setError(err, "Failed to get user");
  }
};

function trackSignup() {
  eventStore.track({ name: EXTENSION_EVENTS.SIGN_UP });
}
</script>

<template>
  <div class="px-4">
    <h1 class="mb-4">Login</h1>
    <BaseAlert
      variant="danger"
      class="mb-2"
      v-if="errorMessage"
      data-test="error-message"
    >
      {{ errorMessage }}
    </BaseAlert>

    <form @submit.prevent="login" class="d-grid gap-2">
      <BaseInput
        v-model="email"
        required
        aria-label="Email"
        type="email"
        data-test="email"
        placeholder="Email"
      />
      <BaseInput
        v-model="password"
        required
        aria-label="Password"
        type="password"
        data-test="password"
        placeholder="Password"
      />
      <BaseButton variant="primary" type="submit" data-test="submit">
        Log In
      </BaseButton>
      <BaseButton
        variant="link"
        :to="{ name: 'password-reset' }"
        data-test="reset"
      >
        Reset Password
      </BaseButton>
    </form>

    <section class="graphic p-4 mt-2 fw-medium">
      <div class="mb-2">Don't have an account?</div>
      <a
        :href="`${appUrl}/signup`"
        @click="trackSignup"
        class="text-success"
        data-test="signup"
        target="_blank"
      >
        <span class="text-success">Sign Up</span>
        <IconArrowRight class="ms-1 arrow-icon" />
      </a>
      <picture>
        <source
          type="image/webp"
          srcset="
            @/assets/images/privacy-cards@1x.webp 1x,
            @/assets/images/privacy-cards@2x.webp 2x
          "
        />
        <source
          type="image/png"
          srcset="
            @/assets/images/privacy-cards@1x.png 1x,
            @/assets/images/privacy-cards@2x.png 2x
          "
        />
        <img
          src="@/assets/images/privacy-cards@1x.png"
          class="cards"
          alt="A stack of Privacy cards for different vendors"
        />
      </picture>
    </section>
  </div>
</template>

<style scoped lang="scss">
.graphic {
  background-color: $color-privacy-brand-lime;
  border-radius: $border-radius;
  border: 1px solid rgba(0, 0, 0, 0.1);
  height: 160px;
  overflow: hidden;
  position: relative;
  color: $body-color;
}

.arrow-icon {
  height: 12px;
  width: 12px;
}
.cards {
  position: absolute;
  bottom: 0;
  left: 4px;
}
</style>
