<script setup lang="ts">
import { computed } from "vue";
import { getLabelIcon, getLabelText, useLogo } from "@/lib/cards/style";
import type { Card } from "@/types/Card";
import IconCardLogoBase from "../icons/IconCardLogoBase.vue";
import { CardType } from "@/types/Card";
import IconDigitalWalletDefault from "../icons/IconDigitalWalletDefault.vue";

const props = defineProps<{
  mccIconUrl?: Card["mccIconUrl"];
  type: Card["type"];
  style: Card["style"];
  merchantCategory?: Card["merchantCategory"];
}>();

const labelIcon = computed(() =>
  getLabelIcon(props.type, props.merchantCategory)
);
const labelText = computed(() =>
  getLabelText(props.type, props.merchantCategory)
);

const logo = computed(() =>
  useLogo({
    mccIconUrl: props.mccIconUrl,
    style: props.style,
    type: props.type,
    merchantCategory: props.merchantCategory,
  })
);

const useComponent = computed(() => {
  return logo.value.type === "component" || logo.value.type === "default";
});
</script>
<template>
  <div
    class="d-flex justify-content-between align-items-start card-type-header p-3"
  >
    <div
      class="card-icon-emoji d-flex align-items-center justify-content-center"
      v-if="logo.type === 'icon'"
      data-test="card-icon"
    >
      {{ logo.src as string }}
    </div>
    <IconDigitalWalletDefault
      v-else-if="props.type === CardType.DIGITAL_WALLET"
      class="card-icon-logo"
      data-test="card-logo"
    />
    <IconCardLogoBase
      class="card-icon-default"
      data-test="card-logo"
      v-else-if="useComponent"
    />
    <img
      v-else
      class="card-icon-logo"
      :src="(logo.src as string)"
      :alt="logo.alt"
      loading="lazy"
      data-test="card-logo"
    />
    <div class="card-type-label d-flex">
      <span v-if="labelIcon" class="d-flex me-2" data-test="label-icon">
        <component :is="labelIcon" />
      </span>
      <span data-test="label-text">
        {{ labelText }}
      </span>
    </div>
  </div>
</template>

