<script lang="ts">
interface ModalProps {
  bankAccountID?: number;
  fundingSources: FundingSource[];
  open: boolean;
}

interface Emits {
  (event: "update", value: number | undefined): void;
}
</script>

<script setup lang="ts">
import { ref, watchEffect } from "vue";
import BaseModal from "@/components/BaseModal/BaseModal.vue";
import FundingButton from "@/components/Funding/FundingButton/FundingButton.vue";
import type { FundingSource } from "@/types/Funding";

const props = defineProps<ModalProps>();
const emit = defineEmits<Emits>();

const currentBankAccountID = ref<number>();

watchEffect(() => {
  currentBankAccountID.value = props.bankAccountID;
});

function reset() {
  currentBankAccountID.value = props.bankAccountID;
}

function updateHandler(fundingSource: FundingSource, event: Event) {
  // We only use bank account IDs because it's only possible to have more
  // than one funding source if they're bank accounts.
  currentBankAccountID.value = fundingSource.bankAccountID;

  event.preventDefault();
  emit("update", currentBankAccountID.value);
}
</script>

<template>
  <BaseModal
    class="funding-modal"
    title="Funding Source"
    :open="props.open"
    overflow="visible"
    :padSides="false"
    :showCloseButton="true"
    @close="reset"
  >
    <div class="container-fluid p-0">
      <FundingButton
        v-for="source in fundingSources"
        :showBorder="false"
        :isSelected="source.bankAccountID === currentBankAccountID"
        :disabled="false"
        :key="source.uuid"
        :source="source"
        @click="(event) => updateHandler(source, event)"
      />
    </div>
  </BaseModal>
</template>

<style scoped lang="scss">
.funding-modal {
  width: 292px;
}
</style>
