<script setup lang="ts">
import { computed, onBeforeMount, onUnmounted, ref } from "vue";
import { useRoute } from "vue-router";
import { CardState } from "@/types/Card";
import useCardsStore from "@/store/cards";
import BaseButton from "@/components/BaseButton/BaseButton.vue";
import BaseAlert from "@/components/BaseAlert/BaseAlert.vue";
import CardCollapsed from "@/components/CardCollapsed/CardCollapsed.vue";
import InterstitialCloseButton from "@/components/InterstitialCloseButton/InterstitialCloseButton.vue";
import appExtensionApi from "@/lib/extension/appExtensionApi";

const cardsStore = useCardsStore();
const route = useRoute();

const cardUuid = route.query.uuid as string;
const loadingCard = ref(true);
const loadingError = ref("");

const card = computed(() => cardsStore.card(cardUuid) || {});
const active = computed(() => {
  const { state } = card.value;
  return state === CardState.OPEN || state === CardState.PAUSED;
});

// Add the background color to the interstitial on mobile when loading
// this component, and remove it when leaving
onBeforeMount(() => {
  appExtensionApi.message.send({
    target: "checkout",
    event: "updateInterstitialStyles",
    payload: { includeBg: false },
  });
});

onUnmounted(() => {
  appExtensionApi.message.send({
    target: "checkout",
    event: "updateInterstitialStyles",
    payload: { includeBg: true },
  });
});

loadCard();

async function loadCard() {
  loadingError.value = "";
  loadingCard.value = true;

  try {
    await cardsStore.get(cardUuid);
  } catch (err) {
    loadingError.value = "Unable to load card details";
  } finally {
    loadingCard.value = false;
  }
}
</script>

<template>
  <div v-if="loadingError" class="text-center d-flex align-items-center">
    <div>
      <BaseAlert variant="warning">
        {{ loadingError }}
      </BaseAlert>
      <InterstitialCloseButton class="ms-auto" />
    </div>
    <BaseButton
      variant="primary"
      class="mt-3"
      @click="loadCard"
      :disabled="loadingCard"
    >
      Try again
    </BaseButton>
  </div>
  <CardCollapsed
    v-else-if="card.cardUuid"
    :copy-info="active"
    :cvv="card.CVV"
    :exp-month="card.expMonth"
    :exp-year="card.expYear"
    :mcc="card.mcc"
    :mcc-icon-url="card.mccIconUrl"
    :pan="card.PAN"
    :type="card.type"
    :style="card.style"
    :uuid="card.cardUuid"
    :merchant-category="card.merchantCategory"
  ></CardCollapsed>
</template>

<style scoped lang="scss"></style>
