<script setup lang="ts">
import { computed, inject, onMounted, ref } from "vue";
import { useRoute, useRouter } from "vue-router";
import PrivacyCard from "@/components/PrivacyCard/PrivacyCard.vue";
import SpendLimitButton from "@/components/SpendLimit/SpendLimitButton/SpendLimitButton.vue";
import type { SubmitEvent } from "@/components/SpendLimit/SpendLimitModal/SpendLimitModal.vue";
import { CardType, type Card } from "@/types/Card";
import type { MerchantCategory } from "@/types/MerchantCategory";
import {
  useUpdateSpendLimit,
  type SpendLimitUpdates,
} from "@/lib/cards/spendLimit";
import popupAlert from "@/lib/popupAlert";
import BaseButton from "@/components/BaseButton/BaseButton.vue";
import CardNickname from "@/components/CardNickname/CardNickname.vue";
import FundingButton from "@/components/Funding/FundingButton/FundingButton.vue";
import FundingModal from "@/components/Funding/FundingModal/FundingModal.vue";
import useCardsStore from "@/store/cards";
import useFundingStore from "@/store/funding";
import { useUserStore } from "@/store/user/userStore";
import { useEventStore } from "@/store/event/eventStore";
import useMerchantCategoriesStore from "@/store/merchantCategories";
import type { CreateCardData } from "@/apis/cards";
import {
  useFoundPaymentInputs,
  useFillCheckout,
  useShowCollapsedCard,
} from "@/lib/cards/useCheckout";
import { BaseDropdown, BaseDropdownItem } from "@/components/BaseDropdown";
import { CONTEXT } from "@/types/App";
import { CARD_EVENTS } from "@/types/Event";
import extensionApi from "@/lib/extension/appExtensionApi";
import { hasActivityConsent } from "@/lib/updates";

const cardsStore = useCardsStore();
const fundingStore = useFundingStore();
const userStore = useUserStore();
const eventStore = useEventStore();
const merchantCategoryStore = useMerchantCategoriesStore();
const router = useRouter();
const route = useRoute();

const merchantCategory = route?.params?.merchantCategory
  ? merchantCategoryStore.merchantCategories[
      route!.params!.merchantCategory as string
    ]
  : undefined;

const card = ref<Card>({
  type: route?.params?.type || CardType.MERCHANT_LOCKED,
  merchantCategory: merchantCategory,
  memo: "",
  hostname: "",
  meta: {},
  spendLimit: route?.params?.type === CardType.SINGLE_USE ? 100 : null,
  style: merchantCategory
    ? {
        icon: merchantCategory.icon,
        bgColor: merchantCategory.color,
      }
    : null,
} as Card);

const cardUser = computed(() => ({
  name: userStore.user.firstName + " " + userStore.user.lastName,
  firstName: userStore.user.firstName,
  lastName: userStore.user.lastName,
  streetAddress: userStore.user.address1 + " " + userStore.user.address2,
  addressLine1: userStore.user.address1,
  addressLine2: userStore.user.address2,
  addressLevel2: userStore.user.city,
  addressLevel1: userStore.user.state,
  postalCode: userStore.user.zipcode,
}));
const updateSpendLimit = useUpdateSpendLimit(card);
const creating = ref(false);
const paymentInputsFound = useFoundPaymentInputs();
const interstitial = inject("context") === CONTEXT.INTERSTITIAL;

const loadingFunding = ref(true);
const fundingError = ref("");
const showFundingModal = ref(false);

const funding = computed(() => fundingStore.source(card.value.bankAccountID));
const fundingSources = computed(() => fundingStore.all);

onMounted(async () => {
  loadFunding();
  let name = "Nickname";
  const activityConsent = await hasActivityConsent();

  if (merchantCategory) {
    name = merchantCategory.name;
  } else if (activityConsent) {
    try {
      const tab = await extensionApi.message.send({ event: "getCurrentTab" });

      if (tab?.url?.startsWith("http")) {
        const url = new URL(tab?.url);
        name = url.hostname.replace("www.", "");
        card.value.hostname = name;
        card.value.meta.hostname = name;
      }
    } catch (error) {
      // noop
    }
  }

  card.value.memo = name;
});

async function loadFunding() {
  loadingFunding.value = true;
  fundingError.value = "";

  try {
    await fundingStore.list();
  } catch (err) {
    fundingError.value = "Unable to load funding source";
  } finally {
    loadingFunding.value = false;
  }
}

function handleNickNameUpdate(value: string) {
  card.value.memo = value;
}

function handleSpendLimitUpdate(event: SubmitEvent) {
  let updateData: SpendLimitUpdates;

  try {
    updateData = updateSpendLimit(event);
  } catch (err: any) {
    popupAlert(err.message);
    return;
  }

  card.value.spendLimit = updateData.spendLimit as Card["spendLimit"];
  card.value.spendLimitDuration = updateData.spendLimitDuration!;
}

async function handleFundingSourceUpdate(bankAccountID: number | undefined) {
  if (!bankAccountID || bankAccountID === funding.value?.bankAccountID) {
    return;
  }

  card.value.bankAccountID = bankAccountID;

  showFundingModal.value = false;
}

function getCardCreateData(card: Card): CreateCardData {
  const { merchantCategory, style, ...rest } = card;
  const createData = rest as CreateCardData;

  if (merchantCategory) {
    createData.merchantCategory = (
      card.merchantCategory as MerchantCategory
    )._id;

    if (!createData.meta) {
      createData.meta = {};
    }

    createData.meta.customStyle = card.style;
  }

  return createData;
}

async function createCard(autofill = false) {
  try {
    creating.value = true;
    const result = await cardsStore.create(getCardCreateData(card.value));

    if (autofill) {
      await useFillCheckout(result, cardUser.value, paymentInputsFound.value);
      useShowCollapsedCard(result.cardUuid, interstitial);
      return;
    }

    popupAlert("Card created", "success");

    eventStore.track({
      name: CARD_EVENTS.CREATED,
      data: {
        cardType: result.type,
        spendLimit: result.spendLimit,
        spendLimitDuration: result.spendLimitDuration,
        merchantCategory: (result.merchantCategory as MerchantCategory)?._id,
      },
    });

    router.replace({
      name: "card-details",
      params: { uuid: result.cardUuid },
      query: { newCategoryCard: merchantCategory?.name },
    });
  } catch (err: any) {
    creating.value = false;
    popupAlert(err.message);
  }
}
</script>

<template>
  <div class="px-4">
    <CardNickname
      v-if="card.memo"
      class="mb-3"
      :value="card.memo"
      @update="handleNickNameUpdate"
    />
    <section class="mb-3">
      <PrivacyCard
        class="mx-auto"
        :copy-info="false"
        :cvv="card.CVV"
        :exp-month="card.expMonth"
        :exp-year="card.expYear"
        :mcc="card.mcc"
        :mcc-icon-url="card.mccIconUrl"
        :pan="card.PAN"
        :type="card.type"
        :style="card.style"
        :network="card.network"
        :new-card="true"
        :merchant-category="card.merchantCategory"
      />
    </section>
    <section class="mb-3">
      <SpendLimitButton
        :is-single-use="card.type === CardType.SINGLE_USE"
        :spend-limit="card.spendLimit"
        :spend-limit-duration="card.spendLimitDuration"
        :spent-this-month-rolling="card.spentThisMonthRolling"
        :spent-this-week="card.spentThisWeek"
        :spent-this-year-rolling="card.spentThisYearRolling"
        :spent-total="card.spentTotal"
        @update="handleSpendLimitUpdate"
      />
    </section>
    <section class="mb-3">
      <div v-if="fundingError">
        <p>{{ fundingError }}</p>
      </div>
      <div v-else>
        <FundingButton
          :loading="loadingFunding"
          :source="funding"
          :disabled="fundingSources.length <= 1"
          @click="showFundingModal = true"
        />
        <FundingModal
          :loading="loadingFunding"
          :fundingSources="fundingSources"
          :bankAccountID="funding?.bankAccountID"
          :open="showFundingModal"
          @update="handleFundingSourceUpdate"
          @close="showFundingModal = false"
        />
      </div>
    </section>
    <div v-if="paymentInputsFound" class="d-flex" data-test="split-create">
      <BaseButton class="rounded-end-0 flex-grow-1" @click="createCard(true)">
        Create and Autofill
      </BaseButton>
      <BaseDropdown toggle-variant="primary" label="" :split="true">
        <template #button>
          <span class="visually-hidden">Toggle Dropdown</span>
        </template>
        <BaseDropdownItem @click="createCard(false)">
          Create Card
        </BaseDropdownItem>
      </BaseDropdown>
    </div>
    <BaseButton
      v-else
      data-test="btn-create-card"
      class="mb-3 w-100"
      @click="createCard(false)"
      :disabled="creating"
    >
      Create Card
    </BaseButton>
  </div>
</template>
