<script setup lang="ts">
import { nextTick, onUnmounted, watchEffect } from "vue";
import {
  createPopperLite as createPopper,
  flip,
  preventOverflow,
  type Instance as PopperInstance,
} from "@popperjs/core";
import { ref } from "vue";
import BaseButton, { type ButtonProps } from "../BaseButton/BaseButton.vue";

type ClickOutsideElement = HTMLElement & {
  clickOutside: (event: MouseEvent) => void;
};

type DropdownProps = {
  label?: string;
  toggleVariant?: ButtonProps["variant"];
  split?: boolean;
  smallButton?: boolean;
};

withDefaults(defineProps<DropdownProps>(), {
  label: "Select item",
  toggleVariant: "secondary",
  split: false,
});

onUnmounted(() => unbindClickOutside());

const show = ref(false);
const dropdown = ref<HTMLElement | null>(null);
const trigger = ref<HTMLElement | null>(null);
const menu = ref<HTMLElement | null>(null);
let popper: PopperInstance | null = null;

async function toggleMenu() {
  if (!popper) {
    // @ts-ignore
    popper = createPopper(trigger.value.element, menu.value, {
      placement: "bottom-end",
      modifiers: [flip, preventOverflow],
    });
  }

  show.value = !show.value;

  await nextTick();

  popper.update();
}

function bindClickOutside() {
  const el = dropdown.value! as ClickOutsideElement;

  el.clickOutside = function handler(event: any) {
    if (!(el === event.target || el.contains(event.target))) {
      show.value = false;
    }
  };

  document.body.addEventListener("click", el.clickOutside);
}

function unbindClickOutside() {
  popper?.destroy();
  const el = dropdown.value! as ClickOutsideElement;

  if (!el) {
    return;
  }

  document.body.removeEventListener("click", el.clickOutside);
}

watchEffect(() => {
  if (dropdown.value) {
    bindClickOutside();
  }
});
</script>

<template>
  <div ref="dropdown" class="dropdown">
    <BaseButton
      ref="trigger"
      :variant="toggleVariant"
      class="dropdown-toggle"
      :class="{ show, 'rounded-start-0': split, 'small-button': smallButton }"
      :aria-expanded="show"
      @click="toggleMenu"
      :size="smallButton ? 'sm' : undefined"
      data-test="dropdown-trigger"
    >
      <slot name="button">
        {{ label }}
      </slot>
    </BaseButton>
    <ul
      ref="menu"
      class="dropdown-menu shadow"
      role="listbox"
      tabindex="0"
      :class="{ show }"
      @click="toggleMenu"
    >
      <slot />
    </ul>
  </div>
</template>

<style scoped lang="scss">
@import "~bootstrap/scss/mixins/color-mode.scss";

.dropdown-menu {
  border: 1px solid rgba($color-privacy-black, 0.08);
}

.small-button {
  padding: 0 !important;
}

@include color-mode(dark) {
  .dropdown-menu {
    border-color: $color-privacy-gray-4;
  }
}
</style>
