<script lang="ts">
interface InputGroupProps {
  prefix?: string;
  suffix?: string;
}
</script>

<script setup lang="ts">
import { useSlots } from "vue";

defineProps<InputGroupProps>();

const slots = useSlots();

const hasPrefix = !!slots.prefix;
const hasSuffix = !!slots.suffix;
</script>

<template>
  <div class="input-group">
    <span v-if="hasPrefix || prefix" class="input-group-text pe-1">
      <slot name="prefix">
        {{ prefix }}
      </slot>
    </span>
    <slot />
    <span v-if="hasSuffix || suffix" class="input-group-text ps-1">
      <slot name="suffix">
        {{ suffix }}
      </slot>
    </span>
  </div>
</template>

<style lang="scss" scoped>
