<script lang="ts">
interface PropType {
  mcc?: Card["mcc"];
  mccIconUrl?: Card["mccIconUrl"];
  size?: CardLogoSize;
  style: Card["style"];
  type: Card["type"];
  merchantCategory?: Card["merchantCategory"];
}
</script>

<script setup lang="ts">
import { useBackgroundColor, useLogo } from "@/lib/cards/style";
import { CardLogoSize, type Card } from "@/types/Card";
import { computed } from "vue";

const props = withDefaults(defineProps<PropType>(), {
  size: CardLogoSize.MEDIUM,
});

const inlineStyles = computed(() =>
  useBackgroundColor({
    mcc: props.mcc,
    mccIconUrl: props.mccIconUrl,
    style: props.style,
    type: props.type,
    merchantCategory: props.merchantCategory,
  })
);

const logo = computed(() =>
  useLogo({
    mcc: props.mcc,
    mccIconUrl: props.mccIconUrl,
    style: props.style,
    type: props.type,
    merchantCategory: props.merchantCategory,
  })
);

const useComponent = computed(() => {
  return logo.value.type === "component" || logo.value.type === "default";
});

const classNames = computed(() => [
  "logo",
  `logo-${props.size}`,
  {
    "logo-mcc": logo.value.type === "mcc",
    "logo-component": logo.value.type === "component",
  },
]);
</script>

<template>
  <div :class="classNames" :style="inlineStyles">
    <div class="icon" v-if="logo.type === 'icon'" data-test="card-icon">
      {{ logo.src as string }}
    </div>
    <component
      v-else-if="useComponent"
      :is="logo.src"
      class="svg"
      :aria-label="logo.alt"
    />
    <img
      v-else
      class="img"
      :src="(logo.src as string)"
      :alt="logo.alt"
      loading="lazy"
      data-test="card-logo"
    />
  </div>
</template>

<style scoped lang="scss">
@import "~bootstrap/scss/mixins";

// Keys are derived from the CardLogoSize enum
$sizes: (
  "xs": (
    fontSize: 12,
    logo: 20,
    padding: 6,
    radius: 6,
  ),
  "sm": (
    fontSize: 14,
    logo: 32,
    padding: 8,
    radius: 8,
  ),
  "md": (
    fontSize: 16,
    logo: 40,
    padding: 10,
    radius: 12,
  ),
  "lg": (
    fontSize: 20,
    logo: 48,
    padding: 12,
    radius: 14,
  ),
  "xl": (
    fontSize: 24,
    logo: 56,
    padding: 12,
    radius: 16,
  ),
);

@each $size, $vals in $sizes {
  .logo-#{$size} {
    --size: #{map-get($vals, logo)}px;
    --padding: #{map-get($vals, padding)}px;
    --radius: #{map-get($vals, radius)}px;
  }
}

@each $size, $vals in $sizes {
  .logo-#{$size} {
    --size: #{map-get($vals, logo)}px;
    --padding: #{map-get($vals, padding)}px;
    --font-size: #{map-get($vals, fontSize)}px;
    --radius: #{map-get($vals, radius)}px;
  }
}

.logo {
  --border: 4px;
  height: var(--size);
  width: var(--size);
  display: flex;
  align-items: center;
  justify-content: center;
  line-height: 1;
  border-radius: var(--radius);
  overflow: hidden;

  &:not(.logo-component) {
    background-color: var(--bs-secondary-bg);
    color: $color-privacy-gray-2;
    padding: var(--padding);
    outline: var(--border) solid rgba($blue, 0);
  }

  a > &:hover {
    outline-color: rgba($blue, 0.2);
    transition: outline 150ms cubic-bezier(0, 0, 0, 1);
  }
}

.icon {
  font-size: var(--font-size);
}

.img {
  object-fit: contain;
  width: 100%;
  height: 100%;
  position: relative;
}

.logo-mcc .img {
  filter: contrast(15%) brightness(175%) sepia(50%);
}

.svg {
  width: 100%;
  height: 100%;
}
</style>
