import { isOtcLoginResponse, isTfaLoginResponse } from "@/lib/typeguards";
import { ref } from "vue";
import { defineStore } from "pinia";
import * as Sentry from "@sentry/vue";
import { auth as authApi } from "@/apis";
import type {
  AuthStates,
  LoginPayload,
  CodeLoginPayload,
  MessageResponse,
  TfaLoginResponse,
} from "@/types/Auth";
import extensionApi from "@/lib/extension/appExtensionApi";

export const useAuthStore = defineStore("auth", () => {
  // STATES
  const token = ref<AuthStates["token"]>("");
  const userToken = ref<AuthStates["userToken"]>("");
  const tfaMessage = ref<AuthStates["tfaMessage"]>("");

  async function init() {
    token.value =
      (await extensionApi.storage.get("token")).token || token.value;
    userToken.value =
      (await extensionApi.storage.get("userToken")).userToken ||
      userToken.value;
  }

  // ACTIONS
  async function login({ email, password }: LoginPayload) {
    const { data } = await authApi.login({ email, password });

    if (isTfaLoginResponse(data)) {
      await extensionApi.storage.set({
        userToken: data.userToken,
        startPage: "tfa",
        secondaryAuthType: "tfa",
      });

      tfaMessage.value = data.message;
      userToken.value = data.userToken;
    } else if (isOtcLoginResponse(data)) {
      await extensionApi.storage.set({
        userToken: data.userToken,
        startPage: "tfa",
        secondaryAuthType: "otc",
      });

      tfaMessage.value = data.message;
      userToken.value = data.userToken;
    } else {
      await extensionApi.storage.set({ token: data.token });

      token.value = data.token;

      try {
        Sentry.setUser({ id: data.id });
      } catch (err) {
        /* noop */
      }
    }

    return data;
  }

  async function logout() {
    await authApi.logout();

    try {
      Sentry.getCurrentScope()?.clear();
    } catch (err) {
      /* noop */
    }

    $reset();
  }

  async function postTfaLogin(
    data: TfaLoginResponse,
    headers: Record<string, any> = {}
  ) {
    if (data.token) {
      await extensionApi.storage.set({ token: data.token });

      if (headers["x-tfa-deviceid"]) {
        await extensionApi.storage.set({
          deviceId: headers["x-tfa-deviceid"],
        });
      }

      await clearTwoFactorAuth();

      token.value = data.token;
    }

    return data;
  }

  async function tfaLogin({ code, rememberDevice }: CodeLoginPayload) {
    const { data, headers } = await authApi.tfaLogin({
      code,
      rememberDevice,
      userToken: userToken.value,
    });

    return postTfaLogin(data, headers);
  }

  async function oneTimeCodeLogin(payload: CodeLoginPayload) {
    const { data, headers } = await authApi.oneTimeCodeLogin({
      ...payload,
      userToken: userToken.value,
    });

    return postTfaLogin(data, headers);
  }

  async function sendVerifySms(): Promise<MessageResponse> {
    const { data } = await authApi.sendVerifySms({
      userToken: userToken.value,
    });

    await extensionApi.storage.set({ startPage: "tfa-recovery-confirm" });

    return data;
  }

  async function removeTFA(verificationCode: string): Promise<void> {
    const { data } = await authApi.removeTFA({
      verificationCode,
      userToken: userToken.value,
    });

    await clearTwoFactorAuth();

    return data;
  }

  async function sendOneTimeCode(): Promise<MessageResponse> {
    const { data } = await authApi.sendOneTimeCode({
      userToken: userToken.value,
    });

    return data;
  }

  async function sendPasswordResetEmail(email: string): Promise<void> {
    await authApi.sendPasswordResetEmail({ email });
  }

  async function clearTwoFactorAuth() {
    userToken.value = "";
    tfaMessage.value = "";

    await extensionApi.storage.remove([
      "userToken",
      "startPage",
      "secondaryAuthType",
    ]);
  }

  async function getShortLivedAuthToken() {
    const { data } = await authApi.getShortLivedToken();
    return data.token;
  }

  function $reset() {
    token.value = "";
    userToken.value = "";
    tfaMessage.value = "";
  }

  return {
    // state
    token,
    userToken,
    tfaMessage,

    // actions
    clearTwoFactorAuth,
    init,
    login,
    logout,
    tfaLogin,
    oneTimeCodeLogin,
    sendVerifySms,
    removeTFA,
    sendOneTimeCode,
    sendPasswordResetEmail,
    $reset,
    getShortLivedAuthToken,
  };
});
