import { DeclineReasons, type Transaction } from "@/types/Transaction";

const declineReasonMap: Record<DeclineReasons, string> = {
  [DeclineReasons.CARD_PAUSED]: "Card was paused at time of transaction.",
  [DeclineReasons.CARD_CLOSED]: "Card was closed at time of transaction.",
  [DeclineReasons.GLOBAL_TRANSACTION_LIMIT]:
    "Transaction exceeded your global spend limit.",
  [DeclineReasons.GLOBAL_MONTHLY_LIMIT]:
    "Transaction exceeded your monthly spend limit.",
  [DeclineReasons.USER_TRANSACTION_LIMIT]:
    "Transaction exceeded your card's spend limit.",
  [DeclineReasons.UNAUTHORIZED_MERCHANT]:
    "An unauthorized merchant charged your card.",
  [DeclineReasons.SINGLE_USE_RECHARGED]:
    "A single-use card was charged multiple times.",
  [DeclineReasons.BANK_CONNECTION_ERROR]:
    "We were unable to connect to your funding source.",
  [DeclineReasons.INSUFFICIENT_FUNDS]:
    "Your funding source didn't appear to have enough funds for this transaction.",
  [DeclineReasons.INSUFFICIENT_FUNDS_JIT]:
    "Your funding source didn't appear to have enough funds for this transaction.",
  [DeclineReasons.MERCHANT_BLACKLIST]:
    "This merchant is not permitted to make charges on Privacy cards.",
  [DeclineReasons.INVALID_CARD_DETAILS]:
    "Incorrect card details. The card form may have been filled out incorrectly.",
  [DeclineReasons.BANK_NOT_VERIFIED]:
    "Your funding source had not yet been verified.",
  [DeclineReasons.ACCOUNT_STATE_REVIEW]:
    "Your account was paused at the time of this transaction.",
  [DeclineReasons.INACTIVE_ACCOUNT]:
    "Your account was paused at the time of this transaction.",
  [DeclineReasons.ACCOUNT_STATE_TRANSACTION_FAIL]:
    "Your account was paused at the time of this transaction.",
  [DeclineReasons.FOREIGN_MERCHANT_VELOCITY]:
    "Your account was paused at the time of this transaction.",
  [DeclineReasons.MERCHANT_ABUSE]: "Suspicious merchant activity",
};

export function parseDeclineReason(reasonCode: Transaction["declineReason"]) {
  return declineReasonMap[reasonCode!] ?? "Declined";
}
