import { computed, ref } from "vue";
import { defineStore } from "pinia";
import { funding } from "@/apis";
import { FundingCardType, type FundingSource } from "@/types/Funding";

export type FundingList = funding.ListFundingResponse["data"];

export default defineStore("funding", () => {
  // STATES
  const listState = ref<FundingList>([]);

  // GETTERS
  const source = computed(
    () =>
      (uuidOrID: string | number): FundingSource | undefined => {
        let funding = listState.value.find(
          (item) => item.uuid === uuidOrID || item.bankAccountID === uuidOrID
        );

        if (!funding) {
          funding = listState.value.find((item) => item.default);
        }

        return funding;
      }
  );

  const all = computed(() => listState.value);

  // ACTIONS
  async function list(): Promise<FundingList> {
    if (listState.value.length) {
      return Promise.resolve(listState.value);
    }

    const { data } = await funding.list();

    // Funding source API includes subscription cards, which this extension has no use for
    const filteredData = data.data.filter((fundingSource) => {
      if (fundingSource.fundingType === FundingCardType.SUBSCRIPTION) {
        return false;
      }

      return true;
    });

    listState.value = filteredData;

    return filteredData;
  }

  function $reset() {
    listState.value = [];
  }

  return {
    // actions
    $reset,
    list,

    // getters
    source,
    all,

    // states
    data: listState,
  };
});
