<script setup lang="ts">
import { computed, onMounted, ref } from "vue";
import { useAuthStore } from "@/store/auth/authStore";
import { useUserStore } from "@/store/user/userStore";
import { useMetricStore } from "@/store/metric/metricStore";
import { useError } from "@/lib/errors/errors";
import appExtensionApi from "@/lib/extension/appExtensionApi";
import BaseAlert from "@/components/BaseAlert/BaseAlert.vue";
import BaseInput from "@/components/BaseInput/BaseInput.vue";
import BaseButton from "@/components/BaseButton/BaseButton.vue";
import BaseCheckbox from "@/components/BaseCheckbox/BaseCheckbox.vue";
import { useRoute, useRouter } from "vue-router";
import popupAlert from "@/lib/popupAlert";

const authStore = useAuthStore();
const userStore = useUserStore();
const metricStore = useMetricStore();
const route = useRoute();
const router = useRouter();
const { errorMessage, setError } = useError();

const authType = ref("tfa");
const code = ref("");
const rememberDevice = ref(false);
const sendingVerifySms = ref(false);

const isTFACode = computed(() => authType.value === "tfa");
const isSubmitDisabled = computed(() => code.value.length !== 6);

onMounted(async () => {
  const { secondaryAuthType } = await appExtensionApi.storage.get(
    "secondaryAuthType"
  );

  if (secondaryAuthType) {
    authType.value = secondaryAuthType;
  }
});

async function login() {
  const params = {
    code: code.value,
    rememberDevice: rememberDevice.value,
  };

  try {
    if (isTFACode.value) {
      await authStore.tfaLogin(params);
    } else {
      await authStore.oneTimeCodeLogin(params);
    }
  } catch (err: unknown) {
    setError(err, "Code is invalid");
    return;
  }

  try {
    await Promise.all([userStore.getUser(), metricStore.getMetrics()]);

    router.push({ name: (route.query?.redirect as string) || "cards" });
  } catch (err: unknown) {
    setError(err, "Failed to get user");
  }
}

async function sendVerifySms() {
  sendingVerifySms.value = true;

  try {
    await authStore.sendVerifySms();

    sendingVerifySms.value = false;
    router.push({ name: "tfa-recovery-confirm" });
  } catch (err: unknown) {
    sendingVerifySms.value = false;
    setError(err, "Failed to send verify sms code");
  }
}

async function resendOtc() {
  sendingVerifySms.value = true;

  try {
    await authStore.sendOneTimeCode();

    popupAlert("One time code sent to your email", "success");
  } catch (err: unknown) {
    setError(err, "Failed to resend one time code");
  }

  sendingVerifySms.value = false;
}

async function navigateToLogin() {
  await authStore.clearTwoFactorAuth();

  router.push({ name: "login" });
}
</script>

<template>
  <div class="px-4">
    <h1 class="mb-4">Two-Factor Auth</h1>
    <p v-if="authStore.tfaMessage">
      {{ authStore.tfaMessage }}
    </p>
    <p v-else>Please enter your 2FA code below.</p>
    <form @submit.prevent="login" class="d-grid gap-2 mb-2">
      <BaseAlert
        v-if="errorMessage"
        variant="danger"
        data-test="tfa-error-message"
      >
        {{ errorMessage }}
      </BaseAlert>
      <BaseInput
        v-model="code"
        placeholder="6 Digit code"
        maxlength="6"
        data-test="tfa-code-input"
        aria-label="6 Digit code"
        autocomplete="one-time-code"
        inputmode="numeric"
      />
      <div class="btn btn-secondary checkbox-wrapper d-flex align-items-center">
        <BaseCheckbox
          v-model="rememberDevice"
          class="me-1"
          id="remember-device"
          data-test="tfa-remember-device"
        />
        <label for="remember-device">Remember device for 90 days</label>
      </div>
      <BaseButton
        variant="primary"
        type="submit"
        data-test="tfa-submit"
        :disabled="isSubmitDisabled"
      >
        Log In
      </BaseButton>
    </form>
    <div class="d-flex justify-content-between">
      <BaseButton
        variant="link"
        @click="navigateToLogin"
        data-test="to-login"
        aria-label="Go to login page"
      >
        Back
      </BaseButton>
      <BaseButton
        variant="link"
        v-if="isTFACode"
        :disabled="sendingVerifySms"
        data-test="tfa-send-verify-sms"
        @click="sendVerifySms"
      >
        Don't Have a Code?
      </BaseButton>
      <BaseButton
        variant="link"
        v-else
        :disabled="sendingVerifySms"
        data-test="resend-otc"
        @click="resendOtc"
      >
        Resend Code
      </BaseButton>
    </div>
  </div>
</template>

<style scoped lang="scss">
.checkbox-wrapper {
  font-weight: $font-weight-normal;
  padding: $input-btn-padding-y $input-btn-padding-x;

  label {
    cursor: pointer;
  }
}
</style>
