<script setup lang="ts">
import { computed, inject } from "vue";
import { useUserStore } from "@/store/user/userStore";
import UserIcon from "@/components/UserIcon/UserIcon.vue";
import HeaderContent from "@/components/content/HeaderContent.vue";
import { CONTEXT } from "@/types/App";
import PrivacyLogo from "@/components/icons/IconPrivacyLogo.vue";
import BaseButton from "@/components/BaseButton/BaseButton.vue";
import IconCogWheel from "@/components/icons/IconCogWheel.vue";
import InterstitialCloseButton from "../InterstitialCloseButton/InterstitialCloseButton.vue";

const interstitial = inject("context") === CONTEXT.INTERSTITIAL;

const userStore = useUserStore();
const userIcon = computed((): string | undefined => {
  return userStore.user.photo
    ? `${
        import.meta.env.EXPOSED_PRIVACY_WEB_BASE_URL
      }/api/v1/user/getAccountPhoto/${userStore.user.photo}`
    : undefined;
});
</script>
<template>
  <HeaderContent class="top-header">
    <router-link
      :to="{ name: 'account' }"
      class="nav-link account px-0"
      active-class="active"
    >
      <UserIcon size="small" :url="userIcon" />
    </router-link>
    <router-link
      :to="{ name: 'account' }"
      class="nav-link"
      :class="interstitial && 'interstitial-logo'"
      active-class="active"
    >
      <PrivacyLogo aria-label="Privacy" />
    </router-link>
    <div class="d-flex align-items-center">
      <router-link
        :to="{ name: 'extension-settings' }"
        class="nav-link"
        active-class="active"
      >
        <BaseButton variant="nav" size="sm" :class="interstitial && 'me-2'">
          <IconCogWheel />
        </BaseButton>
      </router-link>
      <InterstitialCloseButton v-if="interstitial" />
    </div>
  </HeaderContent>
</template>
<style lang="scss" scoped>
.active .user-icon {
  outline: 2px solid var(--bs-primary);
  outline-offset: 2px;
}

.interstitial-logo {
  margin-left: 32px !important;
}
</style>
