import * as transactions from "@/apis/transactions";
import type { Transaction } from "@/types/Transaction";
import { defineStore } from "pinia";
import { ref } from "vue";

type TransactionsList = {
  total: number;
  transactions: string[];
};

type TransactionsStates = {
  transactions: Record<string, Transaction>;
  lists: Record<string, TransactionsList>;
};

export default defineStore("transactions", () => {
  const transactionsState = ref<TransactionsStates["transactions"]>({});
  const listsState = ref<TransactionsStates["lists"]>({});

  async function list(
    params: transactions.ListTransactionParams = {}
  ): Promise<transactions.ListTransactionResponse> {
    const cacheKey = JSON.stringify(params);

    if (listsState.value[cacheKey]) {
      return {
        total: listsState.value[cacheKey].total,
        data: listsState.value[cacheKey].transactions.map(
          (txnUuid) => transactionsState.value[txnUuid]
        ),
      };
    }

    const { data } = await transactions.list(params);

    const txnUuids: string[] = [];
    data.data.forEach((txn) => {
      transactionsState.value[txn.transactionUuid] = txn;
      txnUuids.push(txn.transactionUuid);
    });

    listsState.value[cacheKey] = {
      total: data.total,
      transactions: txnUuids,
    };

    return data;
  }

  return {
    // actions
    list,

    // states
    lists: listsState,
    transactions: transactionsState,
  };
});
