import type {
  LoginSuccess,
  LoginResponseTfa,
  LoginResponseOtc,
} from "@/types/Auth";

export function isTfaLoginResponse(res: LoginSuccess): res is LoginResponseTfa {
  const tfaRes = res as LoginResponseTfa;
  return tfaRes.twoFactorAuth === true && !!tfaRes.userToken;
}

export function isOtcLoginResponse(res: LoginSuccess): res is LoginResponseOtc {
  const otcRes = res as LoginResponseOtc;
  return otcRes.oneTimeCode === true && !!otcRes.userToken;
}
