import type { Component } from "vue";
import type { User } from "./User";
import type { MerchantCategory } from "./MerchantCategory";

export enum CardType {
  SINGLE_USE = "SINGLE_USE",
  MERCHANT_LOCKED = "MERCHANT_LOCKED",
  UNLOCKED = "UNLOCKED",
  PHYSICAL = "PHYSICAL",
  DIGITAL_WALLET = "DIGITAL_WALLET",
}

export enum CardState {
  CACHED = "CACHED",
  OPEN = "OPEN",
  PAUSED = "PAUSED",
  CLOSED_PENDING_REISSUE = "CLOSED_PENDING_REISSUE",
  CLOSED_REISSUED = "CLOSED_REISSUED",
  PRE_ACTIVE_PENDING_FULFILLMENT = "PRE_ACTIVE_PENDING_FULFILLMENT",
  PRE_ACTIVE_FULFILLED = "PRE_ACTIVE_FULFILLED",
  LOST = "LOST",
  CLOSED = "CLOSED",
}

export enum CardNetwork {
  VISA = "visa",
  MASTERCARD = "mastercard",
  DISCOVER = "discover",
  AMEX = "amex",
}

export enum PrivacyCardNetwork {
  VISA = CardNetwork.VISA,
  MASTERCARD = CardNetwork.MASTERCARD,
}

export interface CardLogo {
  type: "icon" | "image" | "mcc" | "component" | "default";
  alt: string;
  src: string | Component;
}

export interface CardStyle {
  bgColor?: string | null;
  icon?: string;
  filename?: string;
  bgImage?: string;
  lastModified?: string;
  hostname?: string;
  strippedHostname?: string;
  friendlyNames?: string[];
}

export enum SpendLimitDuration {
  TRANSACTION = "TRANSACTION",
  MONTHLY = "MONTHLY",
  ANNUALLY = "ANNUALLY",
  FOREVER = "FOREVER",
}

export interface Card {
  authRuleTokens?: string[];
  bankAccountID: number;
  bankAccountUuid: string;
  cardID: number; // number
  cardProgramID?: number; // fullInfo only
  cardUuid: string;
  created: Date; // YYYY-MM-DD HH:mm:ss
  CVV: string;
  expMonth: string;
  expYear: string;
  fundingAccount: string;
  fundingBank: string;
  hostname: string;
  lastFour: string;
  mcc: string;
  mccIconUrl?: string;
  memo: string;
  merchantCategory?: string | MerchantCategory;
  meta: Record<string, any>;
  network: PrivacyCardNetwork;
  note?: string;
  PAN: string;
  promoCredit?: number;
  reloadable: boolean;
  sharedWithRecipients?: {
    _id: string;
    recipientId: {
      _id: string;
      email: string;
    };
    shareTheme: "DEFAULT" | "PRIVACY" | "CONFETTI";
    shareTime: string; // ISO date string
  }[];
  spendLimit: number;
  spendLimitDuration: SpendLimitDuration;
  spentThisMonth?: number; // spentThisMonth only
  spentThisMonthRolling?: number; // spentThisMonth only
  spentThisWeek?: number; // spentThisMonth only
  spentThisYear?: number; // spentThisMonth only
  spentThisYearRolling?: number; // spentThisMonth only
  spentTotal?: number; // spentThisMonth only
  state: CardState;
  style?: CardStyle;
  type: CardType;
  unused: boolean;
}

export enum CardLogoSize {
  X_SMALL = "xs",
  SMALL = "sm",
  MEDIUM = "md",
  LARGE = "lg",
  X_LARGE = "xl",
}

export interface CardUser {
  name: string;
  firstName: User["firstName"];
  lastName: User["lastName"];
  streetAddress: string;
  addressLine1: User["address1"];
  addressLine2: User["address2"];
  addressLevel2: User["city"];
  addressLevel1: User["state"];
  postalCode: User["zipcode"];
}
