import extensionApi from "@/lib/extension/appExtensionApi";

export type Theme = "light" | "dark" | "auto";

export const setTheme = async (theme: Theme) => {
  renderTheme(theme);

  await extensionApi.storage.set({ theme });
};

export const getTheme = async (): Promise<Theme> => {
  return (await extensionApi.storage.get("theme")).theme || "auto";
};

const init = () => {
  getTheme().then(renderTheme);

  // Render theme when OS theme changes
  window
    .matchMedia("(prefers-color-scheme: dark)")
    .addEventListener("change", () => getTheme().then(renderTheme));
};

const renderTheme = (theme: Theme) => {
  // Don't set theme in storybook.
  if (import.meta.env.STORYBOOK === "true") {
    return;
  }

  if (theme === "auto") {
    const osTheme = window.matchMedia("(prefers-color-scheme: dark)").matches
      ? "dark"
      : "light";

    document.documentElement.setAttribute("data-bs-theme", osTheme);
  } else {
    document.documentElement.setAttribute("data-bs-theme", theme);
  }
};

init();
