import appExtensionApi from "../extension/appExtensionApi";
import { isFirefox } from "@/lib/utils/utils";
import { CONSENT_KEY, ACTIVITY_CONSENT_KEY } from "./types";

export const hasDataConsent = async () => {
  const dataConsent = (await appExtensionApi.storage.get(CONSENT_KEY)) || {};
  return !!dataConsent[CONSENT_KEY];
};

export const setDataConsent = () => {
  return appExtensionApi.storage.set({
    [CONSENT_KEY]: true,
  });
};

export const hasActivityConsent = async () => {
  if (!isFirefox()) {
    return true;
  }

  const activityConsent =
    (await appExtensionApi.storage.get(ACTIVITY_CONSENT_KEY)) || {};
  return !!activityConsent[ACTIVITY_CONSENT_KEY];
};

export const isActivityConsentSet = async () => {
  if (!isFirefox()) {
    return true;
  }

  const activityConsent = await appExtensionApi.storage.get(
    ACTIVITY_CONSENT_KEY
  );
  return activityConsent !== undefined;
};

export const setActivityConsent = (value: Boolean) => {
  return appExtensionApi.storage.set({
    [ACTIVITY_CONSENT_KEY]: value,
  });
};
