<script lang="ts">
interface EmitConsent {
  (event: "consent"): void;
  (event: "uninstall"): void;
  (event: "cancel"): void;
}
</script>

<script setup lang="ts">
import "bootstrap/js/dist/collapse";
import BaseButton from "@/components/BaseButton/BaseButton.vue";
import appExtensionApi from "@/lib/extension/appExtensionApi";
import { useEventStore } from "@/store/event/eventStore";
import { setDataConsent, setActivityConsent } from "@/lib/updates";
import { EXTENSION_EVENTS } from "@/types/Event";
import IconChevronDown from "@/components/icons/IconChevronDown.vue";
import { ref } from "vue";

const emit = defineEmits<EmitConsent>();
const eventStore = useEventStore();

eventStore.track({ name: EXTENSION_EVENTS.CONSENT_SHOWN });

const activityConsent = ref(true);

const uninstall = async () => {
  emit("uninstall");

  eventStore.track({ name: EXTENSION_EVENTS.CONSENT_DECLINED });

  const uninstall = await appExtensionApi.message.send({
    event: "uninstallExtension",
  });

  if (!uninstall) {
    eventStore.track({ name: EXTENSION_EVENTS.CONSENT_DECLINE_CANCELLED });
  }
};

const consent = async () => {
  if (appExtensionApi.context?.permissions) {
    const approve = await appExtensionApi.context?.permissions.request({
      origins: ["<all_urls>"],
    });

    if (!approve) {
      return;
    }
  }

  await setDataConsent();

  if (activityConsent.value) {
    await setActivityConsent(true);
  } else {
    await setActivityConsent(false);
  }

  eventStore.track({ name: EXTENSION_EVENTS.CONSENT_ACCEPTED });

  emit("consent");
};
</script>

<template>
  <div>
    <div class="content mx-auto">
      <h2>We're Committed to Your Privacy</h2>
      <p>
        We need your consent to access the following information which is only
        used for the purpose of providing you the best possible experience.
      </p>
      <div class="optin-option mb-3">
        <div
          role="button"
          class="optin-header"
          data-bs-toggle="collapse"
          aria-expanded="false"
          href="#device-info"
        >
          <span class="optin-title">Device Information</span>
          <IconChevronDown />
        </div>
        <div class="collapse" id="device-info">
          <div class="optin-info d-block">
            We collect information about your device necessary to protect
            against fraud and comply with anti-money laundering obligations.
            This information helps us protect your account from fraudsters and
            comply with applicable laws.
          </div>
        </div>
      </div>
      <div class="optin-option mb-3">
        <div class="optin-header">
          <span
            class="optin-title"
            role="button"
            data-bs-toggle="collapse"
            href="#user-activity"
            aria-expanded="false"
          >
            Activity & User Behavior
          </span>
          <input type="checkbox" v-model="activityConsent" />
          <IconChevronDown
            role="button"
            data-bs-toggle="collapse"
            href="#user-activity"
            aria-expanded="false"
          />
        </div>
        <div class="collapse" id="user-activity">
          <div class="optin-info">
            <span class="optin-title d-block mb-2">Activity</span>
            When you create a card through the extension, we save the relevant
            URL you visit so that we can suggest the correct card(s) to autofill
            in the future, suggest merchant-specific card designs, and identify
            any card autofill errors that we want to fix. Importantly, we do not
            track any actions or collect URL information for sites where you do
            not use a Privacy Card.
            <span class="optin-title d-block my-2">User Behavior</span>
            We track which features you use so that we can understand how
            Privacy users engage with the product, which informs improvements
            and updates.
          </div>
        </div>
      </div>
      <div v-if="!activityConsent">
        <div class="alert alert-primary" role="alert">
          If you choose not to share this information, your Privacy Add-on will
          have limited functionality.
        </div>
        <p>Features not supported without full consent</p>
        <ul>
          <li>Virtual card suggestions based on your active browser tab</li>
          <li>Autofill during checkout</li>
        </ul>
      </div>

      <div
        class="actions d-flex justify-content-around border-bottom pt-2 pb-4 mb-4"
      >
        <BaseButton
          @click="uninstall"
          variant="secondary"
          data-test="uninstall"
          class="w-50 me-2"
        >
          Uninstall
        </BaseButton>
        <BaseButton @click="consent" data-test="consent" class="w-50 ms-2">
          <span v-if="!activityConsent">Add without Autofill</span>
          <span v-else>Allow</span>
        </BaseButton>
      </div>

      <div class="policy-link">
        Full details about the information we collect and what we do with it can
        be found in our
        <a
          href="https://privacy.com/privacy-policy"
          class="text-decoration-underline"
          target="_blank"
          data-test="privacy-policy"
        >
          Privacy Policy
        </a>
      </div>
    </div>
  </div>
</template>

