import { hasActivityConsent } from "@/lib/updates";
import type { Event } from "./../../types/Event";
import { v1Api, type ApiResponse } from "@/lib/request";

const EVENT_ROUTE = "/event";

export const EVENT_ENDPOINTS = {
  track: `${EVENT_ROUTE}/track`,
  log: `${EVENT_ROUTE}/log`,
  error: `${EVENT_ROUTE}/error`,
};

export const track = async (event: Event): Promise<ApiResponse<any>> => {
  const activityConsent = await hasActivityConsent();

  if (!activityConsent) {
    return;
  }

  return v1Api.post(EVENT_ENDPOINTS.track, { event });
};

export const log = (events: Event[]): Promise<ApiResponse<any>> => {
  return recordLog(events, false);
};

export const error = (events: Event[]): Promise<ApiResponse<any>> => {
  return recordLog(events, true);
};

const recordLog = async (
  events: Event[],
  isError: boolean = false
): Promise<ApiResponse<any>> => {
  const activityConsent = await hasActivityConsent();

  if (!activityConsent) {
    return;
  }

  const body = {
    events,
  };

  if (isError) {
    return v1Api.post(EVENT_ENDPOINTS.error, body);
  } else {
    return v1Api.post(EVENT_ENDPOINTS.log, body);
  }
};
