<script lang="ts">
export default {
  inheritAttrs: false,
};
</script>

<script setup lang="ts">
import { ref } from "vue";
import BaseButton from "@/components/BaseButton/BaseButton.vue";
import BaseAlert from "@/components/BaseAlert/BaseAlert.vue";
import IconCheck from "@/components/icons/IconCheck.vue";

defineProps<{
  errorMsg?: string;
  resetCompleted: boolean;
}>();

const email = ref("");
defineEmits<{
  submit: (email: string) => void;
}>();
</script>

<template>
  <h1>Reset Password</h1>

  <p>
    We'll send you an email with a link to reset the password to your account.
  </p>

  <form
    @submit.prevent="($event) => $emit('submit', email)"
    class="d-grid gap-2"
  >
    <BaseAlert
      v-if="errorMsg"
      variant="danger"
      data-test="password-reset-error"
    >
      {{ errorMsg }}
    </BaseAlert>

    <input
      class="form-control"
      type="email"
      id="email"
      v-model="email"
      placeholder="Email"
      required
      data-test="password-reset-email"
      :disabled="resetCompleted"
    />

    <BaseButton
      v-if="resetCompleted"
      variant="success"
      data-test="password-reset-submit-success"
      class="pe-none"
    >
      <IconCheck id="check-icon" class="pe-2" />
      Reset Email Sent
    </BaseButton>
    <BaseButton
      variant="primary"
      type="submit"
      data-test="password-reset-button"
      v-else
    >
      Reset
    </BaseButton>
    <BaseButton
      variant="link"
      :to="{ name: 'login' }"
      data-test="password-submit-go-back"
    >
      Back
    </BaseButton>
  </form>
</template>

<style scoped lang="scss"></style>
